<template>
  <div class="product-list">
    <div class="choose-all" v-show="showOrder">
      <span class="mini-checked" @click="checkedAll">
        <input type="checkbox" class="mini-checked-input" ref="checkedAll">
        <span class="checked-core"></span>
      </span>
      <a>全选</a>
    </div>
    <div class="all-list">
      <ul>
        <li v-for="(item, index) in productList">
          <span class="mini-checked" @click="checkedInput(index)" v-show="showOrder">
            <input type="checkbox" class="mini-checked-input" ref="checkedbox">
            <span class="checked-core"></span>
          </span>
          <div class="checked-pic" v-if="item.cover.url">
            <img :src="item.cover.url" alt="">
          </div>
          <div class="checked-info">
            <div class="checked-show" v-show="!item.showEdit">
              <h4>
                <span>{{item.title}}</span>
                <small v-show="showOrder"><img src="./images/edit@2x.png" alt="" @click="edit(index)"></small>
                <a v-show="!showOrder && !takeOff" @click="showTake(index)">提货方式</a>
                <a v-show="!showOrder && takeOff">{{item.deliver_type}}</a>
              </h4>
              <em v-show="mtd">{{express[index]}}</em>
              <section class="checked-price">
                <span class="price" v-show="storeType"><small>￥</small>{{item.price.toFixed(2)}}</span>
                <span v-show="!storeType"></span>
                <span class="num">×{{item.count}}</span>
              </section>
            </div>
            <div class="checked-edit" v-show="item.showEdit">
              <div class="checked-add">
                <span class="minus" @click="minus(index)"></span>
                <input type="text" @focus="focus(index)" class="count" v-model="item.count">
                <span class="add" @click="add(index)"></span>
              </div>
              <div class="checked-btn">
                <a class="complete" @click="complete(index)">完成</a>
                <a class="delete" @click="del(index)">删除</a>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!--<confirm :message="message" @sure="sure" @cancel="cancel"></confirm>-->
  </div>
</template>

<script>
  import Confirm from 'base/confirm/confirm'
  export default {
    props: {
      productList: {
        type: Array,
        default: []
      },
      showOrder: {
        type: Boolean,
        default: true
      },
      takeOff: {
        type: Boolean,
        default: false
      },
      express: {
        type: Array,
        default: []
      },
      mtd: {
        type: Boolean,
        default: true
      },
      storeType: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        checkedBool: false,
        currentIndex: -1,
        currentObj: {},
        totalPrice: 0,
        tempNum: 0,
        message: '确认要删除此商品',
        allCount: 0,
        tempOrder: []
      }
    },
    created() {

    },
    mounted() {

    },
    computed: {
    },
    methods: {
      async checkedAll() {
        this.checkedBool = !this.checkedBool
        this.totalPrice = 0
        let checkAll = this.$refs.checkedAll.checked = this.checkedBool
        if(!checkAll) {
          this.$refs.checkedbox.map(item => {
            item.checked = false
          })
          this.tempOrder = []
          this.totalPrice = 0
        } else {
          this.$refs.checkedbox.map(item => {
            item.checked = true
          })
          this.productList.map(item => {
            this.totalPrice += item.price * item.count
            this.tempOrder.push({
              id: item.item_id,
              count: item.count
            })
          })
        }
        this.$emit('total', this.totalPrice)
        this.$emit('tempOrder', this.tempOrder)
      },
      checkedInput(idx) {
        let temp = 0
        this.tempOrder = []
        let checkedSigle = this.$refs.checkedbox[idx].checked = !this.$refs.checkedbox[idx].checked
        this.$refs.checkedbox.map((item, index) => {
          if(item.checked) {
            temp++
            this.tempOrder.push({
              id: this.productList[index].item_id,
              count: this.productList[index].count
            })
          }
        })
        if(this.allCount == temp) {
          this.$refs.checkedAll.checked = this.checkedBool = true
        } else {
          this.$refs.checkedAll.checked = this.checkedBool = false
        }
        if(checkedSigle) {
          this.calc(idx, 'add')
        } else {
          this.calc(idx, 'minus')
        }

        this.$emit('total', this.totalPrice)
        this.$emit('tempOrder', this.tempOrder)
      },
      calc(idx, mtd) {
        if(!this.productList.length) {
          this.totalPrice = 0
          return
        }
        if(mtd === 'add') {
          this.totalPrice += this.productList[idx].count * this.productList[idx].price
        } else {
          this.totalPrice -= this.productList[idx].count * this.productList[idx].price
        }

      },
      calcChoose() {},
      calcSingle(idx, mtd) {
        if(mtd === 'add') {
          this.totalPrice += this.productList[idx].price
        } else {
          this.totalPrice -= this.productList[idx].price
        }
        this.$emit('total', this.totalPrice)
      },
      edit(idx) {
        this.tempNum = this.productList[idx].count
        this.productList[idx].showEdit = true
      },
      complete(idx) {
        this.productList[idx].showEdit = false
        if(this.$refs.checkedbox[idx].checked) {
          if(this.productList[idx].count > this.tempNum) {
            this.totalPrice += (this.productList[idx].count - this.tempNum) * this.productList[idx].price
          } else if(this.productList[idx].count < this.tempNum) {
            this.totalPrice -= (this.tempNum - this.productList[idx].count) * this.productList[idx].price
          }
          this.$emit('total', this.totalPrice, 1)
        }
        this.$emit('complete', this.productList[idx].count, this.productList[idx].id)
      },
      showTake(idx) {
        this.$emit('showTake', idx)
      },
      del(idx) {
        let {id} = this.productList[idx]
        //需注意删除的时候是以下标而不是以id，这地方可优化，所以要算出总价，在从this.productList中删除该数组
        if(this.$refs.checkedbox[idx].checked) {

          this.calc(idx, 'minus')
          this.$emit('total', this.totalPrice)
        }
        // this.$refs.checkedbox.map(item => {
        //   item.checked = false
        // })
        this.productList.splice(idx, 1)
        this.$emit('del',0 ,id)
        this.$emit('refreshScroll', this.productList)
      },
      add(idx) {
        if(this.productList[idx].count >= this.productList[idx].stock) {
          this.productList[idx].count = this.productList[idx].stock
        } else {
          this.productList[idx].count++
        }
        // this.$refs.checkedbox[idx].checked ? this.calcSingle(idx, 'add') : 0

      },
      focus(idx) {
        this.currentIndex = idx
        this.currentObj = this.productList[this.currentIndex]
      },
      minus(idx) {
        if(this.productList[idx].count <= 1) {
          this.productList[idx].count = 1
        } else {
          this.productList[idx].count--
        }
        // this.$refs.checkedbox[idx].checked ? this.calcSingle(idx, 'minus') : 0
      }
    },
    components: {
      Confirm
    },
    watch: {
      productList(newVal, oldVal) {
        console.log(newVal)
        if(this.allCount == newVal.length) {
          return
        }
        this.allCount = newVal.length
      },
      'currentObj.count': {handler(newVal, oldVal) {

        let temp = /^[0-9]*$/
        if(!temp.test(newVal)) {
          this.currentObj.count = oldVal
        }
        if(newVal) {
          if(newVal > this.currentObj.stock) {
            this.currentObj.count = this.currentObj.stock
          } else if(newVal < 1) {
            this.currentObj.count = 1
          }
        } else {
          this.currentObj.count = 1
        }
      },immediate: true}
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .product-list
    padding-bottom: 50px
    .mini-checked
      flex: 0 0 20px
      width: 20px
      height: 20px
      box-sizing: border-box
      .mini-checked-input
        display: none
      .checked-core
        border-radius: 100%
        border: 1px solid #ccc
        position: relative
        display: block
        width: 20px
        height: 20px
        box-sizing: border-box
      .checked-core:after
        border: 2px solid transparent
        border-left: 0
        border-top: 0
        content: " "
        top: 3px
        left: 6px
        position: absolute
        width: 4px
        height: 8px
        transform: rotate(45deg) scale(0)
        transition: transform .2s
      .mini-checked-input:checked+.checked-core
        background-color:#ff8852
        border-color: #ff8852
      .mini-checked-input:checked+.checked-core:after
        border-color: #fff
        transform: rotate(45deg) scale(1)
    .choose-all
      height: 36px
      align-items: center
      padding: 0 15px
      display: flex
      background: #fff
      a
        margin-left: 8px
        color: #333
    .all-list
      margin-top: 12px
      background: #fff
      ul
        li
          padding: 0 15px
          border-bottom: 1px solid #f2f2f2
          display: flex
          align-items: center
          height: 125px
          .checked-pic
            flex: 0 0 80px
            width: 80px
            height: 80px
            margin: 0 15px 0 8px
            img
              height: 100%
              width: 100%
          .checked-info
            flex: 1 1 auto
            height: 80px
            .checked-show
              display: flex
              flex-direction: column
              justify-content space-between
              height: 80px
              h4
                display: flex
                span
                  flex: 1 1 auto
                  margin-right: 20px
                  font-size: 14px
                small
                  width: 16px
                  flex: 0 0 16px
                  height: 16px
                  img
                    width: 100%
                    height: 100%
                a
                  flex: 0 0 60px
                  height: 16px
                  line-height: 16px
                  border-radius: 8px
                  text-align: center
                  width: 60px
                  background: #ff8852
                  color: #fff
                  font-size: 12px
              em
                width: 48px
                height: 16px
                line-height: 16px
                text-align: center
                border-radius: 8px
                color: #fff
                font-size: 12px
                font-style: normal
                background: #658aff
              .checked-price
                display: flex
                span
                  flex: 0 0 50%
                .num
                  color: #999
                  text-align: right
                .price
                  color: #ff8852
                  small
                    font-size: 12px
            .checked-edit
              .checked-add
                display: flex
                span
                  flex: 0 0 30px
                  height: 30px
                  display: block
                  background: url('./images/minus@2x.png') no-repeat
                  background-size: 30px 30px
                span.add
                  background: url('./images/add@2x.png') no-repeat
                  background-size: 30px 30px
                input
                  flex: 0 0 80px
                  text-align: center
                  width: 80px
                  display: block
                  margin: 0 10px
                  height: 30px
                  line-height: 30px
                  border: 1px solid #ddd
            .checked-btn
              display: flex
              a
                flex: 0 0 80px
                text-align: center
                width: 100px
                height:36px
                line-height: 36px
                background: #ff8852
                display:block
                margin-top: 8px
                color: #fff
              a:last-child
                background: #ddd

</style>



