<template>
  <div class="nav-list">
    <nav>
      <scroll :scrollX="true" :scrollY="false" :data="navList" class="nav-list" ref="scroll">
        <ul ref="nav">
          <li v-for="(item, index) in navList" @click="chooseList(index)">
            {{item}}
          </li>
        </ul>
      </scroll>
    </nav>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'

  export default {
    props: {
      navList: {
        type: Array,
        default: ['全部商品']
      }
    },
    created() {
      this.width = 0
    },
    methods: {
      chooseList(idx) {
        this.$emit('chooseList', idx)
      }
    },
    watch: {
      navList(newVal) {
        this.width = 0
        if(newVal.length) {
          this.$nextTick(() => {
            this.children = [...this.$refs.nav.children]
            this.children.map((item) => {
              this.width += item.clientWidth
            })
            this.$refs.nav.style.width = `${this.width}px`
          })
        }

      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .nav-list
    width: 100%
    overflow: hidden
    line-height: 44px
    ul
      white-space:nowrap
      li
        display: inline-block
        padding: 0 15px
        font-size: $font-size-medium
      .active
        color: #29aae2
</style>



