<template>
  <scroll class="coupon" :data="couponList">
    <coupon-temp-list :couponList="couponList" :showFlag="showFlag" @receive="receive"></coupon-temp-list>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import CouponTempList from 'base/couponTempList/couponTempList'
  import {axiosGet, axiosPost, token} from 'api/config'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        couponList: [],
        useIt: true,
        showFlag: true
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/coupons').then(res => {
          console.log(res)
          if(res.status === 200) {
            this.couponList = this._normalizeData(res.data)
          }
        })
      },
      receive(id) {
        axiosGet(`/me/coupons/${id}`).then(res => {
          console.log(res)
        })
      },
      _normalizeData(arr) {
        arr.map(item => {
          if(item.money === 0) {
            item.msg = '无门槛'
          } else {
            item.msg = `满${item.base_money}减`
          }
        })
        return arr
      }
    },
    components: {
      Scroll,
      CouponTempList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .coupon
    background: #f2f2f2
    z-index: 11
    pos-all()

</style>



