<template>
  <div class="obj-coupon">
    <section class="coupon-code">
      <ul>
        <li>
          <span>序列号</span>
          <input type="text" placeholder="请输入序列号" v-model="Qcode">
        </li>
        <li>
          <span>密码</span>
          <input type="text" placeholder="请输入密码" v-model="Qpwd">
        </li>
      </ul>
      <section class="coupon-btn">
        <a @click="changeSth">兑换实物券</a>
        <a>前往仓库</a>
      </section>
    </section>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import {axiosPost} from 'api/config'
  import Confirm from 'base/confirm/confirm'

  export default {
    data() {
      return {
        Qcode: '',
        Qpwd: '',
        message: ''
      }
    },
    methods: {
      changeSth() {
        if(this.Qcode == '') {
          this.message = '序列号不能为空'
          this.$refs.confirm.show()
          return
        }
        if(this.Qpwd == '') {
          this.message = '密码不能为空'
          this.$refs.confirm.show()
          return
        }
        axiosPost('storehouse/sn/conver',{
          data: {
            sn: this.Qcode,
            password: this.Qpwd
          }
        }).then(res => {
          if(res.status === 200) {
            if(res.data.status == 0) {
              this.message = res.data.msg
              this.$refs.confirm.show()
            } else {
              this.message = res.data.msg
              this.$refs.confirm.show()
            }
          }
        })
      }
    },
    components: {
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .obj-coupon
    background: #f2f2f2
    z-index: 11
    pos-all()
    .coupon-code
      background: #fff
      padding: 12px 0
      ul
        padding: 0 15px
        li
          display: flex
          height: 36px
          line-height: 36px
          span
            flex: 0 0 70px
            width: 70px
          input
            border: 1px solid #f2f2f2
            flex: 1 1 auto
        li:last-child
          margin-top: 12px
    .coupon-btn
      padding: 0 15px
      a
        margin-top: 12px
        width: 100%
        height: 36px
        line-height: 36px
        text-align: center
        background: #ff8852
        color: #fff
        display: block
      a:last-child
        background: #00c886
</style>



