<template>
  <div class="category-product">
    <div class="category-tab">
      <ul>
        <li v-for="(item, index) in navMenu" @click="navChoose(index)">
          {{item}}
        </li>
      </ul>
    </div>
    <scroll :data="allList" :pullUpLoad="pullUpLoad" @pullingUp="pullingUp" ref="scroll" class="product-content">
      <div class="product-scroll">
        <all-list :allList="allList" @productDetail="productDetail"></all-list>
      </div>
    </scroll>
    <div class="list-tab">
      <nav-list :navList="navList" @chooseList="chooseList"></nav-list>
    </div>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import NavList from 'base/navList/navList'
  import allList from 'base/allList/allList'
  import {axiosGet, token} from 'api/config'

  export default {
    created() {
      this.getData()
    },
    data() {
      return {
        navMenu: ['全部商品', '商品分类', '套餐分类'],
        navList: ['全部商品'],
        allList: [],
        navId: [{id:-1}],
        page: 1,
        pullUpLoad: true,
        lastPage: 1,
        currentIndex: 0,
        routePath: '/items'
      }
    },
    methods: {
      pullingUp() {
        this.moreData(this.routePath, this.page)
      },
      getData() {
        this.moreData('/items', this.page)
      },
      moreData(route, page) {
        if(this.lastPage < page) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet(route,{
          params: {
            page
          }
        }).then(res => {
          if(res.status === 200) {
            if(this.lastPage != res.data.meta.last_page) {
              this.lastPage = res.data.meta.last_page
            }
            this.allList = [...this.allList, ...res.data.data]
          }
        })
      },
      navChoose(idx) {
        this.navList= []
        this.navId = []
        this.page = 1
        this.allList = []
        this.currentIndex = idx
        this.lastPage = 1
        if(idx == 0) {
          this.navList.push('全部商品')
          this.navId.push({id: -1})
          this.moreData('/items', this.page)
          this.routePath = '/items'
        } else if(idx == 1) {
          this.chooseRoute('/categories/1')
        } else {
          this.chooseRoute('/categories/0')
        }
      },
      chooseRoute(route) {
        console.log(route)
        axiosGet(route).then(res => {
          if(res.status === 200) {
            res.data.data.map(item => {
              this.navList.push(item.name)
              this.navId.push(item.id)
            })
            this.moreData(`/categories/${this.navId[0]}/items`, this.page)
            this.routePath = `/categories/${this.navId[0]}/items`
          }
        })
      },
      chooseList(idx) {
        this.page = 1
        this.allList = []
        this.lastPage = 1
        let id = this.navId[idx]
        if(id == -1) {
          this.moreData(`/items`, this.page)
          this.routePath = '/items'
          return
        }
        this.moreData(`/categories/${id}/items`, this.page)
        this.routePath = `/categories/${id}/items`
      },
      productDetail(idx) {
        let {id} = this.allList[idx]
        this.$router.push({
          path: `/app/product/${id}`
        })
      }
    },
    components: {
      Scroll,
      NavList,
      allList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .category-product
    background: #f2f2f2
    z-index: 11
    @pos-all()
    .category-tab
      position: fixed
      bottom: 0
      width: 100%
      z-index: 11
      background: #fff
      border-top: 1px solid #f2f2f2
      ul
        display: flex
        li
          text-align: center
          line-height: 50px
          border-right: 1px solid #f2f2f2
          height: 50px
          flex: 0 0 33.333%
        li:last-child
          border-right:none
    .product-content
      position: fixed
      width: 100%
      top: 44px
      bottom: 50px
      overflow: hidden
    .list-tab
      line-height: 44px
      border-bottom: 1px solid #f2f2f2
      box-sizng: border-box
      height: 44px
</style>



