<template>
  <div class="banner-box">
    <swiper :options="bannerOption" class="banner" ref="banner" v-if="banner.length>0">
      <!-- slides -->
      <swiper-slide class="banner-item" v-for="(item,index) in banner" :key="index">
        <a class="item-link" :href="item.target">
          <img :src="item.url">
        </a>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper'


  export default {
    props: {
      banner:{
        type: Array,
        default: []
      }
    },
    computed: {
      swiper() {
        return this.$refs.banner.swiper
      }
    },
    created() {
    },
    mounted() {

    },
    methods: {

    },
    data() {
      return {
        bannerOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          loop: true,
          observer:true,
          autoplay: {
            disableOnInteraction: false
          },
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    watch: {
      banner(newVal, oldVal) {

      }
    }
  }
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import 'swiper/dist/css/swiper.css'

  .banner-box
    position relative
    @include wrapper()
    .banner
      width 100%
      height 48vw
      .banner-item
        width 100%
        height 100%
        .item-link
          display block
          width 100%
          height 100%
          img
            width 100%
            height 100%
    .swiper-pagination
      position absolute
      bottom 5px
      left 50%
      transform translateX(-50%)
      span + span
        margin-left 5px
</style>



