<template>
  <scroll class="give-store" :data="recordList">
    <div class="give-scroll">
      <record-list :recordList="recordList"></record-list>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import RecordList from 'base/recordList/recordList'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        recordList: []
      }
    },
    created() {
      this.recordList = MockData.storeData.giveData
    },
    methods: {

    },
    components: {
      RecordList,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .give-store
    z-index: 11
    background: #f2f2f2
    pos-all()
</style>



