<template>
  <div class="dispatch" v-show="showFlag">
    <scroll class="dispatching" :data="productDetail.covers">
      <div class="dispatching-scroll" @click="unbindClick">
        <section class="product-part" v-if="productDetail.covers">
          <div class="product-pic">
            <img :src="productDetail.covers[0].url" alt="">
          </div>
          <section class="product-info">
            <div class="checked-show">
              <h4>
                <span>{{productDetail.title}}</span>
                <a>分期配送</a>
              </h4>
              <section class="checked-price">
                <span class="price"><small>￥</small>{{productDetail.price}}</span>
              </section>
            </div>
          </section>
        </section>
        <section class="dispatch-tab">
          <ul>
            <li v-for="(item, index) in tab" :class="tabNum == index ? 'active' : ''" @click="tabNav(index)">
              {{item}}
            </li>
          </ul>
        </section>
        <section class="week-choose" v-if="tabNum == 0">
          <ul>
            <li v-for="(item, index) in week" @click="weekChoose(index)" :class="weekBool[index] == true ? 'choose' : ''">
              <h4>{{item}}</h4>
              <span>{{eggNum[index]}}个</span>
            </li>
          </ul>
        </section>
        <section class="week-choose" v-else>
          <ul>
            <li v-for="(item, index) in dateTime" @click="dayChoose(index)" :class="{'choose': dateBool[index]}">
              <h4>{{item}}</h4>
              <span>{{eggNum[index]}}个</span>
            </li>
          </ul>
        </section>
        <section class="egg-num" v-show="tabNum == 0">
          <ul>
            <li v-for="(item, index) in weekEggNum" @click="tipWeekChoose(index)" :class="tipWeekNum == index ? 'choose' : ''">
              {{item.number}}
            </li>
          </ul>
          <input type="text" v-model="weekInput" @click.stop @focus="bindWeek(0)" @blur="unbindWeek(0)" ref="weekNumInputBind"> 个
        </section>
        <section class="egg-num" v-show="tabNum == 0">
          <ul>
            <li v-for="(item, index) in weekEggTime" @click="tipTimeWeek(index)" :class="tipWeekTime == index ? 'choose' : ''">
              {{item.number}}
            </li>
          </ul>
          <input type="text" v-model="weekContinue" @click.stop @focus="bindWeek(1)" @blur="unbindWeek(1)" ref="weekInputBind"> 周
        </section>
        <section class="egg-num" v-show="tabNum == 1">
          <ul>
            <li v-for="(item, index) in monthEggNum" @click="tipMonthChoose(index)" :class="tipMonthNum == index ? 'choose' : ''">
              {{item.number}}
            </li>
          </ul>
          <input type="text" v-model="monthInput" @click.stop @focus="bindMonth(0)" @blur="unbindMonth(0)" ref="monthNumInputBind"> 个
        </section>
        <section class="egg-num" v-show="tabNum == 1">
          <ul>
            <li v-for="(item, index) in monthEggTime" @click="tipTimeMonth(index)" :class="tipMonthTime == index ? 'choose' : ''">
              {{item.number}}
            </li>
          </ul>
          <input type="text" v-model="monthContinue"@click.stop @focus="bindMonth(1)" @blur="unbindMonth(1)" ref="monthInputBind"> 月
        </section>
        <section class="egg-pay-choose">
          每周配送2次，每周共配送{{weekQuery}}枚。送{{weekQueryF}}周，共240枚。可享受每枚2.60元的购买价，节省0.00元，合计：624.00元。2018-11-05开始配送。如您的有认养母鸡或蛋库商品，确认后可以选择蛋库或指标抵扣，可以更加优惠！！
        </section>
        <section class="first-date-time">
          <ul>
            <li v-for="(item, index) in firstDateDispatch" @click="dateDispatch(index)" :class="firstDateNum == index ? 'choose' : ''">
              {{item}}
            </li>
          </ul>
        </section>

      </div>
    </scroll>
    <section class="sure-btn">
      <a @click="cancel">取消</a>
      <a @click="sure">确认</a>
    </section>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import {axiosGet} from 'api/config'
  import {mGetDate, nowDate, nowDay, nGetDate} from "api/util";

  export default {
    props: {
      routeId: {
        type: Number,
        default: null
      },
      productIndex: {
        type: Number,
        default: null
      }
    },
    data() {
      return {
        a: true,
        tab: ['按周分期', '按月分期'],
        week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
        dateTime: [],
        dateBool: [],
        tabNum: 0,
        weekBool: [],
        eggNum: [],
        chooseNum: 0,
        nowDateTime: 1,
        nowDayTime: 28,
        weekEggTime: [],
        monthEggTime: [],
        weekEggNum: [],
        monthEggNum: [],
        tipWeekNum: 0,
        tipMonthNum: 0,
        tipWeekTime: 0,
        tipMonthTime: 0,
        weekQuery: 0,
        weekQueryF: 0,
        monthQuery: 0,
        monthQueryF: 0,
        weekInput: '',
        monthInput: '',
        weekContinue: '',
        monthContinue: '',
        weekCount: 0,
        monthCount: 0,
        productDetail: {},
        firstDateDispatch: [],
        firstDateNum: -1,
        showFlag: false,
        firstDate: -1,
        fullYear: 0,
        fullMonth: 0,
        fullDay: 0,
        tempIndex: 0,
        message: ''
      }
    },
    created() {
      this.getData()
      const firstTotal = 30
      let date = new Date()
      let temp = 1
      let tempDate = 0
      let remainDay = 1
      let lastMonthDay = 0
      let mDay = mGetDate()
      this.nowDateTime = nowDate()
      this.nowDayTime = nowDay()
      this.week.map(item => {
        this.weekBool.push(false)
      })
      for(let i = 1; i <= 31; i++) {
        this.dateTime.push(i)
        this.dateBool.push(false)
        this.eggNum.push(0)
      }
      //总共30个空格，从当前日开始，到月末填几个空
      for(let j = this.nowDateTime; j <= mDay; j++) {
        this.firstDateDispatch.push(j)
        remainDay++
      }
      //剩下几个空
      lastMonthDay = firstTotal - remainDay + 1
      for(let k = 1; k <= lastMonthDay; k++) {
        this.firstDateDispatch.push(k)
      }
      this.firstDateDispatch.map((item, index) => {
        //用了下空挡，比如上一个月的第一个1号肯定是在index=0的位置，接下去的1号小标index肯定是大于0
        if(item == 1 && index != 0) {
          if((date.getMonth() + temp + 1) >= 13) {
            tempDate = 1
            this.fullYear = date.getFullYear() - 0 + 1
          } else {
            temp++
            tempDate = date.getMonth() + temp
            this.fullYear = date.getFullYear()
          }
          this.firstDateDispatch[index] = `${tempDate}月`
        }
      })

    },
    methods: {
      show() {
        axiosGet(`/items/${this.routeId}`).then(res => {
          if(res.status === 200) {
            this.productDetail = res.data.data
          }
        })
        this.firstDateDispatch.map((item, index) => {
          item += ''
          if(item.indexOf('月') != -1) {
            this.tempIndex = index
          }
        })
        this.showFlag = true
      },
      hide() {

        this.initChoose(this.weekBool)
        this.initChoose(this.dateBool)
        this.firstDateNum = -1
        this.weekInput = this.weekQuery = this.weekEggNum[0].number
        this.weekContinue = this.weekQueryF = this.weekEggTime[0].number
        this.monthInput = this.monthQuery = this.monthEggNum[0].number
        this.monthContinue = this.monthQueryF = this.monthEggTime[0].number
        this.tipWeekNum = this.tipMonthNum = this.tipWeekTime = this.tipMonthTime = 0
        this.showFlag = false
      },
      getData() {
        axiosGet('/delivery').then(res => {
          if(res.status === 200) {
            this.weekEggNum = res.data.week
            this.monthEggNum = res.data.month
            this.weekEggTime = res.data.week_time
            this.monthEggTime = res.data.month_time
            this.weekInput = this.weekQuery = this.weekEggNum[0].number
            this.weekContinue = this.weekQueryF = this.weekEggTime[0].number
            this.monthInput = this.monthQuery = this.monthEggNum[0].number
            this.monthContinue = this.monthQueryF = this.monthEggTime[0].number
          }
        })
      },
      tabNav(idx) {
        this.tabNum = idx
        if(this.tabNum == 1) {
          this.initChoose(this.weekBool)
        } else {
          this.initChoose(this.dateBool)
        }
        this.weekInput = this.weekQuery = this.weekEggNum[0].number
        this.weekContinue = this.weekQueryF = this.weekEggTime[0].number
        this.monthInput = this.monthQuery = this.monthEggNum[0].number
        this.monthContinue = this.monthQueryF = this.monthEggTime[0].number
        this.tipWeekNum = this.tipMonthNum = this.tipWeekTime = this.tipMonthTime = 0
      },
      tipWeekChoose(idx) {
        this.tipWeekNum = idx
        this.weekInput = this.weekQuery = this.weekEggNum[idx].number
        this.giveNum(this.weekBool, this.weekQuery)
      },
      tipTimeWeek(idx) {
        this.tipWeekTime = idx
        this.weekContinue = this.weekQueryF = this.weekEggTime[idx].number
      },
      tipMonthChoose(idx) {
        this.tipMonthNum = idx
        this.monthInput = this.monthQuery = this.monthEggNum[idx].number
        this.giveNum(this.dateBool, this.monthQuery)
      },
      tipTimeMonth(idx) {
        this.tipMonthTime = idx
        this.monthContinue = this.monthQueryF = this.monthEggTime[idx].number
      },
      weekChoose(idx) {
        this.$set(this.weekBool, idx, !this.weekBool[idx])
        if(this.weekBool[idx]) {
          this.$set(this.eggNum, idx, this.weekQuery)
        } else {
          this.$set(this.eggNum, idx, 0)
        }
        this.weekBool.map(item => {
          if(item) {
            this.weekCount++
          }
        })
      },
      dayChoose(idx) {
        this.$set(this.dateBool, idx, !this.dateBool[idx])
        if(this.dateBool[idx]) {
          this.$set(this.eggNum, idx, this. monthQuery)
        } else {
          this.$set(this.eggNum, idx, 0)
        }
      },
      giveNum(arr, num) {
        arr.map((item, index) => {
          if(item) {
            this.$set(this.eggNum, index, num)
          }
        })
      },
      bindWeek(idx) {
        if(idx == 0) {
          this.tipWeekNum = -1
//          this.tipWeekTime = 0
        } else {
          this.tipWeekTime = -1
//          this.tipWeekNum = 0
        }

      },
      unbindWeek(idx) {
        if(this.weekInput == '' && idx == 0) {
          this.weekInput = this.weekQuery = this.weekEggNum[0].number
          this.giveNum(this.weekBool, this.weekQuery)
        }
        if(this.weekContinue == '' && idx == 1) {
          this.weekContinue = this.weekQueryF = this.weekEggTime[0].number
        }
      },
      bindMonth(idx) {
        if(idx == 0) {
          this.tipMonthNum = -1
        } else {
          this.tipMonthTime = -1
        }

      },
      unbindMonth(idx) {
        if(this.monthInput == '' && idx == 0) {
          this.monthInput = this.monthQuery = this.monthEggNum[0].number
          this.giveNum(this.dateBool, this.monthQuery)
        }
        if(this.monthContinue == '' && idx == 1) {
          this.monthContinue = this.monthQueryF = this.monthEggTime[0].number
        }
      },
      initChoose(arr) {
        arr.map((item, index) => {
          if(item) {
            this.$set(arr, index, !item)
            this.$set(this.eggNum, index, 0)
          }
        })
      },
      unbindClick() {
        this.$refs.weekNumInputBind.blur()
        this.$refs.weekInputBind.blur()
        this.$refs.monthNumInputBind.blur()
        this.$refs.monthInputBind.blur()
      },
      dateDispatch(idx) {
        this.firstDateNum = idx
        this.fullDay = this.firstDateDispatch[idx] +''
        console.log(this.fullDay)
        if(this.fullDay < 10) {
          this.fullDay = `0${this.fullDay}`
        }
        if(this.fullDay.indexOf('月') > -1) {
          this.fullDay = '01'
        }
        let date = new Date()
        if(idx >= this.tempIndex && this.tempIndex != 0) {
          this.fullMonth = date.getMonth() + 2
          if(this.fullMonth >= 13) {
            this.fullMonth = 1
          }
        } else if (this.tempIndex == 0 || idx < this.tempIndex) {
          this.fullMonth = date.getMonth() + 1
        }
        console.log(this.fullMonth)
      },
      cancel() {
        this.$emit('backCancel', this.productIndex)
        this.hide()
      },
      sure() {
        let tempArr = ''
        let tempStr = ''
        let tempCount = 0
        let tempTotal = 0
        let tempObj = {}
        if(!this.tabNum) {
          console.log(this.routeId, this.weekQuery, this.weekQueryF, this.tabNum)
          this.weekBool.map((item, index) => {
            if(item) {
              tempArr += `${index+1},`
              tempCount++
            }
          })
          if(tempCount == 0) {
            this.message = '请选择配送期'
            this.$refs.confirm.show()
            return
          }
          tempStr = `${this.fullYear}${this.fullMonth}${this.fullDay}`
          console.log(tempStr)
          if(!tempStr || tempStr.length !=8 ) {
            this.message = '请选择第一次配送时间'
            this.$refs.confirm.show()
            return
          }
          tempTotal = tempCount * (this.weekQuery - 0)
          tempArr = tempArr.slice(0, tempArr.length-1)
          tempObj = {
            id: this.routeId,
            type: 3,
            stage: this.tabNum,
            num: this.weekQuery,
            time: this.weekQueryF,
            timeset: tempArr,
            first_time: tempStr,
            count: tempTotal
          }
          this.$emit('backData', tempObj, tempTotal, this.productIndex)
        } else {
          console.log(this.routeId, this.monthQuery, this.monthQueryF, this.tabNum)
          this.dateBool.map((item, index) => {
            if(item) {
              tempArr += `${index+1},`
              tempCount++
            }
          })
          if(tempCount == 0) {
            this.message = '请选择配送期'
            this.$refs.confirm.show()
            return
          }
          tempStr = `${this.fullYear}${this.fullMonth}${this.fullDay}`
          if(!tempStr || tempStr.length !=8 ) {
            this.message = '请选择第一次配送时间'
            this.$refs.confirm.show()
            return
          }
          tempTotal = tempCount * (this.monthQuery - 0)
          tempArr = tempArr.slice(0, tempArr.length-1)
          tempObj = {
            id: this.routeId,
            type: 3,
            stage: this.tabNum,
            num: this.monthQuery,
            time: this.monthQueryF,
            timeset: tempArr,
            first_time: tempStr,
            count: tempTotal
          }
          this.$emit('backData', tempObj, tempTotal, this.productIndex)

        }
        this.hide()
      }
    },
    components: {
      Scroll,
      Confirm
    },
    watch: {
      weekInput(newVal, oldVal) {
        if(newVal == oldVal) {
          return
        } else {
          this.weekQuery = newVal
          this.giveNum(this.weekBool, this.weekQuery)
        }

      },
      weekContinue(newVal, oldVal) {
        if(newVal == oldVal) {
          return
        } else {
          this.weekQueryF = newVal
        }
      },
      monthInput(newVal, oldVal) {
        if(newVal == oldVal) {
          return
        } else {
          this.monthQuery = newVal
          this.giveNum(this.dateBool, this.monthQuery)
        }
      },
      monthContinue(newVal, oldVal) {
        if(newVal == oldVal) {
          return
        } else {
          this.monthQueryF = newVal
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .dispatch
    background: #f2f2f2
    z-index: 12
    pos-all()
    .dispatching
      background: #f2f2f2
      z-index: 11
      pos-all()
      bottom: 50px
      .product-part
        background: #fff
        padding: 0 15px
        border-bottom: 1px solid #f2f2f2
        display: flex
        align-items: center
        height: 125px
        .product-pic
          flex: 0 0 80px
          width: 80px
          height: 80px
          margin: 0 15px 0 8px
          img
            height: 100%
            width: 100%
        .product-info
          flex: 1 1 auto
          height: 80px
          .checked-show
            display: flex
            flex-direction: column
            justify-content space-between
            height: 80px
            h4
              display: flex
              span
                flex: 1 1 auto
                margin-right: 20px
                font-size: 14px
              a
                flex: 0 0 60px
                height: 16px
                line-height: 16px
                border-radius: 8px
                text-align: center
                width: 60px
                background: #ff8852
                color: #fff
                font-size: 12px
            .checked-price
              display: flex
              span
                flex: 0 0 50%
              .num
                color: #999
                text-align: right
              .price
                color: #ff8852
                small
                  font-size: 12px
      .dispatch-tab
        background: #fff
        ul
          display: flex
          li
            line-height: 44px
            text-align: center
            height: 44px
            flex: 0 0 50%
          li.active
            color: #fff
            background: #ff8852
      .week-choose, .egg-num, .first-date-time
        background: #fff
        border-bottom: 1px solid #f2f2f2
        ul
          display: flex
          flex-wrap: wrap
          padding: 15px 1vw
          li
            background: #fff
            border: 1px solid #ff8852
            border-radius: 6px
            text-align: center
            display: flex
            flex-direction: column
            justify-content: center
            height: 12vw
            max-width: 12vw
            flex: 1 1 12vw
            margin: 0.65vw
          li.choose
            color: #fff
            background: #ff8852
          li.disabled
            color: #fff
            border: 1px solid #ddd
            background: #ddd
      .egg-num
        display: flex
        justify-content: left
        align-items: center
        ul
          flex-wrap: nowrap
          flex: 1 1 60vw
          max-width: 60vw
        input
          margin-right: 10px
          width: 60px
          height: 12vw
          border-radius: 6px
          border: 1px solid #ff8852
          flex: 0 0 60px
      .egg-pay-choose
        background: #fff
        line-height: 18px
        padding: 15px
      .first-date-time
        border-top: 1px solid #f2f2f2
    .sure-btn
      display: flex
      height: 50px
      line-height: 50px
      position: fixed
      bottom: 0
      width: 100%
      background: #ffffff
      z-index: 12
      a
        text-align: center
        flex: 0 0 50%
      a:last-child
        color: #fff
        background: #ff8852
</style>



