<template>
  <div class="add-address">
    <scroll class="add-scroll">
      <div class="add-content">
        <section class="contact-line">
          <span>收货人</span>
          <input type="text" placeholder="请输入联系人" v-model="name"/>
        </section>
        <section class="contact-line">
          <span>联系电话</span>
          <input type="text" placeholder="请输入联系电话" v-model="phone"/>
        </section>
        <section class="contact-line">
          <span>邮编</span>
          <input type="text" placeholder="请输入邮编" v-model="postalCode"/>
        </section>
        <section class="contact-text">
          <span>联系地址</span>
          <textarea placeholder="请输入详细地址" v-model="detail"></textarea>
        </section>
      </div>
      <div class="is-default">
        <span class="mini-checked" @click="checkedInput">
          <input type="checkbox" class="mini-checked-input" ref="checkedbox">
          <span class="checked-core"></span>
        </span>
        默认地址
      </div>
      <div class="add-btn" @click="saveAddress">
        <a>保存地址</a>
      </div>
      <confirm :message="message" ref="confirm"></confirm>
    </scroll>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import {mapGetters} from 'vuex'
  import {axiosPost, axiosGet, token} from 'api/config'

  export default {
    data() {
      return {
        name: '',
        phone: '',
        postalCode: '',
        detail: '',
        message: '地址添加成功',
        isDefault: 0
      }
    },
    computed: {
    },
    created() {
      if(this.$route.query.idx) {
        let {idx} = this.$route.query
        document.title = '编辑地址'
        axiosGet(`/addresss/${idx}`).then(res => {
          if(res.status === 200) {
            let {user_name, tel, postal_code, detail, type}  = res.data.data
            this.name = user_name
            this.postalCode = postal_code
            this.detail = detail
            this.phone = tel
            this.isDefault = type
            if(type == 1) {
              this.$refs.checkedbox.checked = true
            }
            this.message = '地址修改成功'
          }
        })
      }
    },
    methods: {
      checkedInput() {
        this.$refs.checkedbox.checked = !this.$refs.checkedbox.checked
        this.isDefault = this.$refs.checkedbox.checked ? 1 : 0
      },
      saveAddress() {
        axiosPost('/addadr',{
          data: {
            user_name: this.name,
            postal_code: this.postalCode,
            tel: this.phone,
            detail: this.detail,
            type: this.isDefault
          }
        }).then(res => {
          console.log(res)
          if(res.data.state === 0) {
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.push({
                path: '/app/address'
              })
            },1000)
          }
        })
      }
    },
    components: {
      Scroll,
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'
  .add-address
    pos-all()
    z-index: 11
    background: #f2f2f2
    .mini-checked
      flex: 0 0 20px
      width: 20px
      height: 20px
      box-sizing: border-box
      margin-right: 10px
      .mini-checked-input
        display: none
      .checked-core
        border-radius: 100%
        border: 1px solid #ccc
        position: relative
        display: block
        width: 20px
        height: 20px
        box-sizing: border-box
      .checked-core:after
        border: 2px solid transparent
        border-left: 0
        border-top: 0
        content: " "
        top: 3px
        left: 6px
        position: absolute
        width: 4px
        height: 8px
        transform: rotate(45deg) scale(0)
        transition: transform .2s
      .mini-checked-input:checked+.checked-core
        background-color:#ff8852
        border-color: #ff8852
      .mini-checked-input:checked+.checked-core:after
        border-color: #fff
        transform: rotate(45deg) scale(1)
    .add-scroll
      pos-all()
      bottom: 50px
      .add-content
        background: #fff
        .contact-line
          box-sizing: border-box
          display: flex
          padding: 0 15px
          height: 44px
          line-height: 44px
          border-bottom: 1px solid #f2f2f2
          span
            flex: 0 0 70px
          input
            flex: 1 1 auto
        .contact-text
          box-sizing: border-box
          display: flex
          align-items: baseline
          padding: 0 15px
          border-bottom: 1px solid #f2f2f2
          span
            flex: 0 0 70px
          textarea
            flex: 1 1 auto
            border: none
            height: 80px
      .is-default
        height: 44px
        padding: 0 15px
        background: #fff
        align-items: center
        display: flex
      .add-btn
        margin-top: 15px
        padding: 0 15px
        a
          display: block
          border-radius: 8px
          text-align: center
          color: #fff
          background: #ff8852
          height: 44px
          line-height: 44px
</style>



