<template>
  <scroll class="count-search" :data="countList">
    <div class="count-search-scroll">
      <record-list :recordList="countList"></record-list>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import RecordList from 'base/recordList/recordList'
  import {axiosGet, axiosAll} from 'api/config'
  import axios from 'axios'
  import MockData from 'common/mock/data'

  export default {
    created() {
      this.getData()
    },
    data() {
      return {
        countList: []
      }
    },
    methods: {
      getData() {
        let arr = []
        arr.push(this.rechargeFunc(), this.recordFunc())
        axiosAll(arr).then(axios.spread((acct, perms) => {
          if(acct.status === 200) {
            this.countList.push(...this._normalizeRecharge(acct.data.data))
          }
          if(perms.status === 200) {
            this.countList.push(...this._normalizeRecord(perms.data.data))
          }
        }))
      },
      rechargeFunc() {
        return axiosGet('/order/rgrecord')
      },
      recordFunc() {
        return axiosGet('/cash/record')
      },
      _normalizeRecharge(arr) {
        let temp = []
        arr.map(item => {
          console.log(item)
          temp.push({
            title: '充值',
            money: item.price,
            createdTime: this._normalizeTime(item.pay_time),
            tempStatus: '充值成功'
          })
        })

        return temp
      },
      _normalizeRecord(arr) {
        let temp = []
        let tempStatus = ''
        arr.map(item => {
          console.log(item)
          if(item.status == 1) {
            tempStatus = '提现成功'
          } else {
            tempStatus = '提现中'
          }
          temp.push({
            title: '提现',
            money: -item.money,
            createdTime: this._normalizeTime(item.created_at),
            tempStatus
          })
        })
        console.log(temp)
        return temp
      },
      _normalizeTime(str) {
        let idx = str.indexOf(' ')
        let length = str.length
        let st = str.slice(0, idx)
        return st
      }
    },
    components: {
      Scroll,
      RecordList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .count-search
    background: #f2f2f2
    z-index: 11
    pos-all()
</style>



