<template>
  <div class="share-url">
    <scroll class="share-in">
      <div class="share-scroll">
        <div class="share-content">
          <h3>推广令</h3>
          <section class="user-info">
            <div class="user-pic">
              <img :src="userObj.avatarUrl" alt="">
            </div>
            <h4>{{userObj.nickname}}</h4>
            <a class="btn">点击右上角三个小圆点，分享到朋友圈</a>
          </section>
        </div>
        <div class="coupon-take">
          <h3>领取福利</h3>
          <div class="coupon-ps">
            <article>
              <p>
                <span>认养30元优惠券：</span>您只要点击分享到朋友圈，就能领取30元的认养优惠券，无限次数，无限领取（自动存入我的优惠券，请前往个人中心查看）
              </p>
              <p class="tcenter">分享领取福利</p>
            </article>
          </div>
          <div class="coupon">
            <ul>
              <li>
                <img src="./images/couponadopt@2x.png" alt="">
              </li>
            </ul>
          </div>
        </div>
      </div>
    </scroll>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import {axiosGet} from 'api/config'
  import {getCookie} from 'api/util'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import weChat from 'api/mixins'

  export default {
    data() {
      return {
        userObj: {},
        userQcode: {},
        urlStr: '',
        message: ''
      }
    },
    created() {
//      this.token = getCookie('access_token')
      this.url = window.location.href
      this.weChatConfig(this.url)
      this.getData()
      this.goShareData()
    },
    mixins: [weChat],
    methods: {
      getData() {
//        axiosGet('/users/show',{
//          headers: {
//            Authorization: `Bearer ${this.token}`
//          }
//        }).then(res => {
//          if(res.status === 200) {
//            this.userObj = res.data.data
//          }
//        })
        axiosGet('/users/show').then(res => {
          if(res.status === 200) {
            this.userObj = res.data.data
          }
        })
      },
      friendCenter() {
        this.goShareData()
      }
    },
    components: {
      Scroll,
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"

  .share-in
    pos-all()
    z-index: 11
    background: #fff
    .share-content
      background: linear-gradient(top, #fff4e8, #fff)
      width: 100%
      text-align: center
      padding: 0 0 15px
      h3
        color: #ff8852
        font-size: 18px
        line-height: 80px
        height: 80px
        text-align: center
      .user-info
        .user-pic
          height: 60px
          width: 60px
          margin: 0 auto
          img
            border-radius: 50%
            width: 60px
            height: 60px
        h4
          margin: 12px 0
        small
          color: #999
          font-size: 14px
        a
          display: block
          margin: 0 auto
          width: 270px
          height: 44px
          background: #ff8852
          color: #fff
          border-radius: 22px
          line-height: 44px
    .coupon-take
      background: #ffffff
      margin-top: 12px
      h3
        color: #ff8852
        font-size: 18px
        line-height: 60px
        height: 60px
        text-align: center
      .coupon
        height: 33.333vw
        ul
          display: flex
          height: 33.333vw
          justify-content: center
          align-items: center
          li
            width: 44vw
            height: 26.666vw
            margin-right: 15px
            img
              height: 100%
              width: 100%
          li:last-child
            margin-right: 0
      .coupon-ps
        padding: 0 15px
        p
          margin-bottom: 15px
          line-height: 20px
          color: #999
          span
            color: #ff8852
        p.tcenter
          text-align: center
          color: #ff8852
        p:last-child
          margin: 0
</style>



