<template>
  <div class="product-detail">
    <scroll class="product-detail-scroll">
      <div class="product-content">
        <banner :banner="bannerList"></banner>
        <section class="product-info">
          <h4>
            <section class="price"><a><small>￥</small>{{productDetail.price}}</a><em>会员价</em></section>
            <span class="origin-price">价格<small>￥{{productDetail.original_price}}</small></span>
          </h4>
          <h3>{{productDetail.title}}</h3>
          <section class="express">
            <span>快递：{{productDetail.freight}}元</span>
            <span>库存: {{productDetail.stock}}件</span>
            <span>销售{{productDetail.sales_volume}}笔</span>
          </section>
        </section>
        <section class="num">
          <span>数量</span>
          <section class="count-num">
            <a class="minus" :class="!disabledMinus ? 'disbaled-minus' : ''" @click="minus"></a>
            <input type="text" v-model="productDetail.count">
            <a class="add" :class="!disabledAdd ? 'disabled-add' : ''" @click="add"></a>
          </section>
        </section>
        <section class="product-detail-info">
          <section class="tab">
            <ul>
              <li v-for="(item, index) in tabNav" @click="tab(index)">
                <a :class="currentIndex === index ? 'active' : ''">{{item}}</a>
              </li>
            </ul>
          </section>
        </section>
        <section class="product-item">
          <div class="item1" v-show="currentIndex===0">
            <div v-html="productDetail.details"></div>
          </div>
          <div class="item2" v-show="currentIndex===1">
            <div v-html="productDetail.parameter"></div>
          </div>
          <div class="item3" v-show="currentIndex===2">
            <div v-html="productDetail.packaging"></div>
          </div>
        </section>
      </div>
    </scroll>
    <div class="product-btn">
      <section class="home-favorite">
        <ul>
          <li @click="goRoute">
            <i></i>
            <span>商城</span>
          </li>
          <li class="favorite-choose" @click="favoriteBtn">
            <i :class="favorite ? 'choose' : ''"></i>
            <span>收藏</span>
          </li>
        </ul>
      </section>
      <section class="car-buy">
        <a @click="goCar">加入购物车</a>
        <a @click="goBuy">立即购买</a>
      </section>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Banner from 'base/banner/banner'
  import Confirm from 'base/confirm/confirm'
  import {axiosPost, axiosGet, axiosDelete, token} from 'api/config'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        bannerList: [],
        productDetail: {},
        currentIndex: 0,
        tabNav: ['商品详情', '详细参数', '包装售后'],
        message: '成功加入购物车',
        favorite: false,
        disabledMinus: false,
        disabledAdd: true,
        id: -1
      }
    },
    created() {
      let {id} = this.$route.params
      this.getData(id)
      // this.bannerList = MockData.productDetail.banner
      // this.productDetail = MockData.productDetail

    },
    methods: {
      getData(id) {
        axiosGet(`/items/${id}`).then(res => {
          if(res.status === 200) {
            if(!res.data.data.stock) {
              this.message = '商品库存为0或是下架'
              this.$refs.confirm.show()
              setTimeout(() => {
                this.$router.back()
              },1000)

              return
            }
            this.bannerList = res.data.data.covers
            this.productDetail = res.data.data
            this.id = this.productDetail.id
            this.favorite = this.productDetail.is_favorite  == 0 ? false : true
          }
        })
      },
      tab(idx) {
        this.currentIndex = idx
      },
      goRoute() {
        this.$router.push({
          path: '/app/home'
        })
      },
      goCar() {
        axiosPost(`/cats/${this.id}`,{
          data: {
            count: this.productDetail.count
          }
        }).then(res => {
          this.$refs.confirm.show()
        })
      },
      goBuy() {
        let arr = [{"id": this.id, "count": this.productDetail.count}]
        let arr1 = JSON.stringify(arr)
//        arr1 = encodeURIComponent(arr1)
        this.$router.push({
          path: `/app/submitorder?type=${arr1}`
        })
      },
      favoriteBtn() {

        if(!this.favorite) {
          axiosPost(`/favorites/${this.id}`).then(res => {
            if(res.status === 201) {
              this.message = '收藏成功'
              this.$refs.confirm.show()
            }
          })
        } else {
          axiosDelete(`/favorites/${this.id}`).then(res => {
            this.message = '取消收藏'
            this.$refs.confirm.show()
          })
        }
        this.favorite = !this.favorite
      },
      add() {
        this.productDetail.count++
      },
      minus() {
        this.productDetail.count--
      }
    },
    components: {
      Scroll,
      Banner,
      Confirm
    },
    watch: {
      'productDetail.count': {
        handler(newVal, oldVal) {
          let temp = /^[0-9]*$/
          if(!temp.test(newVal)) {
            this.productDetail.count = oldVal
          }
          if(newVal >= this.productDetail.stock) {
            this.disabledAdd = false
          } else if(newVal < this.productDetail.stock) {
            this.disabledAdd = true
          }
          if(newVal <= 1) {
            this.disabledMinus = false
          } else if(newVal > 1) {
            this.disabledMinus = true
          }


          if(newVal) {
            if(newVal >= this.productDetail.stock) {
              this.productDetail.count = this.productDetail.stock

            } else if(newVal < 1) {
              this.productDetail.count = 1
            }
          } else {
            this.productDetail.count = 1

          }
        },
        immediate: true
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .product-detail
    background: #f2f2f2
    z-index: 11
    pos-all()
    .product-detail-scroll
      pos-all()
      bottom: 50px
      .product-info
        border-top:1px solid #f2f2f2
        background: #ffffff
        padding: 10px 15px 15px 15px
        h4
          display: flex
          align-items: center
          height: 50px
          flex-wrap: wrap
          .price
            flex: 0 0 100%
            box-sizing: border-box
            display: flex
            align-items: center
            a
              font-size: 24px
              color: #ff8852
              margin-right: 5px
              small
                font-size: 12px
            em
              font-size: 12px
              font-style: normal
              color: #fff
              padding: 0 5px
              height: 14px
              line-height: 14px
              border-radius: 12px
              background: linear-gradient(45deg, #ff8548, #ffce53)
          span
            flex: 0 0 100%
            font-size: 12px
            color: #999
            align-self: flex-end
            small
              text-decoration: line-through
        h3
          line-height: 20px
          font-size: 16px
          margin-top: 15px
      .express
        display: flex
        justify-content: space-between
        margin-top: 15px
        span
          color: #999
    .num
      height: 44px
      display: flex
      align-items: center
      padding: 0 15px
      background: #ffffff
      margin-top: 1px
      span, .count-num
        flex: 0 0 50%
      .count-num
        display: flex
        align-items: center
        justify-content: flex-end
        a
          flex: 0 0 24px
          width: 24px
          height: 24px
        .minus
          background: url('./images/minus@3x.png') no-repeat
          background-size: 24px 24px
        .disbaled-minus
          background: url('./images/disabled_minus@3x.png') no-repeat
          background-size: 24px 24px
        .add
          background: url('./images/add@3x.png') no-repeat
          background-size: 24px 24px
        .disabled-add
          background: url('./images/disabled_add@3x.png') no-repeat
          background-size: 24px 24px
        input
          text-align: center
          width: 80px
          height: 40px
          flex: 0 0 40px
    .product-detail-info
      background: #ffffff
      margin-top: 12px
      .tab
        height: 36px
        line-height: 36px
        border-bottom: 1px solid #f2f2f2
        ul
          display: flex
          li
            text-align: center
            height: 36px
            flex: 0 0 33.333%
            a
              display: block
            .active
              border-bottom: 2px solid #ff8852
    .product-item
      padding: 15px
      background: #ffffff
      img
        height: 100%
        width: 100%
  .product-btn
    background: #fff
    position: fixed
    width: 100%
    bottom: 0
    height: 50px
    display: flex
    align-items: center
    justify-content: space-between
    .home-favorite
      flex: 1 1 auto
      ul
        display: flex
        li
          flex: 0 0 50%
          display: flex
          flex-direction: column
          justify-content: center
          align-items: center
          i
            width: 24px
            height: 24px
            background: url('./images/home.png') no-repeat
            background-size: 100% 100%
        .favorite-choose
          i
            background: url('./images/unfavorite.png') no-repeat
            background-size: 100% 100%
          .choose
            background: url('./images/favorite.png') no-repeat
            background-size: 100% 100%
    .car-buy
      flex: 0 0 66.667vw
      width: 66.667vw
      display: flex
      a
        color: #fff
        display: block
        height: 50px
        line-height: 50px
        background: #ffcc53
        text-align: center
        flex: 0 0 50%
      a:last-child
        background: #ff8a49
</style>



