<template>
  <div class="share">
    <div class="no-share" @click="makeShare" v-if="shareFlag">
      <img src="./images/join.png" alt="">
    </div>
    <div class="share-content" v-else>
      <scroll class="scroll-content" v-if="tempUser.length > 0">
        <div class="share-scroll">
          <section class="member-info">
            <div class="member-pic">
              <div class="member-control">
                <img :src="userInfo.avatarUrl" alt="">
              </div>
              <div class="member-phone">
                <h3>{{userInfo.nickname}}</h3>
                <small>推广佣金比{{userInfo.current_level.sales_percent}}%, 邀请佣金比{{userInfo.current_level.invite_percent}}%</small>
              </div>
            </div>
            <aside>
              {{userInfo.current_level.name}}
            </aside>
          </section>
        </div>
        <div class="tip">
          <span>升级到银牌分享家，推广佣金比高达{{userInfo.next_level.sales_percent}}%，邀请佣金比高达{{userInfo.next_level.invite_percent}}%</span>
          <i><img src="./images/more@2x.png" alt=""></i>
        </div>
        <div class="user-earning">
          <small>
            当前用户累计收益
          </small>
          <h3>
            {{userInfo.earnings.toFixed(2)}}
          </h3>
          <span>
            包含待结算{{userInfo.wait_earnings.toFixed(2)}}元
          </span>
        </div>
        <div class="user-profit">
          <section class="user-profit-one">
            <small>
              累计推广金(元)
            </small>
            <h3>
              {{userInfo.pro_money.toFixed(2)}}
            </h3>
            <span>
            待统计{{userInfo.wait_pro_money.toFixed(2)}}元
          </span>
          </section>
          <section class="user-profit-one">
            <small>
              累计消费金(元)
            </small>
            <h3>
              {{userInfo.order_money.toFixed(2)}}
            </h3>
            <span>
            待统计{{userInfo.wait_order_money.toFixed(2)}}元
          </span>
          </section>
        </div>
        <div class="user-apply" @click="goCash">
          <span>可提现佣金<small>{{userInfo.can_money.toFixed(2)}}</small>元</span>
          <i>申请提现 <img src="./images/more@2x.png" alt=""></i>
        </div>
        <div class="user-user">
          <section class="user-money">
            <h4>{{userInfo.child_count}}</h4>
            <span>累计邀请数</span>
          </section>
          <section class="user-money">
            <h4>{{userInfo.pro_order_count}}</h4>
            <span>累计推广数</span>
          </section>
          <!--<section class="user-money">-->
            <!--<h4>0</h4>-->
            <!--<span>累计用户</span>-->
          <!--</section>-->
        </div>
      </scroll>
      <div class="tab-bar-user">
        <router-link to="/app/home" class="nav-bar">首页</router-link>
        <router-link to="/app/spread" class="nav-bar">推广商品</router-link>
        <router-link to="/app/shareurl" class="nav-bar">推广海报</router-link>
      </div>
    </div>
    <div class="share-link">

    </div>
    <toast-confirm :message="message" @sure="sure" @cancel="cancel" ref="toastConfirm"></toast-confirm>
  </div>
</template>

<script>
  import ToastConfirm from 'base/toastConfirm/toastConfirm'
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import {axiosGet, axiosPost} from 'api/config'


  export default {
    data() {
      return {
        shareFlag: false,
        message: '',
        userInfo: {},
        tempUser: []
      }
    },
    created() {
      axiosGet('/sharerCenter').then(res => {
        this.tempUser = Object.keys(res.data.data)
        this.userInfo = res.data.data

      })
      this.getData()
    },
    mounted() {
    },
    methods: {
      getData() {
        axiosGet('/users/show').then(res => {
          if(res.status === 200) {
            if(res.data.data.is_shared == 0) {
              this.shareFlag = true
            } else {
              this.shareFlag = false
            }
          }
        })

      },
      goCash() {
        this.$router.push({
          path: '/app/cash'
        })
      },
      makeShare() {
        this.message = '准备好成为分享者了吗'
        this.$refs.toastConfirm.show()
      },
      sure() {
        axiosGet('/sharer').then(res => {
          this.getData()
        })
      },
      cancel() {
        this.$refs.toastConfirm.hide()
      }
    },
    components: {
      ToastConfirm,
      Loading,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .share
    pos-all()
    background: #f2f2f2
    z-index: 11
    .no-share
      pos-all()
      img
        width: 100%
        height: 100%
    .share-content
      pos-all()
      bottom: 50px
      .scroll-content
        .member-info
          box-sizing: border-box
          display: flex
          align-items: center
          height: 100px
          padding: 0 15px
          background: linear-gradient(45deg, #474959, #2d2f3f)
          .member-pic
            flex: 1 1 auto
            height: 70px
            display: flex
            align-items: center
            color: #fff
            font-size: 16px
            .member-control
              width: 70px
              height: 70px
              margin-right: 15px
              img
                border-radius: 100%
                border: 2px solid #ffdbb9
                box-sizing: border-box
                width: 100%
                height: 100%
            .member-phone
              flex: 1 1 auto
              h3
                margin-bottom: 10px
              small
                font-size: 12px
              .phone
                height: 14px
                line-height: 14px
                img
                  width: 8px
                  height: 14px
                .active
                  font-size: 12px
                  vertical-align: top
          aside
            font-size: 12px
            padding: 3px 5px
            border-radius: 6px
            color: #fff
            background: #a78b5c
        .tip
          padding: 0 15px
          box-sizing: border-box
          background: #fff
          line-height: 44px
          height: 44px
          display: flex
          flex-wrap: nowrap
          span
            flex: 1 1 auto
            font-size: 12px
            text-overflow ellipsis
            white-space: nowrap
            overflow hidden
            margin-right: 20px
          i
            display: block
            flex: 0 0 6px
            height:10px
            width: 6px
            img
              height: 100%
              width: 100%
        .user-earning
          padding: 15px 0
          text-align: center
          background: #ffffff
          margin-top: 1px
          h3
            font-size: 20px
            margin: 10px 0
          small, span
            color: #999
            font-size: 12px
        .user-profit
          padding: 15px 0
          margin-top: 1px
          display: flex
          background: #ffffff
          h3
            font-size: 20px
            margin: 10px 0
          small, span
            color: #999
            font-size: 12px
          .user-profit-one
            text-align: center
            border-right: 1px solid #f2f2f2
            flex: 0 0 50%
          .user-profit-one:last-child
            border-right: 0
        .user-apply
          margin-top: 1px
          display: flex
          padding: 0 15px
          justify-content: space-between
          height: 44px
          line-height: 44px
          background: #ffffff
          span
            flex: 0 0 50%
            small
              color: #ff8852
          i
            color: #999
            text-align: right
            flex: 0 0 50%
            font-style: normal
            img
              height: 10px
              width: 6px
        .user-user
          margin-top: 12px
          padding: 15px 0
          display: flex
          background: #ffffff
          .user-money
            text-align: center
            flex: 0 0 50%
            h4
              color: #3388ff
              margin-bottom: 10px
              font-size: 18px
      .tab-bar-user
        position: fixed
        width:100%
        bottom: 0
        height: 50px
        line-height: 50px
        background: #ffffff
        display: flex
        .nav-bar
          border-right: 1px solid #f2f2f2
          text-align: center
          flex: 0 0 33.3333%
        .nav-bar:last-child
          border: none
</style>



