import * as types from './mutations-types'

const mutations = {
  [types.SET_ADDRESS](state, address) {
    state.address = address
  },
  [types.SET_PHONE](state, phone) {
    state.phone = phone
  },
  [types.SET_NAME](state, name) {
    state.name = name
  },
  [types.SET_POSTAL_CODE](state, postalCode) {
    state.postalCode = postalCode
  },
  [types.SET_DETAIL](state, detail) {
    state.detail = detail
  },
  [types.SET_ISDEFAULT](state, isDefault) {
    state.isDefault = isDefault
  },
  [types.SET_CONFIRMORDER](state, confirmOrder) {
    state.confirmOrder = confirmOrder
  },
  [types.SET_CONFIRMID](state, confirmId) {
    state.confirmId = confirmId
  },
  [types.SET_PERCENT](state, percent) {
    state.percent = percent
  },
  [types.SET_STOREEGGNUM](state, storeEggNum) {
    state.storeEggNum = storeEggNum
  },
  [types.SET_SHOWC](state, showc) {
    state.showc = showc
  }
}

export default mutations



