<template>
  <div class="home-index">
    <scroll class="home">
      <div class="home-scroll">
          <banner :banner="bannerList"></banner>
          <nav class="home-nav">
            <ul>
              <li @click="goStore">
                <a><img src="./images/hot.png" alt=""></a>
                <span>热销商品</span>
              </li>
              <li>
                <a><img src="./images/active.png" alt=""></a>
                <span>节日活动</span>
              </li>
              <li>
                <a><img src="./images/change.png" alt=""></a>
                <span>兑换中心</span>
              </li>
              <li @click="goMessage">
                <a><img src="./images/message.png" alt=""></a>
                <span>反馈中心</span>
              </li>
            </ul>
          </nav>
          <div class="coupon" @click="goShare">
            <ul>
              <li>
                <img src="./images/coupon20@3x.png" alt="">
              </li>
              <li>
                <img src="./images/coupon30@3x.png" alt="">
              </li>
            </ul>
          </div>
          <div class="female">
            <aside>
              <img src="./images/item-pic.png" alt="">
            </aside>
            <section class="female-info">
              <div class="info">
                <h3>认养一只母鸡</h3>
                <span>享用<a> 360 </a>枚成本价鸡蛋</span><span>每枚鸡蛋便宜<a> 0.6 </a>元</span><br/>
                <span>想吃鸡点击<a> 吃鸡</a></span><span>想吃鸡蛋点击<a> 吃蛋</a></span>
              </div>
              <div class="btn">
                <span><small>￥</small>{{singleChicken.price}}</span>
                <a @click="goOrder(singleChicken.id)">点击认养</a>
              </div>
            </section>
          </div>
          <div class="chicken-meal">
            <h3>超惠套餐</h3>
            <section class="egg-meal">
              <div class="yellow-egg-meal" @click="mealDetail(yellowEgg.id)">
                <h4>{{yellowEgg.title}}</h4>
                <span><small>￥</small>{{yellowEgg.price}}</span>
              </div>
              <div class="green-egg-meal" @click="mealDetail(greenEgg.id)">
                <h4>{{greenEgg.title}}</h4>
                <span><small>￥</small>{{greenEgg.price}}</span>
              </div>
            </section>
            <section class="woo-meal">
              <div class="male-meal" @click="mealDetail(maleChicken.id)">
                <aside>
                  <img src="./images/male-meal.png" alt="">
                </aside>
                <div class="male-price">
                  <h4>{{maleChicken.title}}</h4>
                  <span><small>￥</small>{{maleChicken.price}}</span>
                </div>
              </div>
              <div class="female-meal" @click="mealDetail(femaleChicken.id)">
                <div class="male-price">
                  <h4>{{femaleChicken.title}}</h4>
                  <span><small>￥</small>{{femaleChicken.price}}</span>
                </div>
                <aside>
                  <img src="./images/female-meal.png" alt="">
                </aside>
              </div>
            </section>
          </div>
          <div class="product-list">
            <h3>喔喔优选</h3>
            <all-list :allList="productList" @productDetail="productDetail"></all-list>
          </div>
    </div>
      <div class="small-share" v-show="!showTime" @click="goSharePage">
        <img src="./images/coupon_adopt@2x.png" alt="">
      </div>
    </scroll>
    <div class="index-share" v-show="showTime">
      <div class="close" @click="close">
        <img src="./images/close.png" alt="">
      </div>
      <div class="share-part" @click="goSharePage">
        <section class="share-pic-content">
          <img src="./images/share.png" alt="">
        </section>
        <span>
          无限分享到朋友圈，无限次领取认养代金券
        </span>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from 'base/banner/banner'
  import Scroll from 'base/scroll/scroll'
  import {axiosGet, axiosAll} from "api/config"
  import allList from 'base/allList/allList'
  import {mapGetters, mapMutations} from 'vuex'
  import axios from 'axios'


  export default {
    data() {
      return {
        bannerList: [],
        yellowEgg: {},
        greenEgg: {},
        maleChicken: {},
        femaleChicken: {},
        productList: [],
        singleChicken: {},
        showTime: true,
        timer: null
      }
    },
    created() {
      if(this.showc == 0) {
        this.setShowc(1)
        this.timer = setTimeout(() => {
          this.showTime = false
        }, 5000)
      } else {
        this.showTime = false
      }

      this.getData()
      // this.bannerList = MockData.banner
    },
    computed: {
      ...mapGetters([
        'showc'
      ])
    },
    methods: {
      close() {
        this.showTime = false
        clearTimeout(this.timer)
      },
      goShare() {
        this.$router.push({
          path: '/app/shareurl'
        })
      },
      goSharePage() {
        window.location.href = '/app/shareadpot'
//        this.$router.push({
//          path: '/app/shareadpot'
//        })
      },
      async getData() {
        let arr = []
        let arr1 = []
        arr.push(this.bannerFunc(), this.productFunc(), this.allListFunc(), this.recommendedFunc())
        await axiosAll(arr).then(axios.spread((acct, perms, para, res) => {
          this.bannerList = acct.data.data
          this.productList = res.data.data
          perms.data.data.map(item => {
            arr1.push(this.listFunc(item.id))
          })
          para.data.data.map(item => {
            if(item.title === '母鸡') {
              this.singleChicken = item
            }
          })
        }))
        await axios.all(arr1).then(axios.spread((acct, perms) => {
          this.yellowEgg = acct.data.data[1]
          this.greenEgg = acct.data.data[0]
          this.maleChicken = perms.data.data[0]
          this.femaleChicken = perms.data.data[1]
        }))
      },
      productDetail(idx) {
        let {id} = this.productList[idx]
        this.$router.push({
          path: `/app/product/${id}`
        })
      },
      mealDetail(id) {
        this.$router.push({
          path: `/app/product/${id}`
        })
      },
      goOrder(id) {
        let tempArr = []
        tempArr.push({
          id,
          count: 1
        })
        let arr = JSON.stringify(tempArr)
        this.$router.push({
          path: `/app/submitorder?type=${arr}&adopt=1`
        })
      },
      allListFunc() {
        return axiosGet('/items')
      },
      recommendedFunc() {
        return axiosGet('/items/recommended')
      },
      bannerFunc() {
        return axiosGet('/banner')
      },
      productFunc() {
        return axiosGet('/categories/1')
      },
      //固定的鸡蛋套餐id随时会变
      listFunc(id) {
        return axiosGet(`/categories/${id}/items`)
      },
      goMessage() {
        this.$router.push({
          path: '/app/message'
        })
      },
      goStore() {
        this.$router.push({
          path: '/app/categoryproduct'
        })
      },
      ...mapMutations({
        setShowc: 'SET_SHOWC'
      })
    },
    components: {
      Banner,
      Scroll,
      allList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"

  .home
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 50px
    background: #f2f2f2
    .home-nav
      height: 75px
      background: #ffffff
      ul
        display: flex
        height: 75px
        justify-content: center
        align-items: center
        li
          flex: 0 0 25%
          font-size: 14px
          text-align: center
          span
            display: block
            margin-top: 5px
          a
            display: block
            width: 36px
            height: 36px
            margin: 0 auto
            img
              height: 100%
              width: 100%
    .coupon
      height: 33.333vw
      background: #f2f2f2
      ul
        display: flex
        height: 33.333vw
        justify-content: center
        align-items: center
        li
          width: 44vw
          height: 26.666vw
          margin-right: 15px
          img
            height: 100%
            width: 100%
        li:last-child
          margin-right: 0
    .female
      display: flex
      height: 160px
      background: #ffffff
      align-items: center
      justify-content: space-between
      padding: 0 15px
      box-sizing: border-box
      aside
        flex: 0 0 140px
        width: 140px
        height: 150px
        img
          width: 100%
          height: 100%
      .female-info
        flex: 1 1 auto
        display: flex
        flex-direction: column
        justify-content: space-between
        .info
          flex: 1 1 auto
          h3
            font-weight: bold
            font-size: 16px
            height: 24px
            line-height: 24px
            margin-bottom: 8px
          span
            font-size: 12px
            margin-right: 5px
            color: #999
            a
              color: #ff8852
        .btn
          flex: 1 1 auto
          margin-top: 20px
          display: flex
          justify-content space-between
          align-items: center
          span
            flex: 1 1 50%
            color: #ff8852
            font-size: 20px
            small
              font-size: 12px
          a
            width: 80px
            height: 24px
            line-height: 24px
            text-align: center
            background: #ff8852
            border-radius: 12px
            font-size: 14px
            color: #fff
    .chicken-meal
      background: #ffffff
      margin-top: 12px
      h3
        height: 36px
        line-height: 36px
        padding: 0 15px
        font-size: 16px
      .egg-meal
        height: 100px
        display: flex
        background: url('./images/egg-meal.png') #f1f1f1 no-repeat bottom center
        background-size: 111px 100px
        .yellow-egg-meal, .green-egg-meal
          flex: 0 0 50%
          box-sizing: border-box
        .yellow-egg-meal
          padding: 30px 15px 10px
          font-size: 14px
          h4
            margin-bottom: 15px
          span
            margin-left: 46px
            color: #ff8852
            border-bottom: 1px solid #ff8852
            font-size: 16px
            small
              font-size: 12px
        .green-egg-meal
          padding: 30px 15px 10px
          font-size: 14px
          text-align: right
          h4
            margin-bottom: 15px
          span
            margin-right: 40px
            color: #ff8852
            border-bottom: 1px solid #ff8852
            font-size: 16px
            small
              font-size: 12px
      .woo-meal
        display: flex
        height: 100px
        .male-meal, .female-meal
          flex: 0 0 50%
          width: 50%
          display: flex
          justify-content: space-between
          align-items: center
          box-sizing: border-box
          padding: 0 10px
          aside
            flex: 0 0 90px
            width: 90px
            height: 90px
            img
              width: 100%
              height: 100%
          .male-price
            h4
              font-size: 14px
              margin-bottom: 12px
            span
              display: block
              text-align: right
              color: #ff8852
              font-size: 16px
              small
                font-size: 12px
        .male-meal
          padding-right: 15px
          background: linear-gradient(45deg, #f2f2f2, #fff)
        .female-meal
          padding-left: 15px
          background: linear-gradient(135deg, #f2f2f2, #fff)
          .male-price
            span
              text-align: left
    .product-list
      background: #ffffff
      margin-top: 12px
      h3
        height: 36px
        line-height: 36px
        padding: 0 15px
        border-bottom: 1px solid #eee
        font-size: 16px
    .small-share
      position: fixed
      bottom: 80px
      right: 0
      z-index: 11
      width: 50px
      height: 60px
      img
        width: 100%
        height: 100%
  .index-share
    pos-all()
    bottom: 0
    z-index: 13
    background: rgba(0, 0, 0 .7)
    .close
      width: 32px
      height: 32px
      position: absolute
      left: 50%
      top: 50%
      margin-left: 120px
      margin-top: -200px
      img
        width:100%
        height: 100%
    .share-part
      text-align: center
      width: 100%
      position: absolute
      left: 50%
      top: 50%
      margin-left: -50%
      margin-top: -150px
      .share-pic-content
        width: 200px
        margin: 0 auto 20px
        img
          height: 200px
          width: 200px
      span
        padding: 8px 15px
        border-radius: 8px
        background: #eb4132
        color: #ffffff
        font-size: 14px
</style>



