<template>
  <div class="toast-tip" v-show="showFlag">
    <div class="confirm">
      <h3>{{message}}</h3>
      <div class="btn">
        <a @click="cancle">取消</a>
        <a @click="sure">确认</a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      message: {
        type: String,
        default: ''
      },
      num: {
        type: Number,
        default: -1
      },
      idx: {
        type: Number,
        defalut: -1
      }
    },
    data() {
      return {
        showFlag: false
      }
    },
    methods: {
      show() {
        this.showFlag = true
      },
      hide() {
        this.showFlag = false
      },
      cancle() {
        this.$emit('cancle')
        this.hide()
      },
      sure() {
        if(this.idx < 0) {
          this.$emit('sure', this.num, this.idx)
        } else {
          this.$emit('sure', this.num)
        }

        this.hide()
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .toast-tip
    position: fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    background: rgba(0, 0, 0, .5)
    z-index 10000
    .confirm
      width: 84vw
      background: #fff
      position: absolute
      left: 50%
      margin-left: -42vw
      top: 50%
      margin-top: -57px
      height: 114px
      text-align: center
      box-sizing: border-box
      h3
        height: 70px
        border-bottom: 1px solid #eee
        line-height: 70px
        box-sizing: border-box
      .btn
        line-height: 44px
        height: 44px

        display: flex
        box-sizing: border-box
        a
          flex: 0 0 50%
          display: block
        a:first-child
          border-right: 1px solid #eee
</style>




