<template>
  <div class="order-detail">
    <scroll class="order-detail-scroll">
      <!--<section class="express-detail">-->
      <!--<h4>已签收，签收人已签收，签收人已签收，签收人已签收，签收人已签收，签收人已签收，签收人已签收，签收人</h4>-->
      <!--<small>2018-10-29</small>-->
      <!--</section>-->
      <section class="confirm-address">
        <div class="name-phone">
          <aside>收件人：{{addressList.user_name}}</aside>
          <aside>{{addressList.tel}}</aside>
        </div>
        <div class="address">
          收货地址：{{addressList.detail}}
        </div>
      </section>
      <section class="product-list">
        <section class="product-content">
          <ul>
            <li v-for="(item, index) in productList">
              <section class="product-pic">
                <img :src="item.cover.url" alt="">
              </section>
              <section class="product-info">
                <div class="checked-show">
                  <h4>
                    <span>{{item.title}}</span>
                    <a>{{item.deliver_type}}</a>
                  </h4>
                  <section class="checked-price">
                    <span class="price"><small>￥</small>{{item.price}}</span>
                    <span class="num">×{{item.count}}</span>
                  </section>
                </div>
              </section>

            </li>
          </ul>
        </section>
        <section class="confirm-order-info">
          <h3>订单信息</h3>
          <div class="info-box">
            <ul>
              <li>
                <span>商品状态</span>
                <span class="orange">{{confirmOrder.orderStatus}}</span>
              </li>
              <li>
                <span>订单编号</span>
                <span>{{confirmOrder.sn}}</span>
              </li>
              <li>
                <span>商品总价</span>
                <span>￥{{confirmOrder.price}}</span>
              </li>
              <li>
                <span>运费</span>
                <span>￥{{confirmOrder.freight}}</span>
              </li>
              <li>
                <span>创建时间</span>
                <span>{{confirmOrder.created_at}}</span>
              </li>
              <li>
                <span>支付时间</span>
                <span>{{paidAt}}</span>
              </li>
              <li>
                <span>发货时间</span>
                <span>{{deliveredAt}}</span>
              </li>
              <li>
                <span>确认收货时间</span>
                <span>{{confirmedAt}}</span>
              </li>
            </ul>
          </div>
          <div class="pay-count">
            <span>{{totalWord}}</span>
            <span>￥{{totalPrice}}</span>
          </div>
        </section>
      </section>
    </scroll>
    <div class="loading-content" v-show="!productList.length">
      <loading class="loading-pic"></loading>
    </div>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import  {axiosGet} from 'api/config'

  export default {
    created() {
      let {id} = this.$route.query
      this.getData(id)
    },
    data() {
      return {
        addressList: {},
        productList: [],
        confirmOrder: {},
        paidAt: '',
        deliveredAt: '',
        confirmedAt: ''
      }
    },
    computed: {
      totalPrice() {
        return this.confirmOrder.price + this.confirmOrder.freight
      },
      totalWord() {
        return this.confirmOrder.status == 'wait_pay' ? '需付款' : '已付款'
      }
    },
    methods: {
      getData(id) {
        axiosGet(`/orders/${id}`).then(res => {
          if (res.status === 200) {
            console.log(res)
            this.confirmOrder = res.data.data
            switch (this.confirmOrder.status) {
              case 'wait_pay':
                this.$set(this.confirmOrder, 'orderStatus', '待付款')
                break
              case 'wait_deliver':
                this.$set(this.confirmOrder, 'orderStatus', '待发货')
                break
              case 'wait_wait_recv':
                this.$set(this.confirmOrder, 'orderStatus', '待收货')
                break
              case 'finish':
                this.$set(this.confirmOrder, 'orderStatus', '已完成')
                break
            }
            console.log(this.confirmOrder.paid_at)
            this.paidAt = this.backWord(this.confirmOrder.paid_at)
            this.deliveredAt = this.backWord(this.confirmOrder.delivered_at)
            this.confirmedAt = this.backWord(this.confirmOrder.confirmed_at)
            this.addressList = res.data.data.address
            this.productList = [...res.data.data.items, ...res.data.data.stageItems]
          }
        })
      },
      backWord(str) {
        return str != null ? str : '暂无信息'
      }
    },
    components: {
      Scroll,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .order-detail
    background: #f2f2f2
    z-index: 11
    pos-all()
    .loading-content
      pos-all()
      background: #ffffff
      .loading-pic
        position: absolute
        left: 50%
        top: 50%
        margin-left: -10px
        margin-top: -10px
    .confirm-address
      min-height: 60px
      width: 100%
      padding: 10px 15px
      box-sizing: border-box
      background: #fff
      font-size: 14px
      .name-phone
        flex: 0 0 100%
        width: 100%
        display: flex
        justify-content: space-between
        aside
          flex: 1 1 auto
          margin-right: 10px
      .address
        margin-top: 10px
    .express-detail
      background: #ffffff
      padding: 10px 15px
      box-sizing: border-box
      display: flex
      flex-direction: column
      justify-content: center
      min-height: 60px
      border-bottom: 1px solid #f2f2f2
      h4
        flex: 1 1 auto
      small
        margin-top: 5px
        color: #999
        flex: 0 0 12px
    .product-list
      margin-top: 12px
      .product-content
        background: #ffffff
        li
          margin: 15px 0
          padding: 0 15px
          display: flex
          align-items: center
          .product-pic
            flex: 0 0 80px
            width: 80px
            height: 80px
            margin: 0 15px 0 8px
            img
              height: 100%
              width: 100%
          .product-info
            box-sizing: border-box
            flex: 1 1 auto
            height: 80px
            .checked-show
              padding: 10px 0
              display: flex
              flex-direction: column
              justify-content space-between
              height: 60px
              h4
                align-items: left
                display: flex
                span
                  flex: 1 1 auto
                  margin-right: 20px
                  font-size: 14px
                a
                  flex: 0 0 60px
                  height: 16px
                  line-height: 16px
                  border-radius: 8px
                  text-align: center
                  width: 60px
                  background: #ff8852
                  color: #fff
                  font-size: 12px
              .checked-price
                display: flex
                span
                  flex: 0 0 50%
                .num
                  color: #999
                  text-align: right
                .price
                  color: #ff8852
                  small
                    font-size: 12px
      .confirm-order-info
        background: #ffffff
        margin-top: 12px
        span
          flex: 0 0 50%
        span:last-child
          text-align: right
        h3
          color: #ff8852
          padding: 0 15px
          box-sizing: border-box
          border-bottom: 1px solid #f2f2f2
          height: 32px
          line-height: 32px
        .info-box
          padding: 10px 15px
          box-sizing: border-box
          border-bottom: 1px solid #f2f2f2
          li
            display: flex
            height: 18px
            line-height: 18px
            font-size: 12px
            color: #999
            .orange
              color: #ff8852
        .pay-count
          display: flex
          padding: 0 15px
          height: 32px
          line-height: 32px
          border-bottom: 1px solid #f2f2f2
          box-sizing: border-box
          span:last-child
            color: #ff8852
</style>



