<template>
  <div class="entrust">
    <div class="entrust-content">
      <h3>
        <span @click="showFlagTrue">鸡蛋代卖</span>
        <span @click="showFlagFalse">母鸡代卖</span>
      </h3>
      <div class="entrust-tab" v-show="showFlag">
        <section class="entrust-part">
          <ul>
            <li>
              <span>黄壳鸡蛋</span>
              <span><a class="btn">代卖</a></span>
            </li>
            <li>
              <span>黄壳鸡蛋</span>
              <span><a class="btn">代卖</a></span>
            </li>
          </ul>
        </section>
        <section class="entrust-tip">
          养母鸡所享有的认养价鸡蛋（360枚/1只母鸡）可以委托平台代卖
          所得利润为{{eggPrice}}元/枚。鸡蛋代卖后，母鸡在一定时间内不能宰杀，
          如：代卖了30枚，则须认养两个月后才可宰杀。
        </section>
      </div>
      <div class="entrust-tab" v-show="!showFlag">
        <section class="entrust-part">
          <ul>
            <li>
              <span>母鸡</span>
              <span><a class="btn">代卖</a></span>
            </li>
            <li>
              <span>公鸡</span>
              <span><a class="btn">代卖</a></span>
            </li>
          </ul>
        </section>
        <section class="entrust-tip">
          母鸡在养满两年后可以委托平台代卖，代卖价格为{{chickenPrice}}元/只。
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import {axiosGet} from 'api/config'

  export default {
    data() {
      return {
        showFlag: true,
        eggPrice: 0,
        chickenPrice: 0
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/sellsale').then(res => {
          if(res.status === 200) {
            this.eggPrice = res.data.egg
            this.chickenPrice = res.data.chicken
          }
        })
      },
      showFlagTrue() {
        this.showFlag = true
      },
      showFlagFalse() {
        this.showFlag = false
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .entrust
    background: #f2f2f2
    z-index: 11
    pos-all()
    .entrust-content
      h3
        display: flex
        height: 44px
        line-height: 44px
        color: #fff
        span
          text-align: center
          flex: 0 0 50%
          background: #ff8852
        span:last-child
          background: #ff6700
      .entrust-part
        background: #fff
        li
          padding: 0 15px
          display: flex
          line-height: 50px
          height: 50px
          border-bottom: 1px solid #f2f2f2
          box-sizing: border-box
          span
            flex: 0 0 50%
          span:last-child
            text-align: right
            a
              padding: 5px 15px
              border-radius: 6px
              background: #ff8852
              color: #fff
              text-align: right
      .entrust-tip
        line-height: 20px
        color: #999
        background: #fff
        padding: 15px
</style>



