<template>
  <div adpot-chicken>
    <scroll class="adopt" :data="adpotArr">
      <div class="adopt-scroll">
        <adopt-list :adpotArr="adpotArr" @eatChicken="eatChicken" @eatEgg="eatEgg" @delChicken="delChicken"></adopt-list>
      </div>
    </scroll>
    <scroll class="egg-list" :data="eggList" v-show="showFlag" @click="hide">
      <div class="product-egg">
        <ul>
          <li v-for="(item, index) in eggList" @click.stop="takeEgg(index)">
            <div class="product-pic">
              <img :src="item.cover.url" alt="">
            </div>
            <h5>{{item.title}}</h5>
            <section class="price">
              <span><small>￥</small>{{item.price}}</span>元
            </section>
          </li>
        </ul>
      </div>
    </scroll>
    <div class="no-adpot" @click="goOrder" v-show="adpotArr.length == 0">
      <img src="./images/share.jpg" alt="">
    </div>
    <div class="show-loading" v-show="adpotArr.length === 0 && showLoading">
      <loading class="loading"></loading>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import Loading from 'base/loading/loading'
  import AdoptList from 'base/adoptList/adoptList'
  import {axiosGet, axiosPost, axiosDelete} from 'api/config'

  export default {
    data() {
      return {
        adpotArr: [],
        tempOrder: [],
        eggList: [],
        showFlag: false,
        renyId: -1,
        eggIdx: -1,
        message: '',
        showLoading: true
      }
    },
    created() {
      axiosGet('/support').then(res => {
        console.log(res)
        if(res.status === 200) {
          this.showLoading = false
          this.adpotArr = this._normalizeData(res.data.data)
        }
      })
    },
    methods: {
      eatChicken(idx, id) {
        console.log(this.adpotArr[idx])
        if(this.adpotArr[idx].can_kill == 0) {
          this.message = '这只母鸡不能宰杀'
          this.$refs.confirm.show()
          return
        }
        this.tempOrder = []
        this.tempOrder.push({
          id,
          count: 1,
          is_reny: 1
        })
        let arr = JSON.stringify(this.tempOrder)
        this.$router.push({
          path: `/app/submitorder?type=${arr}&isreny=1&renyid=${this.adpotArr[idx].id}`
        })
      },
      eatEgg(idx, id) {
        axiosGet('/items/type/egg').then(res => {
          if(res.status === 200) {
            this.eggList = res.data.data
          }
        })
        this.eggIdx = idx
        this.showFlag = true
      },
      takeEgg(idx) {
        console.log(this.eggList[idx])

        this.tempOrder = []
        this.tempOrder.push({
          id: this.eggList[idx].id,
          count: 1,
          is_reny: 1
        })
        let arr = JSON.stringify(this.tempOrder)
        console.log(idx)
        this.$router.push({
          path: `/app/submitorder?type=${arr}&isreny=2&renyid=${this.adpotArr[this.eggIdx].id}&count=${this.adpotArr[this.eggIdx].egg_num}`
        })
      },
      goOrder() {
        let tempArr = []
        tempArr.push({
          id: 6,
          count: 1
        })
        let arr = JSON.stringify(tempArr)
        this.$router.push({
          path: `/app/submitorder?type=${arr}&adopt=1`
        })
      },
      hide() {
        this.showFlag = false
      },
      delChicken(idx, id) {
        axiosDelete(`/support/${id}`).then(res => {
          if(res.status === 200) {
            if(res.data.status == 1) {
              this.message = res.data.msg
            }
          }
        })
        console.log(this.adpotArr.slice(idx, 1))
        this.adpotArr = this.adpotArr.slice(idx, 1)
      },
      _normalizeData(arr) {
        arr.map((item, index) => {
          if( item.status == 0 ) {
            if(!item.can_kill) {
              this.$set(arr[index].item, 'stateInfo', `不可宰杀，还需饲养${item.can_day_kill}天`)
            } else {
              this.$set(arr[index].item, 'stateInfo', `可宰杀`)
            }
          } else if( item.status == 1 ) {
            this.$set(arr[index].item, 'stateInfo', `已宰杀但还有成本蛋为提取`)
          } else {
            this.$set(arr[index].item, 'stateInfo', `已宰杀并且已经提完成本蛋`)
          }

        })
        return arr
      }
    },
    components: {
      Scroll,
      AdoptList,
      Confirm,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .adopt, .egg-list
    background: #f2f2f2
    z-index: 11
    pos-all()
  .egg-list
    z-index: 12
    background: #ffffff
    ul
      display: flex
      box-sizing: border-box
      padding: 2vw
      flex-wrap: wrap
      li
        flex: 0 0 46vw
        border: 1px solid #eee
        box-sizing: border-box
        margin: 1vw
        .product-pic
          width: 36vw
          margin: 0 auto
          height: auto
          img
            width: 36vw
            height: auto
        h5
          padding: 0 10px
          color: #999
          font-size: 14px
        .price
          padding: 30px 10px 10px
          color: #999
          span
            font-size: 16px
            color: #cd1d1d
            small
              font-size: 12px
  .no-adpot
    position: relative
    z-index:13
    width: 100%
    background: #e45667
    img
      height:auto
      width: 100%
  .show-loading
    background: #ffffff
    z-index: 14
    pos-all()
    .loading
      position: absolute
      margin-left: -10px
      margin-top: -10px
      top: 50%
      left: 50%
</style>



