<template>
  <scroll class="member">
    <div class="member-scroll">
      <section class="member-info">
        <div class="member-pic">
          <div class="member-control">
            <img :src="userInfo.avatarUrl" alt="">
          </div>
          <div class="member-phone">
            <h3>{{userInfo.nickname}}</h3>
            <section class="phone">
              <img src="./images/phone-icon@2x.png" alt="">
              <span :class="phone ? 'active' : ''" @click="bindPhone">{{phone}}</span>
            </section>
          </div>
        </div>
        <aside>
          <img src="./images/arrow@2x.png" alt="">
        </aside>
      </section>
      <section class="account">
        <ul>
          <li>
            <span>余额</span>
            <a>{{money}}</a>
          </li>
          <li>
            <span>可提现</span>
            <a>{{canMoney}}</a>
          </li>
        </ul>
      </section>
      <section class="order">
        <div class="order-padding">
          <h3 @click="goOrder('all')">
            <span>我的订单</span>
            <small>查看全部订单</small>
          </h3>
        </div>
        <div class="order-status">
          <ul>
            <li @click="goOrder('wait')">
              <a><img src="./images/wait@2x.png" alt=""></a>
              <span>待付款</span>
              <small v-show="payMent">{{payMent}}</small>
            </li>
            <li @click="goOrder('ship')">
              <a><img src="./images/pending@2x.png" alt=""></a>
              <span>待发货</span>
              <small v-show="ship">{{ship}}</small>
            </li>
            <li @click="goOrder('receipt')">
              <a><img src="./images/shipping@2x.png" alt=""></a>
              <span>待收货</span>
              <small v-show="receipt">{{receipt}}</small>
            </li>
            <li @click="goOrder('finish')">
              <a><img src="./images/complete@2x.png" alt=""></a>
              <span>已完成</span>
              <small v-show="finish">{{finish}}</small>
            </li>
            <li @click="goOrder('stage')">
              <a><img src="./images/dispatch@2x.png" alt=""></a>
              <span>分期配送</span>
              <small v-show="payMent">{{stage}}</small>
            </li>
          </ul>
        </div>
      </section>
      <section class="func-list">
        <div class="func-item">
          <router-link to="/app/car" class="route-item">
            <h4>
              <i><img src="./images/car@2x.png" alt=""></i>
              <span>购物车</span>
            </h4>
          </router-link>
          <router-link to="/app/adopt" class="route-item">
            <h4>
              <i><img src="./images/chicken@2x.png" alt=""></i>
              <span>我的认养</span>
            </h4>
          </router-link>
          <router-link to="/app/couponlist" class="route-item">
            <h4>
              <i><img src="./images/coupon_list@2x.png" alt=""></i>
              <span>优惠券列表</span>
            </h4>
          </router-link>
          <router-link to="/app/store" class="route-item">
            <h4>
              <i><img src="./images/store@2x.png" alt=""></i>
              <span>我的仓库</span>
            </h4>
          </router-link>
          <router-link to="/app/address" class="route-item">
            <h4>
              <i><img src="./images/address@2x.png" alt=""></i>
              <span>收货地址管理</span>
            </h4>
          </router-link>
          <router-link to="/app/coupon?type=1" class="route-item">
            <h4>
              <i><img src="./images/coupon@2x.png" alt=""></i>
              <span>我的优惠券</span>
            </h4>
          </router-link>
          <router-link to="/app/share" class="route-item">
            <h4>
              <i><img src="./images/share@2x.png" alt=""></i>
              <span>分享中心</span>
            </h4>
          </router-link>
          <router-link to="/app/bind" class="route-item">
            <h4>
              <i><img src="./images/bind@2x.png" alt=""></i>
              <span>账号绑定</span>
            </h4>
          </router-link>
          <router-link to="/app/wallet" class="route-item">
            <h4>
              <i><img src="./images/wallet@2x.png" alt=""></i>
              <span>我的钱包</span>
            </h4>
          </router-link>
          <router-link to="/app/entrust" class="route-item">
            <h4>
              <i><img src="./images/entrust@2x.png" alt=""></i>
              <span>委托代卖</span>
            </h4>
          </router-link>
          <router-link to="/app/exchange" class="route-item">
            <h4>
              <i><img src="./images/exchange@2x.png" alt=""></i>
              <span>兑换中心</span>
            </h4>
          </router-link>
        </div>
      </section>
      <router-view />
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import axios from 'axios'
  import {axiosGet, axiosAll, token} from 'api/config'

  export default {
    components: {
      Scroll
    },
    data() {
      return {
        userInfo: {},
        phone: '',
        payMent: 0,
        receipt: 0,
        ship: 0,
        finish: 0,
        receipt: 0,
        stage: 0,
        money: 0,
        canMoney: 0
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        let arr = []
        arr.push(this.orderStatus(), this.usersShow(), this.walletFunc())
        axiosAll(arr).then(axios.spread((acct, perms, res) => {
          console.log(perms)
          if(perms.status === 200) {
            this.userInfo = perms.data.data
            this.phone = this.userInfo.phone ? this.userInfo.phone : '绑定手机'
          }

          if(acct.status === 200) {
            this.payMent = acct.data.data.payment
            this.ship = acct.data.data.Ship
            this.receipt = acct.data.data.Receipt
            this.finish = acct.data.data.finish
            this.stage = acct.data.data.stage
          }

          if(res.status === 200) {
            this.money = res.data[0].money
            this.canMoney = res.data[0].can_money
          }
        }))
      },
      orderStatus() {
        return axiosGet('/orders/status')
      },
      usersShow() {
        return axiosGet('/users/show')
      },
      walletFunc() {
        return axiosGet('/wallet')
      },
      goOrder(status) {
        this.$router.push({
          path: `/app/orderall?status=${status}`
        })
      },
      bindPhone() {
        if(!this.userInfo.phone) {
          this.$router.push({
            path: '/app/bind'
          })
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .member
    background: #f2f2f2
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 50px
    .member-info
      box-sizing: border-box
      display: flex
      align-items: center
      height: 100px
      padding: 0 30px
      background: linear-gradient(45deg, #ffca54, #ff9062)
      .member-pic
        flex: 1 1 auto
        height: 70px
        display: flex
        align-items: center
        color: #fff
        font-size: 16px
        .member-control
          width: 70px
          height: 70px
          margin-right: 15px
          img
            border-radius: 100%
            border: 2px solid #ffdbb9
            box-sizing: border-box
            width: 100%
            height: 100%
        .member-phone
          flex: 1 1 auto
          h3
            margin-bottom: 10px
          .phone
            height: 14px
            line-height: 14px
            img
              width: 8px
              height: 14px
            .active
              font-size: 12px
              vertical-align: top
      aside
        flex: 0 0 15px
        height: 26px
        width: 15px
        img
          height: 100%
          width: 100%
    .account
      height: 60px
      background: #fff
      ul
        display: flex
        align-items: center
        justify-content: center
        height: 60px
        li
          flex: 0 0 50%
          box-sizing: border-box
          height: 60px
          padding: 10px 0
          text-align: center
          span, a
            display: block
            height: 24px
          a
            color: #ffab54
        li:first-child
          border-right: 1px solid #f2f2f2
    .order
      background: #fff
      margin-top: 12px
      .order-padding
        padding-left: 15px
        h3
          display: flex
          font-size: 14px
          justify-content: space-between
          height: 36px
          line-height: 36px
          border-bottom: 1px solid #f2f2f2
          padding-right: 15px
          span
            flex: 1 1 auto
          small
            background: url('./images/more-small@2x.png') no-repeat right center
            background-size: 5px 8px
            padding-right: 10px
            text-align: right
            font-size: 12px
            color: #999
            flex: 1 1 auto
      .order-status
        height: 72px
        font-size: 12px
        ul
          display: flex
          justify-content: center
          align-items: center
          height: 72px
          li
            height: 54px
            flex: 0 0 20%
            text-align: center
            position: relative
            span, a
              display: block
            span
              height: 24px
              line-height: 24px
            a
              width: 30px
              margin: 0 auto
              height: 30px
              img
                height: 100%
                width: 100%
            small
              display: block
              position: absolute
              background: #fb646b
              top: 0px
              left:50%
              margin-left: 14px
              font-size: 12px
              color: #fff
              text-align: center
              border-radius: 50%
              width: 18px
              height: 18px
              line-height: 18px
    .func-list
      margin-top: 12px
      background: #fff
      .func-item
        padding-left: 15px
        .route-item
          height: 44px
          padding-right: 15px
          border-bottom: 1px solid #f2f2f2
          box-sizing: border-box
          display: block
          h4
            display: flex
            height: 44px
            align-items: center
            background: url('./images/more@2x.png') no-repeat right center
            background-size: 6px 10px
            span
              font-size: 14px
              color: #333
            i
              flex: 0 0 24px
              margin-right: 10px
              width: 24px
              height: 24px
              img
                height: 100%
                width: 100%
</style>



