<template>
  <div class="profile">
    <div v-html="content.content">
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      content: {
        type: Object,
        default: {}
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .profile
    padding: 15px
    background: #fff
    img
      width: 100%
      height: 100%
</style>



