<template>
  <div class="chickens-store-list">
    <scroll class="chicken-store" :data="category">
      <div class="chicken-scroll">
        <store-list :category="category" :categoryId="categoryId" @dispatchEgg="dispatchEgg" @dispatchChicken="dispatchChicken"></store-list>
      </div>
    </scroll>
    <div class="input-phone" v-show="showFlag">
      <div class="input-part">
        <input type="text" placeholder="请输入赠送人的手机号" v-model="phoneQuery">
        <input type="text" placeholder="输入赠送的数量" v-model="countQuery">
        <a @click="showInput">赠送</a>
      </div>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import StoreList from 'base/storeList/storeList'
  import {axiosGet, axiosPost} from 'api/config'
  import {mapMutations} from 'vuex'

  export default {
    data() {
      return {
        category: [],
        categoryId: 1,
        showFlag: false,
        phoneQuery: '',
        countQuery: '',
        currentPro: [],
        message: ''
      }
    },
    created() {
      axiosGet('/storehouse/chicken').then(res => {
        this.category = res.data.data
      })
    },
    methods: {
      dispatchChicken(idx) {
        this.showFlag = true
        this.currentPro = this.category[idx]
        this.id = this.currentPro.id
      },
      showInput() {
        if(this.phoneQuery.length != 11) {
          this.message = '请输入正确的手机号'
          this.$refs.confirm.show()
          return
        }
        axiosPost(`/storehouse/senduser/${this.id}`, {
          data: {
            count: this.countQuery,
            phone: this.phoneQuery
          }
        }).then(res => {
          console.log(res)
          if(res.status === 200) {
            if(res.data.status == 1) {
              this.showFlag = false
              this.message = res.data.msg
              this.$refs.confirm.show()
              setTimeout(() => {
                this.$router.push({
                  path: '/app/store'
                })
              }, 1500)
            } else {
              this.message = res.data.msg
              this.$refs.confirm.show()
              setTimeout(() => {
                this.$router.push({
                  path: '/app/store'
                })
              }, 1500)
            }
          }
        })
      },
      dispatchEgg(idx) {
        console.log(this.category[idx])
        this.setStoreEggNum(this.category[idx].num)
        let arr = [{"id": this.category[idx].item.id, "count": this.category[idx].num}]
        let arr1 = JSON.stringify(arr)
        this.$router.push({
          path: `/app/submitorder?type=${arr1}&eggstore=1&eggId=${this.category[idx].id}`
        })
      },
      ...mapMutations({
        setStoreEggNum: 'SET_STOREEGGNUM'
      })
    },
    components: {
      Scroll,
      StoreList,
      Confirm
    },
    watch: {
      countQuery(newVal, oldVal) {
        console.log(newVal)
        let temp = /^[0-9]*$/
        if(!temp.test(newVal)) {
          this.message = '请输入数字'
          this.countQuery = ''
          this.$refs.confirm.show()
        }
        if(newVal < 0) {
          this.countQuery = 1
        }
        if(newVal > this.currentPro.num) {
          this.countQuery = this.currentPro.num
        }
      },
      phoneQuery(newVal, oldVal) {
        let temp = /^[0-9]*$/
        if(!temp.test(newVal)) {
          this.message = '请输入数字'
          this.phoneQuery = ''
          this.$refs.confirm.show()
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"

  .chicken-store
    pos-all()
    background: #f2f2f2
    z-index: 11
  .input-phone
    pos-all()
    z-index: 12
    background: rgba(0, 0, 0, .7)
    .input-part
      margin: 0 auto
      height: 200px
      padding: 0 15px
      display: flex
      flex-direction: column
      justify-content: center
      box-sizing: border-box
      width: 80%
      background: #ffffff
      position: absolute
      left: 50%
      top: 50%
      margin: -100px 0 0 -40%
      input
        height: 44px
        line-height: 44px
        flex: 0 0 44px
        border: 1px solid #f2f2f2
        margin-bottom: 12px
      a
        flex: 0 0 44px
        width: 100%
        text-align: center
        color: #fff
        background: #ff8852
        height: 44px
        line-height: 44px
</style>



