<template>
  <div class="bind">
    <div class="bind-content">
      <input type="text" placeholder="请输入电话号码" v-model="query">
    </div>
    <div class="bind-btn">
      <a @click="bindPhone">
        绑定手机
      </a>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Confirm from 'base/confirm/confirm'
  import {axiosPost} from 'api/config'

  export default {
    data() {
      return {
        query: '',
        message: ''
      }
    },
    methods: {
      bindPhone() {
        if(this.query.length != 11) {
          this.message = '请输入正确电话号码'
          this.$refs.confirm.show()
          return
        }
        axiosPost('/users/store', {
          data: {
            phone: this.query
          }
        }).then(res => {
          console.log(res)
          if(res.status === 200) {
            if(res.data.status == 0) {
              this.message = res.data.msg
              this.$refs.confirm.show()
              return
            }
            this.message = '绑定成功'
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.push({
                path: '/app/me'
              })
            }, 1500)
          }
        })
      }
    },
    watch: {
      query(newVal, oldVal) {
        let temp = /^[0-9]*$/
        if(!temp.test(newVal)) {
          this.message = '请输入数字'
          this.query = ''
          this.$refs.confirm.show()
        }
      }
    },
    components: {
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'
  .bind
    pos-all()
    background: #f2f2f2
    z-index: 11
    .bind-content
      height: 44px
      line-height: 44px
      padding: 0 15px
      border-bottom: 1px solid #f2f2f2
      background: #fff
    .bind-btn
      padding: 0 15px
      margin-top: 12px
      a
        display: block
        background: #ff8852
        color: #fff
        height: 44px
        line-height: 44px
        text-align: center
        border-radius: 6px
</style>



