<template>
  <div class="wallet">
    <div class="wallet-account">
      <section class="wallet-package">
        <i><img src="./images/wallet@2x.png" alt=""></i>
        <h5>余额</h5>
        <span>{{money.money}}</span>
      </section>
      <section class="wallet-package">
        <i><img src="./images/account@2x.png" alt=""></i>
        <h5>可提现</h5>
        <span>{{money.can_money}}</span>
      </section>
    </div>
    <div class="wallet-list">
      <div class="wallet-item">
        <router-link to="/app/countsearch" class="route-item">
          <h4>
            <i><img src="./images/search@2x.png" alt=""></i>
            <span>账目查询</span>
          </h4>
        </router-link>
        <a class="route-item" @click="goRecharge">
          <h4>
            <i><img src="./images/recharge@2x.png" alt=""></i>
            <span>微信充值</span>
          </h4>
        </a>
        <a class="route-item" @click="goMoney">
          <h4>
            <i><img src="./images/coupon_recharge@2x.png" alt=""></i>
            <span>充值券充值</span>
          </h4>
        </a>
        <!--<router-link to="/app/moneycoupon" class="route-item">-->
          <!--<h4>-->
            <!--<i><img src="./images/coupon_recharge@2x.png" alt=""></i>-->
            <!--<span>充值券充值</span>-->
          <!--</h4>-->
        <!--</router-link>-->
        <router-link to="/app/cash" class="route-item">
          <h4>
            <i><img src="./images/cash@2x.png" alt=""></i>
            <span>申请提现</span>
          </h4>
        </router-link>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
  import {axiosGet} from 'api/config'

  export default {
    data() {
      return {
        money: {}
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/wallet').then(res => {
          this.money = res.data[0]
        })
      },
      goRecharge() {
        window.location.href = '/app/recharge/'
      },
      goMoney() {
        window.location.href = `/app/moneycoupon/`
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .wallet
    background: #f2f2f2
    z-index: 11
    pos-all()
    .wallet-account
      display: flex
      background: #ff9b56
      height: 120px
      .wallet-package
        display: flex
        flex-direction: column
        justify-content: center
        border-right: 1px solid #de864a
        flex: 0 0 50%
        align-items: center
        i
          height: 48px
          width: 48px
          img
            width: 100%
            height: 100%
        h5
          color: #ffe0ca
          margin-bottom: 8px
        span
          font-size: 16px
          color: #fff
    .wallet-list
      background: #fff
      .wallet-item
        padding-left: 15px
        .route-item
          height: 44px
          padding-right: 15px
          border-bottom: 1px solid #f2f2f2
          box-sizing: border-box
          display: block
          h4
            display: flex
            height: 44px
            align-items: center
            background: url('./images/more@2x.png') no-repeat right center
            background-size: 6px 10px
            span
              font-size: 14px
              color: #333
            i
              flex: 0 0 24px
              margin-right: 10px
              width: 24px
              height: 24px
              img
                height: 100%
                width: 100%
</style>



