<template>
  <scroll class="notice-article" ref="scroll" :data="articleList" :pullUpLoad="pullUpLoad" @pullingUp="pullingUp">
    <article-list ref="articleList" class="article-list" :articleList="articleList" @wxArticle="wxArticle"></article-list>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import ArticleList from 'base/articleList/articleList'
  import {axiosGet} from 'api/config'

  export default {
    data() {
      return {
        articleList: [],
        pullUpLoad: true,
        page: 0,
        lastPage: 0,
        bodyHeight: 0
      }
    },
    created() {
      this.$nextTick(() => {
        this.bodyHeight = this.$refs.scroll.$el.clientHeight
      })
      this.getData()
    },
    methods: {
      getData() {
        this.moreData(this.page)
      },
      moreData(page) {
        if(this.lastPage <= page && this.lastPage != 0) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet('/material/articles',{
          params: {
            scope: 'notice',
            page: this.page
          }
        }).then(res => {
          if(res.status === 200) {
            if(this.lastPage != res.data.meta.last_page) {
              this.lastPage = res.data.meta.last_page
            }
            this.articleList = [...this.articleList, ...res.data.data]
            //判断是不是满屏，无满屏填充满屏
            this.$nextTick(() => {
              if(this.$refs.articleList.$el.clientHeight <= this.bodyHeight) {
                this.moreData(this.page)
              }
            })
          }
        })
      },
      wxArticle(idx) {
        window.location.href = this.articleList[idx].url + '/'
      },
      pullingUp() {
        this.moreData(this.page)
      }
    },
    components: {
      Scroll,
      ArticleList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .notice-article
    background: #f2f2f2
    z-index: 11
    pos-all()
    .article-list
      background: #ffffff
</style>



