<template>
  <scroll class="coupon" :data="couponList">
    <coupon-temp-list :couponList="couponList" :useIt="useIt" @useCoupon="useCoupon"></coupon-temp-list>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import CouponTempList from 'base/couponTempList/couponTempList'
  import {axiosGet, token} from 'api/config'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        couponList: [],
        useIt: true
      }
    },
    created() {

      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/me/coupons').then(res => {
          if(res.status === 200) {
            this.couponList = this._normalizeData(res.data.data)
          }
        })
      },
      _normalizeData(arr) {
        arr.map(item => {
          item.created_at = this.mkdata(item.created_at)
          item.end_time = this.mkdata(item.end_time)
          if(item.money === 0) {
            item.msg = '无门槛'
          } else {
            item.msg = `满${item.base_money}减`
          }
        })
        return arr
      },
      mkdata(str) {
        let idx = str.indexOf(' ')
        let length = str.length
        let st = str.slice(0, idx)
        return st
      },
      useCoupon(id) {
        if(this.$route.query.type == 1) {
          this.$router.push({
            path: '/app/categoryproduct'
          })
        } else {
          this.$router.back()
        }
      }
    },
    components: {
      Scroll,
      CouponTempList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .coupon
    background: #f2f2f2
    z-index: 11
    pos-all()

</style>



