import {axiosGet, axiosPost} from './config'
const vc = require('vconsole')

const wx = window.wx
export default {
  created() {
    const v = new vc()

  },
  data() {
    return {
      appId: null,
      message: ''
    }
  },
  methods: {
    weChatConfig(url) {
      axiosGet('/jsSdk',{
        params: {
          url
        }
      }).then(res => {
        console.log(res)
        let { appId, timestamp, nonceStr, signature } = res.data
        this.appId = appId
        wx.config({
          // debug: true,
          appId,
          timestamp,
          nonceStr,
          signature,
          jsApiList: [
            'scanQRCode',
            'chooseWXPay',
            'onMenuShareTimeline',
            'updateTimelineShareData'
          ]
        })

      })
    },
    async goScan() {
      let temp = -1
      let tempCode = ''
      let tempPwd = ''
      wx.scanQRCode({
        needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
        scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
        success: function (res) {

          temp = res.resultStr.indexOf(' ')
          tempCode = res.resultStr.substr(0, temp)
          tempPwd = res.resultStr.substr(temp+1, res.resultStr.length - 1)
          axiosPost('/me/qrcode', {
            data: {
              sn: tempCode,
              password: tempPwd
            }
          }).then(res => {
            if(res.status === 200) {
              this.message = res.data.msg
              this.$refs.confirm.show()
              setTimeout(() => {
                this.$router.push({
                  path: '/app/me'
                })
              }, 1500)
            }
          })
        }
      })
    },
    goShareData() {
      wx.ready(() => {
        wx.onMenuShareTimeline({
          title: '惊爆，无限分享无限领取认养代金券',
          link: 'http://www.fujianqiuge.com/home/',
          imgUrl: '',
          success: function (res) {
            // 设置成功
            axiosGet('/shareGetCoupon').then(itemData => {
              if(itemData.status === 200) {
                this.message = itemData.data.msg
                this.$refs.confirm.show()
                setTimeout(() => {
                  this.$router.push({
                    path: '/app/coupon?type=1'
                  })
                }, 1500)
              }
            })
          }
        })
      })

    },
    goPay(res) {

      wx.chooseWXPay({
        timestamp: res.timeStamp,
        nonceStr: res.nonceStr,
        package: res.package,
        signType: res.signType,
        paySign: res.paySign,
        success: item => {
          console.log(item)
          try {
            window.WeixinJSBridge.log(item.err_msg)
          } catch(e) {
            console.log(e)
          }

          if(item.err_msg === 'get_brand_wcpay_request:ok') {
            this.message = '支付成功'
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.push({
                path: '/app/me'
              })
            }, 1500)
          } else if(item.err_msg === 'get_brand_wcpay_request:fail'){
            this.message = '支付失败'
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.push({
                path: '/app/me'
              })
            }, 1500)
          } else if(item.err_msg === 'get_brand_wcpay_request:cancel') {
            this.message = '支付取消'
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.push({
                path: '/app/me'
              })
            }, 1500)
          }
        }
      })
    }
  }
}



