<template>
  <div class="exchange">
    <ul>
      <li @click="objCoupon">
        <h4>喔喔实物兑换券</h4>
        <section class="exchange-menu">
          <a>黄壳蛋券</a>
          <a>绿壳蛋券</a>
          <a>母鸡券</a>
          <a>公鸡券</a>
        </section>
      </li>
      <li @click="moneyCoupon">
        <h4>喔喔钱包充值</h4>
        <section class="exchange-menu">
          <a>充值券</a>
        </section>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    methods: {
      objCoupon() {
        this.$router.push({
          path: '/app/objcoupon'
        })
      },
      moneyCoupon() {
        window.location.href = '/app/moneycoupon'
//        this.$router.push({
//          path: '/app/moneycoupon'
//        })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .exchange
    background: #f2f2f2
    z-index: 11
    pos-all()
    ul
      padding: 0 15px
      li
        margin-top: 12px
        h4
          height: 15vw
          color: #ba9e8a
          font-size: 16px
          line-height: 15vw
          padding-left: 30px
          background: url('./images/exchange_bg@2x.png') no-repeat top center
          background-size: 100% auto
        .exchange-menu
          padding: 0 10px
          height: 15vw
          line-height: 15vw
          background: #fff
          border-bottom-left-radius: 10px
          border-bottom-right-radius: 10px
          display: flex
          align-items: center
          a
            width: 15vw
            padding: 0 5px
            margin-right: 5px
            color: #fff
            height: 8vw
            line-height: 8vw
            font-size: 12px
            text-align: center
            border-radius: 4vw
            background: linear-gradient(45deg, #ffce4f, #ff945e)
            display: inline-block
          a:nth-of-type(2)
            background: linear-gradient(45deg, #6ae8a5, #00c886)
          a:nth-of-type(3)
            background: linear-gradient(45deg, #ff8db2, #ff558e)
          a:nth-of-type(4)
            background: linear-gradient(45deg, #83bbff, #6074ff)
</style>



