<template>
  <scroll :data="videoList" class="video" :pullUpLoad="pullUpLoad" @pullingUp="pullingUp">
    <div class="video-list">
      <ul>
        <li v-for="(item, index) in videoList" @click="goVideo(index)">
          <div class="video-pic">
            <img src="./images/video-pic.png" alt="">
          </div>
          <div class="video-content">
            <h4>{{item.name}}</h4>
            <span>创建时间 {{item.created_at}}</span>
          </div>
        </li>
      </ul>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import {axiosGet} from 'api/config'

  export default {
    created() {
      this.getData()
    },
    data() {
      return {
        videoList: [],
        pullUpLoad: true,
        lastPage: 0,
        page: 0
      }
    },
    methods: {
      getData() {
        this.moreData(this.page)
      },
      moreData(page) {
        if(this.lastPage <= page && this.lastPage != 0) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet('/material/videos',{
          params: {
            page: this.page
          }
        }).then(res => {
          console.log(res)
          if(res.status === 200) {
            if(this.lastPage != res.data.last_page) {
              this.lastPage = res.data.last_page
            }
            this.videoList = [...this.videoList, ...res.data.data]
          }
        })
      },
      pullingUp() {
        this.moreData(this.page)
      },
      goVideo(idx) {
        window.location.href = this.videoList[idx].url + '/'
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .video
    background: #f2f2f2
    z-index: 11
    pos-all()
    .video-list
      background: #fff
      ul
        li
          height: 100px
          display: flex
          /*align-items: center*/
          border-bottom: 1px solid #f2f2f2
          .video-pic
            flex: 0 0 100px
            margin-right: 20px
            height: 100px
            width: 100px
            img
              height: 100%
              width: 100%
          .video-content
            display: flex
            padding: 15px 0
            flex-direction: column
            justify-content: space-between
            flex: 1 1 auto
            span
              color: #999
              font-size: 12px
</style>



