<template>
  <div class="company">
    <scroll :data="articleList" ref="scroll" class="company-list" :pullUpLoad="pullUpLoad" @pullingUp="pullingUp">
      <div class="company-scroll">
        <section class="banner" @click="goVideo">
          <img src="./images/banner.jpg" alt="">
        </section>
        <section class="nav-list">
          <ul>
            <li @click="goCompany">
              <a><img src="./images/company@2x.png" alt=""></a>
              <span>企业介绍</span>
            </li>
            <li @click="goActive">
              <a><img src="./images/active@2x.png" alt=""></a>
              <span>公司活动</span>
            </li>
            <li @click="goNotice">
              <a><img src="./images/notice@2x.png" alt=""></a>
              <span>最新公告</span>
            </li>
          </ul>
        </section>
        <section class="article-content">
          <h3><span>鸡笼百科</span><a @click="more">更多</a></h3>
          <article-list :articleList="articleList" @wxArticle="wxArticle"></article-list>
        </section>
      </div>
    </scroll>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import ArticleList from 'base/articleList/articleList'
  import {axiosGet} from 'api/config'

  export default {
    created() {
      this.getData()
    },
    data() {
      return {
        articleList: [],
        pullUpLoad: true,
        page: 0,
        lastPage: 0
      }
    },
    methods: {
      getData() {
        this.moreData(this.page)
      },
      moreData(page) {
        if(this.lastPage <= page && this.lastPage != 0) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet('/material/articles',{
          params: {
            scope: 'normal',
            page: this.page
          }
        }).then(res => {
          if(res.status === 200) {
            if(this.lastPage != res.data.meta.last_page) {
              this.lastPage = res.data.meta.last_page
            }
            this.articleList = [...this.articleList, ...res.data.data]
          }
        })
      },
      goCompany() {
        this.$router.push({
          path: '/app/companymenu'
        })
      },
      pullingUp() {
        this.moreData(this.page)
      },
      wxArticle(idx) {
        //需要加'/'不然会跳转两次重定向
        window.location.href = this.articleList[idx].url + '/'
      },
      more() {
        this.$router.push({
          path: '/app/wxallarticle'
        })
      },
      goActive() {
        this.$router.push({
          path: '/app/activeArticle'
        })
      },
      goNotice() {
        this.$router.push({
          path: '/app/noticeArticle'
        })
      },
      goVideo() {
        this.$router.push({
          path: '/app/videoList'
        })
      }
    },
    components: {
      Scroll,
      ArticleList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .company
    background: #f2f2f2
    z-index: 0
    pos-all()
    .company-list
      pos-all()
      bottom: 50px
      .banner
        width: 100%
        height: 48vw
        border-bottom: 1px solid #f2f2f2
        box-sizing: border-box
        img
          height: 100%
          width: 100%
    .nav-list
      height: 75px
      background: #ffffff
      ul
        display: flex
        height: 75px
        justify-content: center
        align-items: center
        li
          flex: 0 0 33.333%
          font-size: 14px
          text-align: center
          span
            display: block
            margin-top: 5px
          a
            display: block
            width: 36px
            height: 36px
            margin: 0 auto
            img
              height: 100%
              width: 100%
    .article-content
      background: #fff
      margin-top: 12px
      h3
        display: flex
        font-size: 16px
        padding: 0 15px
        border-bottom: 1px solid #f2f2f2
        box-sizing: border-box
        line-height: 44px
        height: 44px
        span, a
          flex: 0 0 50%
        a
          text-align: right
</style>



