<template>
  <div class="submit-order">
    <scroll class="submit-scroll">
      <div class="scroll-part">
        <div class="address-choose" @click="goAddress">
          <div class="name-phone">
            <aside>{{userName}}</aside>
            <aside>{{tel}}</aside>
          </div>
          <div class="address">
            {{addr}}
          </div>
        </div>
        <product-list class="product-list" :showOrder="showOrder" :productList="productList" @showTake="showTake" :express="express" :storeType="storeType"></product-list>
        <!--<div class="fee">-->
          <!--<div class="fee-content">-->
            <!--<span>配送费</span>-->
            <!--<span><small>￥</small>10</span>-->
          <!--</div>-->
          <!--<a>宁德市区指定范围内一次性配送少于20枚鸡蛋，5元运费； 全国范围一次性配送少于30枚鸡蛋，10元运费</a>-->
        <!--</div>-->
        <div class="message change-count" v-if="isReny == 2 || eggType == 1">
          <span>{{eggStr}}</span>
          <input type="text" placeholder="填入所需提取的个数" v-model="countQuery">
        </div>
        <div class="coupon" v-show="renyShow && eggType != 1">
          <span>运费</span>
          <span><small v-show="product.freight">￥</small>{{freight}}</span>
        </div>
        <div class="coupon" @click="goCoupon" v-show="renyShow && eggType != 1">
          <span>请选择优惠券</span>
          <span><small v-show="couponNum">￥</small>{{coupon}}</span>
        </div>
        <div class="message" v-show="eggType != 1">
          <span>买家留言</span>
          <input type="text" placeholder="选填：填写备注或是留言" v-model="query">
        </div>
        <div class="result-info" v-show="eggType != 1">
          <h4>结算说明</h4>
        </div>
      </div>
    </scroll>
    <div class="submit-btn">
      <aside>
        <h5 v-show="renyShow && eggType != 1">合计：<span><small>￥</small>{{allPrice}}</span></h5>
      </aside>
      <a class="btn" @click="goPay">{{renyWord}}</a>
    </div>
    <div class="coupon-part" v-if="showFlag" @click="hide">
      <scroll :data="couponList" class="coupon-scroll" ref="scrollCoupon">
        <coupon-temp-list :couponList="couponList" :useIt="useIt" @useCoupon="useCoupon"></coupon-temp-list>
      </scroll>
    </div>
    <div class="express-choose" v-show="showExpress" @click="hideExpress">
      <ul>
        <li v-for="(item, index) in expressChoose" @click="exChoose(index)">
          {{item}}
        </li>
      </ul>
    </div>
    <dispatching ref="dispatching" :productIndex="productIndex" :routeId="dispatchId" @backData="backData" @backCancel="backCancel"></dispatching>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import ProductList from 'base/product-list/product-list'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import CouponTempList from 'base/couponTempList/couponTempList'
  import Dispatching from 'components/dispatching/dispatching'
  import {axiosPost, axiosGet} from 'api/config'
  import {mapGetters, mapMutations} from 'vuex'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        product: {},
        productList: [],
        couponList: [],
        showOrder: false,
        useIt: true,
        couponNum: false,
        showFlag: false,
        coupon: '请选择',
        query: '',
        addr: '',
        tel: null,
        pcode: '',
        userName: '',
        expressChoose: [],
        expressKes: [],
        express: [],
        showExpress: false,
        upExpress: -1,
        dispatchId: -1,
        productIndex: -1,
        couponId: '',
        typeNum: 0,
        typeCate: 0,
        tempOrder: [],
        productOldList: [],
        tempDispatchIndex: -1,
        oldExpress: [],
        allFreight: 0,
        isReny: -1,
        renyId: -1,
        renyShow: true,
        countQuery: '',
        renyCount: 0,
        eggType: -1,
        storeType: true,
        storeEggId: -1,
        message: '',
        eggStr: '修改提成本蛋数量',
        adopt: -1
      }
    },
    beforeRouteEnter(to, from, next) {
      let arr = to.query.type
      axiosPost('/orders/statement', {
        data: {
          content: arr
        }
      }).then(res => {
        next(vm => {
          vm.setData(res)
        })
      })
    },
    created() {
      // this.productList = MockData.orderInfo.results
      //吃鸡 时候
      this.isReny = this.$route.query.isreny
      this.renyId = this.$route.query.renyid
      this.renyCount = this.$route.query.count
      this.eggType = this.$route.query.eggstore
      this.storeEggId = this.$route.query.eggId
      this.adopt = this.$route.query.adopt
      console.log(this.adopt)
      console.log(this.eggType)
      console.log(this.renyId)
      if(this.isReny == 1) {
        this.renyShow = false
      }
      //仓库下单配送
      if(this.eggType == 1) {
        this.storeType = false
        this.eggStr = '修改提取的鸡蛋数'
        if(this.storeEggNum == 0) {
          this.$router.replace({
            path: '/app/store'
          })
        } else {
          this.renyCount = this.storeEggNum
        }

      }

      this.getAddress()
    },
    beforeDestroy() {
      this.setStoreEggNum(0)
    },
    computed: {
      freight() {
        return this.product.freight ? this.product.freight : '免运费'
      },
      renyWord() {
        return this.renyShow && this.eggType != 1 ? '结算' : '确认'
      },
      allPrice() {
        let temp = 0
        let tempNum = 0
        let tempStr
//        if(typeof this.coupon == 'string') {
//          tempStr = 0
//        } else {
//          tempStr = this.coupon
//        }
        if(this.isReny == 2) {
          this.productList.map(item => {
            console.log(item)
            temp += item.original_price * item.count
          })
        } else {
          this.productList.map(item => {
            console.log(item)
            temp += item.unit_price * item.count
          })
        }
        tempNum = (temp - 0) + (this.product.freight - 0)

        return tempNum.toFixed(2)
//        return (temp - tempStr + this.product.freight).toFixed(2)
      },
      ...mapGetters([
        'address',
        'name',
        'postalCode',
        'phone',
        'storeEggNum'
      ])
    },
    methods: {
      getAddress() {
        if(!this.address || !this.name || !this.postalCode || !this.phone) {
          let tempObj = []
          axiosGet('orderaddress').then(res => {
            tempObj = res.data.data.filter(item => {
              return item.type === 1
            })
            this.addr = tempObj[0].detail
            this.tel = tempObj[0].tel
            this.userName = tempObj[0].user_name
            this.pcode = tempObj[0].postal_code
          })
        } else {
          this.addr = this.address
          this.tel = this.phone
          this.userName = this.name
          this.pcode = this.postalCode
        }

      },
      setData(res) {
        let tempArr = []
        this.$nextTick(() => {
          if(res.status === 200) {
            console.log(res)
            this.product = res.data
            this.allFreight = this.product.freight
            console.log(this.allFreight)
            this.productList = res.data.items
            console.log(this.productList)
            this.productOldList = JSON.stringify(res.data.items)
            this.couponList = this._normalizeData(res.data.coupons)
            this.coupon = this.couponList.length ? '请选择' : '无'
            //初始化订单
            if(this.isReny == 2) {
              console.log(this.productList)
              this.productList.map(item => {
                item.price = item.original_price
              })
            }
            this.productList.map((item, index) => {

              //判断是不是认养,如果是认养初始化展示是认养
              if(this.adopt == 1) {
                this.tempOrder.push({
                  id: item.item_id,
                  count: item.count,
                  type: 1
                })
                this.express.push('认养')
              } else {
                this.tempOrder.push({
                  id: item.item_id,
                  count: item.count,
                  type: 0
                })
                this.express.push('配送')
              }

              this.oldExpress.push('配送')
            })
            console.log(this.tempOrder)
          }
        })
      },
      postOrder(obj) {
        let tempData = {}
        axiosPost('/orders',{
          data: obj
        }).then(res => {
          console.log(res)
          if(res.status === 201) {
            tempData = res.data.data
            if(this.isReny == 2) {
              tempData.original_price = this.productList[0].original_price
              tempData.isReny = 2
            }
            this.setConfirmOrder(tempData)

            if(this.isReny == 1) {
              this.$router.push({
                path: '/app/orderAll?status=ship'
              })
            } else {
              this.$router.push({
                path: '/app/confirmorder'
              })
            }
          }
        })
      },
      storePostOrder(obj) {
        axiosPost(`/storehouse/${this.storeEggId}`,{
          data: obj
        }).then(res => {
          console.log(res)
          this.message = '提交成功'
          this.$refs.confirm.show()
          setTimeout(() => {
            this.$router.push({
              path: '/app/orderAll?status=ship'
            })
          }, 1500)

        })
      },
      goPay() {
        let tempContent = []
        this.tempOrder = JSON.stringify(this.tempOrder)
        let storeObj =  {}
        let obj = {
          user_name: this.userName,
          postal_code: this.pcode,
          tel: this.tel,
          detail: this.addr,
          remark: this.query,
          type: this.typeNum,
          coupon_id: this.couponId,
          content: this.tempOrder
        }
        //吃鸡接口
        if(this.isReny == 1) {
          obj.is_reny = 2
          obj.reny_id = this.renyId
        }
        if(this.isReny == 2) {
          let tempO = JSON.parse(this.tempOrder)
          if(this.countQuery == '') {
            tempO[0].count = 1
          } else {
            tempO[0].count = this.countQuery
          }
          this.tempOrder = JSON.stringify(tempO)
          obj.is_reny = 1
          obj.reny_id = this.renyId
          obj.content = this.tempOrder
        }
        //仓库配送
        if(this.eggType == 1) {
          let st = this.upExpress == -1 ? 0 : this.upExpress
          console.log(this.tempOrder)
          storeObj = {
            user_name: this.userName,
            postal_code: this.pcode,
            tel: this.tel,
            detail: this.addr,
            type: st,
            count: this.productList[0].count
          }
          console.log(storeObj)
          this.storePostOrder(storeObj)
          return
        } else {
          this.postOrder(obj)
        }


      },
      goCoupon() {
        if(!this.couponList.length) {
          return
        }
        this.showFlag = true
        this.$refs.scrollCoupon.refresh()
      },
      goAddress() {
        this.$router.push({
          path: '/app/address?type=1'
        })
      },
      backData(obj, total, idx) {
        let tempPrice = 0
        this.typeNum = 1
        this.tempDispatchIndex = idx
        this.tempOrder[idx] = {
          ...this.tempOrder[idx],
          ...obj
        }
        this.$set(this.productList[idx], 'count', this.tempOrder[idx].count)
        if(this.isReny != 2) {
          this.$set(this.productList[idx], 'price', this.tempOrder[idx].count * this.productList[idx].unit_price)
          tempPrice = this.tempOrder[idx].count * this.productList[idx].original_price
          axiosPost('/me/coupons/money',{
            data: {
              money: tempPrice
            }
          }).then(res => {
            this.couponList = this._normalizeEve(res.data.data)
            this.coupon = this.couponList.length ? '请选择' : '无'
          })
        } else {
          this.$set(this.productList[idx], 'price', this.tempOrder[idx].count * this.productList[idx].original_price)
          console.log(this.productList[idx])
          tempPrice = this.tempOrder[idx].count * this.productList[idx].original_price
          axiosPost('/me/coupons/money',{
            data: {
              money: tempPrice
            }
          }).then(res => {
            console.log(res)
            this.couponList = this._normalizeEve(res.data.data)
            this.coupon = this.couponList.length ? '请选择' : '无'
          })
        }

        console.log(this.productList[idx])
        console.log(this.tempOrder[idx])
      },
      backCancel(idx) {
        this.tempDispatchIndex = idx
        this.upExpress = this.expressKeys[0]
        this.$set(this.express, idx, this.oldExpress[idx])
      },
      _normalizeExpress(obj) {
        return Object.values(obj)
      },
      _normalizeKeys(obj) {
        return Object.keys(obj)
      },
      _normalizeEve(arr) {
        arr.map(item => {
          item.created_at = this.mkdata(item.created_at)
          item.end_time = this.mkdata(item.end_time)
          item.coupon_id = item.coupon_id
          if(item.base_money === 0) {
            item.msg = '无门槛'
          } else {
            item.msg = `满${item.base_money}减`
          }
        })
        return arr
      },
      _normalizeData(arr) {
        arr.map(item => {
          item.created_at = this.mkdata(item.created_at)
          item.end_time = this.mkdata(item.pivot.end_time)
          item.coupon_id = item.pivot.coupon_id
          if(item.base_money === 0) {
            item.msg = '无门槛'
          } else {
            item.msg = `满${item.base_money}减`
          }
        })
        return arr
      },
      mkdata(str) {
        let idx = str.indexOf(' ')
        let length = str.length
        let st = str.slice(0, idx)
        return st
      },
      useCoupon(id, idx) {
        this.couponId = id
        this.coupon = idx
        console.log(this.coupon)
        this.couponNum = true
        this.hide()
      },
      //选择提货方式,存储旧的配送方式，以防止分期配送取消时候的，配送方式的不便
      showTake(idx) {
        this.productIndex = idx
        this.dispatchId = this.productList[idx].item_id
        if(this.isReny == 2) {
          let temp = this.productList[idx].deliver_type
          let tempL = []
          this.expressChoose = []
          this.expressKeys = []
          for(let [key, value] of Object.entries(temp)) {
            if(value == '配送' || value == '自取' || value == '存入仓库') {
              this.expressChoose.push(value)
              this.expressKeys.push(key)
            }
          }
        } else if(this.isReny == 1 || this.eggType == 1) {
          let temp = this.productList[idx].deliver_type
          let tempL = []
          this.expressChoose = []
          this.expressKeys = []
          for(let [key, value] of Object.entries(temp)) {
            if(value == '配送' || value == '自取') {
              this.expressChoose.push(value)
              this.expressKeys.push(key)
            }
          }
          console.log(this.expressChoose, this.expressKeys)
        } else if(this.adopt == 1) {
          let temp = this.productList[idx].deliver_type
          let tempL = {}
          this.expressChoose = []
          this.expressKeys = []
          for(let [key, value] of Object.entries(temp)) {
            if(value == '认养') {
              this.expressChoose.push(value)
              this.expressKeys.push(key)
            }
          }

          console.log(this.productList[idx] )
          console.log(this.expressChoose, this.expressKeys)
        } else {
          this.expressChoose = this._normalizeExpress(this.productList[idx].deliver_type)
          this.expressKeys = this._normalizeKeys(this.productList[idx].deliver_type)
        }

        this.showExpress = true
        this.oldExpress[idx] = this.express[idx]
        console.log(this.oldExpress[idx])
      },
      //如果是分期配送的话跳转路由
      exChoose(idx) {
        this.upExpress = this.expressKeys[idx]
        this.$set(this.express, this.productIndex, this.expressChoose[idx])
        if(this.eggType != 1) {
          this.tempOrder[this.productIndex].type = this.upExpress
          console.log(this.tempOrder)
        }

        if(this.upExpress == 3) {
          this.$refs.dispatching.show()
        }
        if(this.upExpress != 3 && this.oldExpress[this.productIndex] == '分期配送'){
          let temp = JSON.parse(this.productOldList)
          this.$set(this.productList[this.productIndex], 'count', temp[this.productIndex].count)
          this.$set(this.productList[this.productIndex], 'price', temp[this.productIndex].price)
          console.log(this.productOldList[this.productIndex])
        }
      },
      hideExpress() {
        this.showExpress = false
      },
      hide() {
        this.showFlag = false
      },
      ...mapMutations({
        setConfirmOrder: 'SET_CONFIRMORDER',
        setStoreEggNum: 'SET_STOREEGGNUM'
      })
    },
    components: {
      ProductList,
      CouponTempList,
      Scroll,
      Dispatching,
      Confirm
    },
    watch: {
      countQuery(newVal, oldVal) {
        let temp = /^[0-9]*$/
        let tempPrice = 0
        if(!temp.test(newVal)) {
          this.countQuery = oldVal
        }

        if(newVal < 0) {
          this.countQuery = 1
        }
        if(newVal - 0 > this.renyCount) {
          this.countQuery = this.renyCount
        }

        if(this.productList.length == 1) {
          this.$set(this.productList[0], 'count', this.countQuery)
          tempPrice = newVal * this.productList[0].price
          axiosPost('/me/coupons/money',{
            data: {
              money: tempPrice
            }
          }).then(res => {
            console.log(res)
            this.couponList = this._normalizeEve(res.data.data)
            this.coupon = this.couponList.length ? '请选择' : '无'
          })
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .submit-scroll
    left: 0
    right: 0
    bottom: 50px
    top: 0
    z-index: 11
    background: #f2f2f2
    position: fixed
    .address-choose
      min-height: 60px
      width: 100%
      padding: 10px 15px
      box-sizing: border-box
      background: #fff
      font-size: 14px
      .name-phone
        flex: 0 0 100%
        width: 100%
        display: flex
        justify-content: space-between
        aside
          flex: 0 0 50%
        aside:last-child
          text-align: right
      .address
        margin-top: 10px
    .product-list
      margin-top: 12px
      padding-bottom: 0
    .fee
      margin-top: 12px
      display: flex
      height: 60px
      padding: 10px 15px
      align-items: center
      flex-wrap: wrap
      background: #fff
      font-size: 14px
      border-bottom: 1px solid #f2f2f2
      .fee-content
        flex: 0 0 100%
        display: flex
        justify-content: space-between
        span
          flex: 0 0 50%
        span:last-child
          text-align: right
          color: #ff8852
          small
            font-size: 12px
      a
        flex: 0 0 100%
        font-size: 12px
    .coupon
      display: flex
      height: 44px
      border-bottom: 1px solid #f2f2f2
      box-sizing: border-box
      line-height: 44px
      background: #fff
      padding: 0 15px
      span
        flex: 0 0 50%
      span:last-child
        text-align: right
        color: #ff8852
        small
          font-size: 12px
    .message
      display: flex
      height: 44px
      border-bottom: 1px solid #f2f2f2
      box-sizing: border-box
      line-height: 44px
      background: #fff
      padding: 0 15px
      span
        flex: 0 0 70px
      input
        flex: 1 1 auto
    .change-count
      justify-content: space-between
      span
        flex: 0 0 120px
      input
        text-align: right
    .result-info
      padding: 0 15px
      height: 44px
      line-height: 44px
      background: #fff
      h4
        background: url('./images/more@2x.png') no-repeat right center
        background-size: 8px 12px

  .submit-btn
    pos-bottom()
    display: flex
    aside
      flex: 1 1 auto
      padding-left: 15px
      box-sizing: border-box
      span
        color: #ff8852
        font-size: 16px
        small
          font-size: 12px
    a
      display: block
      text-align: center
      color: #fff
      background: #ff8852
      flex: 0 0 110px
      height: 50px
      line-height: 50px
  .coupon-part
    pos-all()
    z-index: 12
    background: rgba(0, 0, 0, .6)
    .coupon-scroll
      pos-all()
      background: transparent
  .express-choose
    pos-all()
    z-index: 12
    background: rgba(0, 0, 0, .8)
    ul
      flex-wrap: wrap
      display: flex
      padding: 2vw
      li
        border-radius: 6px
        background: #fff
        height: 44px
        line-height: 44px
        margin: 1vw 1vw
        text-align: center
        color: #333
        flex: 0 0 46vw
</style>



