<template>
  <div id="app">
    <router-view></router-view>
    <tabbar></tabbar>
  </div>
</template>

<script>
  import Tabbar from 'base/tabbar/tabbar'
  import {axiosGet} from 'api/config'
  import {mapMutations} from 'vuex'

  export default {
    created() {
      axiosGet('/sharerCenter').then(res => {
        this.setPercent(res.data.data.current_level.sales_percent)
      })
    },
    components: {
      Tabbar
    },
    methods: {
      ...mapMutations({
        setPercent: 'SET_PERCENT'
      })
    }
  }
</script>

<style>

</style>



