<template>
  <div class="address">
    <scroll class="address-scroll">
      <div class="address-content">
        <ul>
          <li v-for="(item, index) in addressList" @click="chooseAddress(index)">
            <section class="address-part">
              <div class="name-phone">
                <aside>{{item.user_name}}</aside>
                <aside>{{item.tel}}</aside>
              </div>
              <div class="address-detail">
                {{item.detail}}
              </div>
            </section>
            <section class="address-default">
              <div class="default-checked">
                <span class="mini-checked" @click.stop="checkedInput(index)">
                  <input type="checkbox" class="mini-checked-input" ref="checkedbox">
                  <span class="checked-core"></span>
                </span>
                默认地址
              </div>
              <div class="modify-btn">
                <a @click.stop="editAddress(index)">编辑</a>
                <a @click.stop="deleteAddress(index)">删除</a>
              </div>
            </section>
          </li>
        </ul>
      </div>
    </scroll>
    <div class="add-address" @click="addAddress">
      添加地址
    </div>
    <confirm :message="message" ref="confirm"></confirm>
    <toast-confirm :message="message" ref="toastConfirm" :num="num" :idx="idx" @sure="sure" @cancel="cancel"></toast-confirm>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import ToastConfirm from 'base/toastConfirm/toastConfirm'
  import { axiosGet, axiosPost, axiosDelete, token} from 'api/config'
  import {mapMutations} from 'vuex'
  import MockData from 'common/mock/data'

  export default {
    data() {
      return {
        addressList: [],
        message: '',
        num: -1,
        idx: -1,
        routeId: '',
        routeNum: -1
      }
    },
    created() {
      this.getData()
//      this.addressList = MockData.address.data
      this.routeNum = this.$route.query.type
    },
    mounted() {

    },
    methods: {
      getData() {
        let _this = this
        axiosGet('/orderaddress').then(res => {
          if(res.status === 200) {
            this.addressList = res.data.data
            this.$nextTick(() => {
              this.addressList.map((item, index) => {
                if(item.type == 1) {
                  _this.$refs.checkedbox[index].checked = true
                }
              })
            })
          }
        })
      },
      checkedInput(idx) {
        let checkedSigle = this.$refs.checkedbox[idx].checked = !this.$refs.checkedbox[idx].checked
        let id = this.addressList[idx].id
        console.log(id)
        axiosGet(`/defaultAdd/${id}`).then(res => {
          if(res.status === 200) {
            if(res.data.state === 1) {
              this.message = res.data.msg
              this.$refs.confirm.show()
            }
          }
        })
        this.addressList.map((item, index) => {
          if(idx != index) {
            this.$refs.checkedbox[index].checked = false
          }
        })
      },
      chooseAddress(idx) {
        if(this.routeNum != 1) {
          return
        }
        let {detail, id, postal_code, tel, type, user_name} = this.addressList[idx]
        console.log(detail)
        this.setAddress(detail)
        this.setName(user_name)
        this.setPhone(tel)
        this.setPostalCode(postal_code)
        this.$router.back()
      },
      addAddress() {
        this.$router.push({
          path: '/app/addaddress'
        })
      },
      editAddress(idx) {
        let {id} = this.addressList[idx]
        this.$router.push({
          path: `/app/addaddress?idx=${id}`
        })
      },
      deleteAddress(idx) {
        let {id} = this.addressList[idx]
        this.num = id
        this.idx = idx
        this.message = '确定删除该地址'
        this.$refs.toastConfirm.show()
      },
      sure(id, idx) {
        axiosDelete(`/addresss/${id}`).then(res => {
          if(res.status === 200) {
            if(res.data.state === 0) {
              this.message = res.data.msg
              this.$refs.confirm.show()
              this.addressList.splice(idx, 1)
            }
          }
        })
      },
      cancel() {},
      ...mapMutations({
        setName: 'SET_NAME',
        setPhone: 'SET_PHONE',
        setAddress: 'SET_ADDRESS',
        setPostalCode: 'SET_POSTAL_CODE'
      })
    },
    components: {
      Scroll,
      Confirm,
      ToastConfirm
    },
    watch: {
      addressList(newVal) {

      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .address
    pos-all()
    z-index: 11
    background: #f2f2f2
    .mini-checked
      flex: 0 0 20px
      width: 20px
      height: 20px
      box-sizing: border-box
      margin-right: 10px
      .mini-checked-input
        display: none
      .checked-core
        border-radius: 100%
        border: 1px solid #ccc
        position: relative
        display: block
        width: 20px
        height: 20px
        box-sizing: border-box
      .checked-core:after
        border: 2px solid transparent
        border-left: 0
        border-top: 0
        content: " "
        top: 3px
        left: 6px
        position: absolute
        width: 4px
        height: 8px
        transform: rotate(45deg) scale(0)
        transition: transform .2s
      .mini-checked-input:checked+.checked-core
        background-color:#ff8852
        border-color: #ff8852
      .mini-checked-input:checked+.checked-core:after
        border-color: #fff
        transform: rotate(45deg) scale(1)
    .address-scroll
      pos-all()
      bottom: 50px
    .add-address
      color: #fff
      display: flex
      pos-bottom()
      background: #ff8852
      justify-content: center
    .address-content
      li
        margin-top: 12px
        min-height: 60px
        width: 100%
        background: #fff
        font-size: 14px
        .address-part
          border-bottom: 1px solid #f2f2f2
          padding: 10px 15px
          box-sizing: border-box
          .name-phone
            flex: 0 0 100%
            width: 100%
            display: flex
            justify-content: space-between
            aside
              flex: 0 0 50%
            aside:last-child
              text-align: right
          .address-detail
            margin-top: 10px
        .address-default
          display: flex
          align-items: center
          justify-content: space-between
          padding: 0 15px
          box-sizing: border-box
          height: 36px
          .default-checked
            display: flex
            align-items: center
          .modify-btn
            a
              display: inline-block
              width: 50px
              line-height: 24px
              text-align: center
              height: 24px
              color: #fff
              background: #bebebe
            a:first-child
              background: #ff8852
      li:first-child
        margin-top: 0
</style>



