<template>
  <div class="confirm-order">
    <scroll class="confirm-scroll">
      <div class="scroll-part">
        <div class="confirm-address">
          <div class="name-phone">
            <aside>收件人：{{addressList.user_name}}</aside>
            <aside>{{addressList.tel}}</aside>
          </div>
          <div class="address">
            收货地址：{{addressList.detail}}
          </div>
        </div>
        <product-list class="product-list" :takeOff="takeOff" :showOrder="showOrder"
                      :productList="productList" :mtd="mtd" :express="express"></product-list>
        <div class="order-info">
          <h3>订单信息</h3>
          <div class="info-box">
            <ul>
              <li>
                <span>订单编号</span>
                <span>{{confirmOrder.sn}}</span>
              </li>
              <li>
                <span>商品总价</span>
                <span>￥{{confirmOrder.price}}</span>
              </li>
              <li>
                <span>运费</span>
                <span>￥{{confirmOrder.freight}}</span>
              </li>
              <li>
                <span>优惠价格</span>
                <span>￥{{confirmOrder.coupon}}</span>
              </li>
            </ul>
          </div>
          <div class="pay-count">
            <span>需付款</span>
            <span>￥{{totalPrice}}</span>
          </div>
        </div>

      </div>
    </scroll>
    <div class="confirm-btn">
      <a class="cancel-btn" @click="cancel">取消订单</a>
      <a class="sure-btn" @click="sure">确认订单</a>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import ProductList from 'base/product-list/product-list'
  import Confirm from 'base/confirm/confirm'
  import {mapGetters, mapMutations} from 'vuex'
  import {axiosDelete} from 'api/config'
  import Scroll from 'base/scroll/scroll'

  export default {
    data() {
      return {
        showOrder: false,
        mtd:false,
        takeOff: true,
        productList: [],
        addressList: {},
        express: [],
        message: '',
        id: -1
      }
    },
    mounted() {
      console.log(this.confirmId)
      console.log(this.confirmOrder)
      if(this.confirmId == this.confirmOrder.id) {
        this.message = '此商品已经提交过订单'
        this.$refs.confirm.show()
        setTimeout(() => {
          this.$router.push({
            path: '/app/me'
          })
        }, 1000)
      }

      let temp = JSON.stringify(this.confirmOrder)
      let tempArr = this._normalizeData(JSON.parse(temp).stageItems)
      this.productList = [...this.confirmOrder.items, ...tempArr]
      console.log(this.productList)
      if(this.confirmOrder.isReny == 2) {
        console.log(this.productList[0])
        this.productList[0].price = this.confirmOrder.original_price
      }
      this.addressList = this.confirmOrder.address
      this.id = this.confirmOrder.id
      this.productList.map((item) => {
        this.express.push(item.deliver_type)
      })


    },

    computed: {
      totalPrice() {
        return this.confirmOrder.price + this.confirmOrder.freight
//        return this.confirmOrder.price - this.confirmOrder.coupon + this.confirmOrder.freight
      },
      ...mapGetters([
        'confirmOrder',
        'confirmId'
      ])
    },
    methods: {
      sure() {
        console.log(location.hostname)
        this.setConfirmId(this.id)
        window.location.href = `/app/pay/?id=${this.id}`
//        this.$router.push({
//          path: `/app/pay/?id=${this.id}`
//        })
//        window.location.href = `${location.hostname}/app/pay?id=${this.id}`
      },
      cancel() {
        axiosDelete(`/orders/${this.id}`).then(res => {
          console.log(res)
          this.message = '取消成功'
          this.$refs.confirm.show()
          setTimeout(() => {
            this.$router.push({
              path: '/app/home'
            })
          }, 1000)
        })
      },
      _normalizeData(arr) {
        console.log(arr)
        if(!arr) {
          this.message = '无效订单'
          this.$refs.confirm.show()
          setTimeout(() => {
            this.$router.push({
              path: '/app/me'
            })
          }, 1000)
          return
        }
        arr.map(item => {
          item.price = item.price * item.count
        })
        return arr
      },
      ...mapMutations({
        setConfirmId: 'SET_CONFIRMID'
      })
    },
    components: {
      ProductList,
      Confirm,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'
  .confirm-order
    background: #f2f2f2
    z-index: 11
    pos-all()
    .confirm-scroll
      pos-all()
      bottom: 50px
      .confirm-address
        min-height: 60px
        width: 100%
        padding: 10px 15px
        box-sizing: border-box
        background: #fff
        font-size: 14px
        .name-phone
          flex: 0 0 100%
          width: 100%
          display: flex
          justify-content: space-between
          aside
            flex: 1 1 auto
            margin-right: 10px
        .address
          margin-top: 10px
      .product-list
        padding-bottom: 0
      .order-info
        background: #ffffff
        margin-top: 12px
        span
          flex: 0 0 50%
        span:last-child
          text-align: right
        h3
          color: #ff8852
          padding: 0 15px
          box-sizing: border-box
          border-bottom: 1px solid #f2f2f2
          height: 32px
          line-height: 32px
        .info-box
          padding: 10px 15px
          box-sizing: border-box
          border-bottom: 1px solid #f2f2f2
          li
            display: flex
            height: 18px
            line-height: 18px
            font-size: 12px
            color: #999
      .pay-count
        display: flex
        padding: 0 15px
        height: 32px
        line-height: 32px
        border-bottom: 1px solid #f2f2f2
        box-sizing: border-box
        span:last-child
          color: #ff8852
    .confirm-btn
      pos-bottom()
      display: flex
      a
        text-align: center
        flex: 0 0 50%
      a:last-child
        color: #fff
        background: #ff8852
</style>



