<template>
  <div class="order-all-list">
    <div class="order-tab">
      <ul>
        <li v-for="(item, index) in orderNav" @click="tabOrderList(index)" :class="currentIndex == index ? 'active' : ''">
          {{item}}
        </li>
      </ul>
    </div>
    <scroll class="order-all" :data="orderList" ref="scroll" :pullUpLoad="pullUpLoad" @pullingUp="pullingUp">
      <div class="order-scroll">
        <order-list :orderList="orderList" @cancel="cancel" @pay="pay" @getGoods="getGoods" @goDetail="goDetail"></order-list>
      </div>
    </scroll>
    <toast-confirm :message="message" ref="toastConfirm" :num="sliceIndex" @cancel="cancelConfirm" @sure="sure"></toast-confirm>
    <div class="back-me" @click="goBack">返回个人中心</div>
  </div>

</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import ToastConfirm from 'base/toastConfirm/toastConfirm'
  import OrderList from 'base/orderList/orderList'
  import {axiosGet, axiosDelete, axiosPut} from 'api/config'


  export default {
    data() {
      return {
        orderList: [],
        page: 0,
        lastPage: 0,
        pullUpLoad: true,
        orderNav: ['全部订单', '待付款', '待发货', '待收货', '已完成', '分期配送'],
        orderType: ['all', 'wait', 'ship', 'receipt', 'finish', 'stage'],
        id: -1,
        message: '',
        sliceIndex: -1,
        routeStatus: '',
        currentIndex: 0,
        routePath: ''
      }
    },
    created() {
      console.log(this.$route.query.status)
      switch (this.$route.query.status) {
        case 'wait':
          this.routeStatus = '0'
          this.currentIndex = 1
          break
        case 'ship':
          this.routeStatus = '1'
          this.currentIndex = 2
          break
        case 'receipt':
          this.routeStatus = '2'
          this.currentIndex = 3
          break
        case 'finish':
          this.routeStatus = '4'
          this.currentIndex = 4
          break
        case 'all':
          this.routeStatus = ''
          this.currentIndex = 0
          break
      }
      if(this.$route.query.status == 'stage') {
        this.currentIndex = 5
        this.routePath = '/orders/stage/index'
        this.getData(this.routePath)
      } else {
        this.routePath = '/orders'
        this.getData(this.routePath)
      }
    },
    mounted() {
//      if (window.history && window.history.pushState) {
//        window.addEventListener('popstate', this.goBack, false)
//      }
    },
    methods: {
      goBack() {
        this.$router.replace({
          path: '/app/me'
        })
      },
      getData(routePath) {
        this.orderList = []
        this.moreData(this.page, routePath)
      },
      moreData(page, routePath) {
        if (this.lastPage <= page && this.lastPage != 0) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet(routePath,{
          params:{
            page: this.page,
            status: this.routeStatus
          }
        }).then(res => {
          console.log(res)
          if (res.status === 200) {
            if (this.lastPage != res.data.meta.last_page) {
              this.lastPage = res.data.meta.last_page
            }
            this.orderList = [...this.orderList, ...res.data.data]
          }
        })

      },
      pullingUp() {
        this.moreData(this.page, this.routePath)
      },
      tabOrderList(idx) {
        this.page = 0
        this.lastPage = 0
        this.currentIndex = idx
        switch (idx) {
          case 0:
            this.routeStatus = ''
            break
          case 1:
            this.routeStatus = '0'
            break
          case 2:
            this.routeStatus = '1'
            break
          case 3:
            this.routeStatus = '2'
            break
          case 4:
            this.routeStatus = '4'
            break
        }
        console.log(this.routeStatus)
        if(idx == this.orderNav.length -1) {
          this.routePath = '/orders/stage/index'
          this.getData(this.routePath)
        } else {
          this.routePath = '/orders'
          this.getData(this.routePath)
        }

        this.$router.push({
          path: `/app/orderall?status=${this.orderType[idx]}`
        })
      },
      cancel(idx, status) {
        this.id = this.orderList[idx].id
        this.sliceIndex = idx
        if(status === 'finish' || status === 'confirm' || status === 'recv') {
          this.message = '是否删除订单'
        } else {
          this.message = '是否取消订单'
        }

        this.$refs.toastConfirm.show()
      },
      cancelConfirm() {},
      sure(idx) {
        console.log(idx)
        axiosDelete(`/orders/${this.orderList[idx].id}`).then(res => {
          console.log(res)
        })
        this.orderList.map((item, index) => {
          if(idx == index) {
            this.$delete(this.orderList, idx)
          }
        })
      },
      pay(idx) {
        window.location.href = `/app/pay?id=${this.orderList[idx].id}`
//        this.$router.push({
//          path: `/app/pay?id=${this.orderList[idx].id}`
//        })
      },
      getGoods(idx) {
        axiosPut(`/orders/${this.orderList[idx].id}/confirm`).then(res => {
          console.log(res)
        })
        this.orderList.map((item, index) => {
          if(idx == index) {
            this.$delete(this.orderList, idx)
          }
        })
      },
      goDetail(idx, id) {
        console.log(idx, id)
        this.$router.push({
          path: `/app/orderdetail?id=${id}`
        })
      }
    },
    components: {
      Scroll,
      OrderList,
      ToastConfirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .order-all-list
    background: #f2f2f2
    z-index: 11
    pos-all()
    .back-me
      position: absolute
      border-top-right-radius: 6px
      border-bottom-right-radius: 6px
      width: 36px
      height: 44px
      padding: 0 10px
      line-height: 22px
      background: rgba(255,136,82,.8)
      color: #fff
      bottom: 40px
      left: 0
      font-size: 12px
      z-index: 13
    .order-all
      pos-all()
      top: 50px
    .order-tab
      padding: 0 10px
      box-sizing: border-box
      position: fixed
      top: 0
      width: 100%
      z-index: 12
      height: 44px
      line-height: 44px
      background: #fff
      ul
        display: flex
        li
          text-align: center
          flex: 0 0 16.6667%
        li.active
          color: #ff8852
</style>



