<template>
  <div class="spread">
    <scroll :data="spreadList":pullUpLoad="pullUpLoad" @pullingUp="pullingUp" ref="scroll">
      <div class="spread-scroll">
        <ul>
          <li v-for="(item, index) in spreadList" @click="productDetail(index)">
            <div class="checked-pic" v-if="item.cover.url">
              <img :src="item.cover.url" alt="">
            </div>
            <div class="checked-info">
              <div class="checked-show">
                <h4>
                  <span>{{item.title}}</span>
                </h4>
                <i>佣金{{percent}}%</i>
                <section class="checked-price">
                  <a class="price">预计收益 {{item.profit.toFixed(2)}}</a>
                </section>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </scroll>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import {axiosGet} from 'api/config'
  import {mapGetters} from 'vuex'

  export default {
    created() {
      this.getData()
    },
    data() {
      return {
        spreadList: [],
        page: 1,
        pullUpLoad: true,
        lastPage: 1
      }
    },
    computed: {
      ...mapGetters([
        'percent'
      ])
    },
    methods: {
      pullingUp() {
        this.moreData(this.page)
      },
      getData() {
        this.moreData(this.page)
      },
      moreData(page) {
        if(this.lastPage < page) {
          this.$refs.scroll.dirtyRefresh(true)
          return
        }
        this.page++
        axiosGet('/extension/items',{
          params: {
            page
          }
        }).then(res => {
          console.log(res)
          if(this.lastPage != res.data.meta.last_page) {
            this.lastPage = res.data.meta.last_page
          }
          this.spreadList = [...this.spreadList, ...res.data.data]
          this.spreadList.map(item => {
            item.profit = item.price * this.percent / 100
            console.log(item)
          })
        })

      },
      productDetail(idx) {
        let {id} = this.spreadList[idx]
        this.$router.push({
          path: `/app/product/${id}`
        })
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .spread
    background: #f2f2f2
    z-index: 11
    pos-all()
    .spread-scroll
      background: #ffffff
      li
        padding: 0 15px
        border-bottom: 1px solid #f2f2f2
        display: flex
        align-items: center
        height: 125px
        .checked-pic
          flex: 0 0 80px
          width: 80px
          height: 80px
          margin: 0 15px 0 8px
          img
            height: 100%
            width: 100%
        .checked-info
          flex: 1 1 auto
          height: 80px
          .checked-show
            display: flex
            flex-direction: column
            justify-content space-between
            height: 80px
            h4
              display: flex
              span
                flex: 1 1 auto
                margin-right: 20px
                font-size: 14px
              small
                width: 16px
                flex: 0 0 16px
                height: 16px
                img
                  width: 100%
                  height: 100%
              a
                flex: 0 0 60px
                height: 16px
                line-height: 16px
                border-radius: 8px
                text-align: center
                width: 60px
                background: #ff8852
                color: #fff
                font-size: 12px
            i
              width: 60px
              margin-top: 15px
              height: 16px
              line-height: 16px
              text-align: center
              border-radius: 8px
              color: #fff
              font-size: 12px
              font-style: normal
              background: #ff8852
            .checked-price
              a
                color: #ff8852
</style>



