<template>
  <div class="company-profile">
    <profile :content="content" class="profile"></profile>
  </div>
</template>

<script>
  import Profile from 'base/profile/profile'
  import {axiosGet, token} from "api/config"

  export default {
    created() {
      console.log(this.$route)
      this.getData()
    },
    data() {
      return {
        content: {}
      }
    },
    methods: {
      getData() {
        axiosGet('/introduction').then(res => {
          this.content = res.data.data
        })
      }
    },
    components: {
      Profile
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .company-profile
    background: #f2f2f2
    z-index: 11
    pos-all()
    img
      width: 100%
      height: 100%
</style>



