<template>
  <div class="store-list">
    <div class="store-content" v-if="categoryList.length > 0">
      <ul>
        <li v-for="(item, index) in categoryList">
          <i><img :src="item.item.cover.url" alt=""></i>
          <section class="product-info">
            <h4>
              <span>种类：{{item.item.title}}</span>
              <small>×{{item.num}}</small>
            </h4>
            <div class="product-btn">
              <a @click="giveOne(index)" v-show="categoryId === 1">赠送</a>
              <a @click="dispatchOne(index)">配送</a>
            </div>
          </section>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  export default {
    props: {
      category: {
        type: Array,
        default: []
      },
      categoryId: {
        type: Number,
        default: 1
      }
    },
    data() {
      return{
        categoryList: []
      }
    },
    created() {
      console.log(this.category)

    },
    methods: {
      giveOne() {

      },
      dispatchOne(idx) {
        this.$emit('dispatchEgg', idx)
      },
      giveOne(idx) {
        this.$emit('dispatchChicken', idx)
      }
    },
    components: {
      Scroll
    },
    watch: {
      category(newVal, oldVal) {
        console.log(newVal)
        this.categoryList = newVal
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .store-list
    .store-content
      ul
        padding: 0 15px
        li
          padding: 10px
          border-radius: 8px
          background: #ffffff
          margin-top: 12px
          display: flex
          i
            flex: 0 0 80px
            height: 80px
            width: 80px
            margin-right: 15px
            img
              height: 100%
              width: 100%
          .product-info
            display: flex
            flex-direction: column
            flex: 1 1 auto
            justify-content: center
            h4
              display: flex
              padding-right: 15px
              span
                flex: 1 1 auto
              small
                flex: 0 0 20px
                color: #999
            .product-btn
              margin-top: 20px
              padding-right: 15px
              a
                background: #ff8852
                color: #fff
                border-radius: 6px
                width: 60px
                height: 30px
                line-height: 30px
                text-align: center
                display: inline-block


</style>



