<template>
  <div class="confirm" v-show="showFlag">
    {{message}}
  </div>
</template>

<script>
  export default {
    props: {
      message: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        showFlag: false
      }
    },
    destroyed() {
      this.timer = null
    },
    methods: {
      show() {
        this.timer = null
        this.showFlag = true
        this.timer = setTimeout(() => {
          this.showFlag = false
        }, 1000)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .confirm
    position: absolute
    left: 50%
    top: 50%
    color: #fff
    border-radius: 8px
    margin-left: -20vw
    margin-top: -18px
    background: rgba(0,0,0,.6)
    width: 40vw
    text-align: center
    height: 36px
    z-index: 15
    line-height: 36px
</style>



