import axios from 'axios'
import {getCookie} from './util'

// const token = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwOlwvXC93d3cuZnVqaWFucWl1Z2UuY29tXC9hdXRoXC9vYXV0aF9jYWxsYmFjayIsImlhdCI6MTU0MDUyNTU5OCwiZXhwIjoxNTQzMTE3NTk4LCJuYmYiOjE1NDA1MjU1OTgsImp0aSI6IlhYZnBUUmVNVUs1RWk1ekwiLCJzdWIiOjMsInBydiI6IjIzYmQ1Yzg5NDlmNjAwYWRiMzllNzAxYzQwMDg3MmRiN2E1OTc2ZjcifQ.XTCHm6LBguxYwI1Kz6v0mpASXYqSpu-yvuYzc-2ZFw0'
axios.defaults.baseURL = 'http://www.fujianqiuge.com'
var timer = setInterval(() => {
  var token = getCookie('access_token')
  console.log(token)
  if(token) {
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    console.log(token)
    clearInterval(timer)
  }
}, 500)
// axios.defaults.headers.common['Authorization'] = `Bearer ${token}`

let config = {
  method: 'get',
  // baseURL,
  timeout: 10000,
  params: {},
  headers: {}
}
let postconfig = {
  method: 'post',
  // baseURL,
  timeout: 10000,
  params: {},
  data: {},
  headers: {}
}
let deleteconfig = {
  method: 'delete',
  // baseURL,
  timeout: 10000,
  data: {}
}

let putconfig = {
  method: 'put',
  // baseURL,
  timeout: 10000,
  headers: {},
  data: {}
}
export const axiosGet = (url, params, headers) => {
  return axios({ ...config, url, ...params, ...headers})
}
export const axiosPost = (url, params, headers, data) => {
  return axios( { ...postconfig, url, ...params, ...headers, ...data})
}
export const axiosDelete = (url, headers, params, data) => {
  return axios( { ...deleteconfig, ...headers, url, ...data})
}
export const axiosPut = (url, params, headers, data) => {
  return axios( { ...putconfig, url, ...headers, ...data})
}
export const axiosAll = func => {
  return axios.all([...func])
}



