<template>
  <div class="pay">
    <div class="pay-content">
      <small>账户余额（元）</small>
      <span>{{wallet.money}}</span>
    </div>
    <div class="pay-methods">
      <ul>
        <li @click="wePay">
          <i><img src="./images/wxpay@2x.png" alt=""></i>
          <span>微信支付</span>
        </li>
        <li @click="accoutPay">
          <i><img src="./images/account@2x.png" alt=""></i>
          <span>余额支付</span>
        </li>
      </ul>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Confirm from 'base/confirm/confirm'
  import {axiosGet, axiosPost, axiosPut} from 'api/config'
  import {getCookie} from 'api/util'
  import weChat from 'api/mixins'

  export default {
    data() {
      return {
        wallet: [],
        id: -1,
        message: '',
        url: '',
        token: ''
      }
    },
    created() {
      this.token = getCookie('access_token')
      this.url = window.location.href
      this.weChatConfig(this.url)
      this.getData()
      this.id = this.$route.query.id
    },
//    beforeDestroyed() {
//      this.id = -1
//    },
    mixins: [weChat],
    methods: {
      getData() {
        axiosGet('/wallet',{
          headers: {
            Authorization: `Bearer ${this.token}`
          }
        }).then(res => {
          if(res.status === 200) {
            this.wallet = res.data[0]
          }
        })
      },
      wePay() {
        axiosPut(`/orders/${this.id}/pay`).then(res => {
          console.log(res)
          if(res.status === 200) {
            this.goPay(res.data.data)
          }
        })
      },
      accoutPay() {
        axiosPost(`/orders/${this.id}/walletpay`).then(res => {
          console.log(res)
          if(res.data.status == 1) {
            this.message = res.data.msg
            this.$refs.confirm.show()
            setTimeout(() => {
              this.$router.replace({
                path: '/app/orderall?status=ship'
              })
            })
          }
        })
      }
    },
    components: {
      Confirm
    }
  }
</script>i

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'
  .pay
    pos-all()
    z-index: 11
    background: #f2f2f2
    .pay-content
      padding: 0 15px
      display: flex
      flex-direction: column
      background: #ff8852
      height: 80px
      justify-content: center
      color: #fff
      small
        font-size: 14px
      span
        margin-top: 10px
        font-size: 28px
    .pay-methods
      ul
        padding-left: 15px
        background: #fff
        li
          display: flex
          align-items: center
          height: 44px
          border-bottom: 1px solid #f2f2f2
          i
            flex: 0 0 24px
            width: 24px
            height: auto
            margin-right: 10px
            img
              width: 24px
              height: auto
          span
            flex: 1 1 auto
</style>



