<template>
  <div class="store">
    <div class="store-list">
      <router-link to="/app/companyprofile" class="store-item">
        <span><img src="./images/profile@2x.png" alt=""></span>
        <h5>公司简介</h5>
      </router-link>
      <router-link to="/app/storeprofile" class="store-item">
        <span><img src="./images/store@2x.png" alt=""></span>
        <h5>所有店铺</h5>
      </router-link>
      <router-link to="/app/model" class="store-item">
        <span><img src="./images/model@2x.png" alt=""></span>
        <h5>运营模式</h5>
      </router-link>
      <router-link to="/app/takeprofile" class="store-item">
        <span><img src="./images/take@2x.png" alt=""></span>
        <h5>配送说明</h5>
      </router-link>
      <router-link to="/app/contactprofile" class="store-item">
        <span><img src="./images/contact@2x.png" alt=""></span>
        <h5>联系方式</h5>
      </router-link>
      <router-link to="/app/serviceprofile" class="store-item">
        <span><img src="./images/service@2x.png" alt=""></span>
        <h5>售后说明</h5>
      </router-link>
      <router-link to="/app/userprofile" class="store-item">
        <span><img src="./images/user@2x.png" alt=""></span>
        <h5>大客户</h5>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        routeName: []
      }
    },
    methods: {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .store
    pos-all()
    background: #f2f2f2
    z-index: 11
    .store-list
      padding: 2vw
      display: flex
      flex-wrap: wrap
      box-sizing: border-box
      .store-item
        margin-top: 16px
        background: #fff
        display: flex
        flex-direction: column
        justify-content center
        align-items: center
        height: 100px
        flex: 0 0 46vw
        margin: 1vw
        border-radius: 8px
        h5
          margin-top: 5px
        span
          width: 50px
          height: 50px
          display: block
          img
            height: 100%
            width: 100%
</style>



