<template>
  <div class="allList">
    <ul>
      <li v-for="(item, index) in allList" @click="productDetail(index)">
        <div class="product-pic">
          <img :src="item.cover.url" alt="">
        </div>
        <h5>{{item.title}}</h5>
        <section class="price">
          <span><small>￥</small>{{item.price}}</span>元
        </section>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      allList: {
        type: Array,
        default: []
      }
    },
    methods: {
      productDetail(idx) {
        this.$emit('productDetail', idx)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .allList
    ul
      display: flex
      box-sizing: border-box
      padding: 2vw
      flex-wrap: wrap
      li
        flex: 0 0 46vw
        border: 1px solid #eee
        box-sizing: border-box
        margin: 1vw
        .product-pic
          width: 36vw
          margin: 0 auto
          height: auto
          img
            width: 36vw
            height: auto
        h5
          padding: 0 10px
          color: #999
          font-size: 14px
        .price
          padding: 30px 10px 10px
          color: #999
          span
            font-size: 16px
            color: #cd1d1d
            small
              font-size: 12px
</style>



