<template>
  <div class="car">
    <scroll :data="productList" class="car-list">
      <div class="car-scroll">
        <product-list :productList="productList" :express="express" @del="del" @refreshScroll="refreshScroll" @total="total" @complete="complete" @tempOrder="tempOrder"></product-list>
      </div>
    </scroll>
    <div class="total-count">
      <aside>
        合计：<span><small>￥</small>{{price}}</span>
      </aside>
      <a class="btn" @click="submitOrder">结算</a>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
    <div class="empty-car" v-show="!productList.length">
      <div class="empty-content">
        <div class="empty-pic">
          <img src="./images/car.png" alt="">
        </div>
        <div class="empty-path">
          <h4>购物车空空如也，快来填满~~</h4>
          <a @click="goShopping">前往热销商品</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import ProductList from 'base/product-list/product-list'
  import {axiosGet, axiosDelete, axiosPut, token} from 'api/config'
  import MockData from 'common/mock/data'

  export default {
    props: {

    },
    data() {
      return {
        productList: [],
        price: 0,
        message: '',
        submitOrders: null,
        express: []
      }
    },
    created() {
      this.getData()
      // this.productList = MockData.product
    },
    methods: {
      getData() {
        axiosGet('/cats').then(res => {
          if(res.status === 200) {
            console.log(res)
            this.productList = this._normalizeData(res.data.data)
            this.productList.map(item => {
              this.express.push('配送')
            })
          }
        })
      },
      refreshScroll(data) {
        this.productList = data
      },
      total(price, type, id) {
        price = Math.abs(price.toFixed(2))
        this.price = price
      },
      del(type, id) {
        this.delOne(type, id)
      },
      delOne(type, id) {
        axiosDelete(`/cats/${id}`).then(res => {
          console.log(res)
        })
      },
      complete(idx, id) {
        axiosPut(`/cats/edit/${id}`,{
          data: {
            count: idx
          }
        }).then(res => {
          console.log(res)
        })
      },
      tempOrder(obj) {
        this.submitOrders = JSON.stringify(obj)
      },
      submitOrder() {
        if(!this.price) {
          this.message = '至少选择一件商品'
          this.$refs.confirm.show()
          return
        }
        this.$router.push({
          path: `/app/submitorder?type=${this.submitOrders}`
        })
      },
      goShopping() {
        this.$router.push({
          path: '/app/home'
        })
      },
      _normalizeData(arr) {
        let arrTemp = []
        arr.map(item => {
          item.showEdit = false
          arrTemp.push(item)
        })
        return arrTemp
      }
    },
    components: {
      Scroll,
      ProductList,
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .car
    .car-list
      background: #f2f2f2
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
    .total-count
      position: fixed
      width: 100%
      bottom: 0
      background: #fff
      z-index: 11
      display: flex
      height: 50px
      line-height: 50px
      aside
        flex: 1 1 auto
        padding-left: 15px
        box-sizing: border-box
        span
          color: #ff8852
          font-size: 16px
          small
            font-size: 12px
      a
        display: block
        text-align: center
        color: #fff
        background: #ff8852
        flex: 0 0 110px
        height: 50px
        line-height: 50px
    .empty-car
      z-index: 15
      background: #ffffff
      pos-all()
      .empty-content
        width: 300px
        height: 228px
        position: absolute
        top: 50%
        left: 50%
        margin-top: -150px
        margin-left: -150px
        .empty-pic
          margin: 0 auto
          width: 150px
          height: 114px
          img
            width: 150px
            height: 114px
        .empty-path
          margin-top: 10px
          text-align: center
          h4
            height: 40px
          a
            color: #fff
            border-radius: 6px
            background: #ff8852
            padding: 5px 10px
</style>



