<template>
  <div class="coupon-list">
    <ul>
      <li v-for="(item, index) in couponList" @click.stop>
        <section class="condition">
          <span class="item-count"><small>￥</small>{{item.money}}</span>
          <span class="item">{{item.msg}}</span>
        </section>
        <section class="coupon-time" v-if="!showFlag">
          <h5>有效期</h5>
          <span>{{item.created_at}} ~ {{item.end_time}}</span>
        </section>
        <section class="coupon-time" v-else>
          <h5>有效期</h5>
          <span>{{item.time}}天</span>
        </section>
        <section class="coupon-btn" @click="receive(index)" v-if="!useIt">
          立即<br/>领取
        </section>
        <section class="coupon-btn" @click="useCoupon(index)" v-else>
          立即<br/>使用
        </section>
      </li>
    </ul>
    <confirm ref="confirm" :message="message"></confirm>
  </div>
</template>

<script>
  import Confirm from 'base/confirm/confirm'

  export default {
    data() {
      return {
        message: '领取成功'
      }
    },
    props: {
      couponList: {
        type: Array,
        default: []
      },
      useIt: {
        type: Boolean,
        default: false
      },
      showFlag: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      receive(idx) {
        this.$refs.confirm.show()
        let {id} = this.couponList[idx]
        this.$emit('receive', id)
      },
      useCoupon(idx) {
        let {coupon_id} = this.couponList[idx]
        this.$emit('useCoupon', coupon_id, this.couponList[idx].money)
      }
    },
    components: {
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .coupon-list
    padding: 0 15px
    li
      display: flex
      height: 100px
      align-items: center
      justify-content: space-between
      background: #fff
      margin-top: 12px
      border-radius: 8px
      .condition
        display: flex
        padding: 18px 0
        border-right: 1px dashed #eee
        flex-direction: column
        text-align: center
        max-width: 100px
        flex: 1 1 auto
        .item-count
          color: #ff8852
          font-size: 24px
          small
            font-size: 12px
            color: #999
        .item
          color: #ff8852
          margin-top: 12px
    .coupon-time
      flex: 1 1 auto
      padding: 0 15px
      box-sizing: border-box
      h5
        margin-bottom: 10px
      span
        font-size: 12px
        color: #999
    .coupon-btn
      display: flex
      align-items: center
      font-size: 14px
      background: #ff8852
      justify-content: center
      color: #fff
      border-top-right-radius: 8px
      border-bottom-right-radius: 8px
      height: 100px
      line-height: 20px
      width: 50px
      flex: 0 0 50px
</style>



