<template>
  <div class="record-list">
    <ul>
      <li v-for="(item, index) in recordList">
        <section class="record-info">
          <span>{{item.title}}</span>
          <span v-if="markTip">{{item.money}}</span>
          <span v-else>{{item.title}}{{item.count}}只</span>
        </section>
        <section class="record-ass">
          <span>{{item.createdTime}}</span>
          <span v-if="markTip">{{item.tempStatus}}</span>
          <span v-else>{{item.remain}}</span>
        </section>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      recordList: {
        type: Array,
        default: []
      },
      markTip: {
        type: Boolean,
        default: true
      }
    },
    created() {
      console.log(this.recordList)
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .record-list
    background: #fff
    ul
      li
        display: flex
        flex-direction: column
        justify-content: center
        box-sizing: border-box
        padding: 5px 15px
        height: 80px
        border-bottom: 1px solid #f2f2f2
        .record-info, .record-ass
          display: flex
          justify-content: space-between
          span
            flex: 0 0 50%
          span:last-child
            text-align: right
        .record-info
          span
            font-size: 16px
        .record-ass
          margin-top: 15px
          span
            color: #999
            font-size: 12px
</style>



