<template>
  <scroll class="out-store" :data="recordList">
    <div class="out-scroll">
      <record-list :recordList="recordList" :markTip="markTip"></record-list>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import RecordList from 'base/recordList/recordList'
  import {axiosGet} from 'api/config'

  export default {
    data() {
      return {
        recordList: [],
        markTip: false
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/storehouse/record').then(res => {
          this.recordList = this._normalizeData(res.data.data)
          console.log(this.recordList)
        })
      },
      _normalizeData(arr) {
        let temp = []
        let tempStr = ''
        arr.map(item => {
          if(item.type == 0) {
            tempStr = '配送'
          } else if(item.type == 1) {
            tempStr = '赠送'
          } else {
            tempStr = '入库'
          }
          temp.push({
            count: item.count,
            title: tempStr,
            createdTime: this._normalizeTime(item.created_at),
            remain: item.item.title
          })
        })
        return temp
      },
      _normalizeTime(str) {
        let idx = str.indexOf(' ')
        let length = str.length
        let st = str.slice(0, idx)
        return st
      }
    },
    components: {
      RecordList,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .out-store
    z-index: 11
    background: #f2f2f2
    pos-all()
</style>



