<template>
  <div class="money-coupon">
    <div class="money-btn">
      <a @click="scan">扫一扫充值券</a>
      <a @click="goHome">前往商城</a>
      <a @click="goScan">查看钱包</a>
    </div>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import weChat from 'api/mixins'
  import Confirm from 'base/confirm/confirm'
  import {isIOSOrAndroid} from 'api/util'


  export default {
    created() {
//      if(isIOSOrAndroid()) {
//        this.url = location.hostname + '/app/home'
//      } else {
//        this.url = window.location.href
//      }
      this.url = window.location.href
      // this.url = 'http://www.fujianqiuge.com/'
      this.weChatConfig(this.url)
    },
    data() {
      return {
        url: ''
      }
    },
    mixins: [weChat],
    methods: {
      scan() {
        this.goScan()
      },
      goHome() {
        this.$router.push({
          path: '/app/home'
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .money-coupon
    background: #f2f2f2
    z-index: 11
    pos-all()
    .money-btn
      padding: 12px 15px
      background: #fff
      a
        margin-top: 12px
        width: 100%
        height: 36px
        line-height: 36px
        text-align: center
        background: #ff8852
        color: #fff
        display: block
      a:nth-of-type(2)
        background: #00c886
      a:last-child
        margin-top: 12px
        background: #ff558e
</style>



