import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/home/home'
import Index from 'components/home/index'
import Store from 'components/store/store'
import Member from 'components/member/member'
import Car from 'components/car/car'
import SubmitOrder from 'components/submitOrder/submitOrder'
import ConfirmOrder from 'components/submitOrder/confirmOrder'
import Address from 'components/address/address'
import AddAddress from 'components/address/addAddress'
import Pay from 'components/pay/pay'
import Bind from 'components/bind/bind'
import ChickenStore from 'components/chickenStore/chickenStore'
import EggStore from 'components/chickenStore/eggStore'
import OutStore from 'components/chickenStore/outStore'
import GiveStore from 'components/chickenStore/giveStore'
import Exchange from 'components/exchange/exchange'
import ObjCoupon from 'components/exchange/objCoupon'
import MoneyCoupon from 'components/exchange/moneyCoupon'
import Wallet from 'components/wallet/wallet'
import CountSearch from 'components/wallet/countSearch'
import Recharge from 'components/wallet/recharge'
import Cash from 'components/wallet/cash'
import Message from 'components/message/message'
import Entrust from 'components/entrust/entrust'
import Coupon from 'components/coupon/coupon'
import Adopt from 'components/adopt/adopt'
import ProductDetail from 'components/productDetail/productDetail'
import Product from 'components/product/product'
import CategoryProduct from 'components/categoryProduct/categoryProduct'
import CouponList from 'components/coupon/couponList'
import Company from 'components/company/company'
import CompanyMenu from 'components/company/companyMenu'
import VideoList from 'components/company/videoList'
import CompanyProfile from 'components/company/companyProfile'
import WxAllArticle from 'components/company/wxallarticle'
import ActiveArticle from 'components/company/activeArticle'
import NoticeArticle from 'components/company/noticeArticle'
import OrderAll from 'components/order/orderAll'
import OrderDetail from 'components/orderDetail/orderDetail'
import Share from 'components/share/share'
import Spread from 'components/spread/spread'
import ShareUrl from 'components/shareUrl/shareUrl'
import ShareAdpot from 'components/shareUrl/shareAdpot'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    // {
    //   path: '/app',
    //   redirect: '/app/index'
    // },
    // {
    //   path: '/app/index',
    //   name: 'Index',
    //   component: Index,
    //   meta: {
    //     title: '喔喔商城'
    //   }
    // },
    {
      path: '/app/home',
      name: 'Home',
      component: Home,
      meta: {
        title: '喔喔商城'
      }
    },
    {
      path: '/app/companyprofile',
      name: 'CompanyProfile',
      component: CompanyProfile,
      meta: {
        title: '企业简介'
      }
    },
    {
      path: '/app/wxallarticle',
      name: 'WxAllArticle',
      component: WxAllArticle,
      meta: {
        title: '鸡笼百科'
      }
    },
    {
      path: '/app/activeArticle',
      name: 'ActiveArticle',
      component: ActiveArticle,
      meta: {
        title: '公司活动'
      }
    },
    {
      path: '/app/noticeArticle',
      name: 'NoticeArticle',
      component: NoticeArticle,
      meta: {
        title: '最新公告'
      }
    },
    {
      path: '/app/spread',
      name: 'Spread',
      component: Spread,
      meta: {
        title: '推广商品'
      }
    },
    {
      path: '/app/shareurl',
      name: 'ShareUrl',
      component: ShareUrl,
      meta: {
        title: '推广海报'
      }
    },
    {
      path: '/app/shareadpot',
      name: 'ShareAdpot',
      component: ShareAdpot,
      meta: {
        title: '认养推广'
      }
    },
    {
      path: '/app/company',
      name: 'Company',
      component: Company,
      meta: {
        title: '企业信息'
      }
    },
    {
      path: '/app/share',
      name: 'Share',
      component: Share,
      meta: {
        title: '分享中心'
      }
    },
    {
      path: '/app/store',
      name: 'Store',
      component: Store,
      meta: {
        title: '我的仓库'
      }
    },
    {
      path: '/app/me',
      name: 'Member',
      component: Member,
      meta: {
        title: '个人中心'
      }
    },
    {
      path: '/app/car',
      name: 'Car',
      component: Car,
      meta: {
        title: '购物车'
      }
    },
    {
      path: '/app/submitorder',
      name: 'SubmitOrder',
      component: SubmitOrder,
      meta: {
        title: '提交订单'
      }
    },
    {
      path: '/app/orderall',
      name: 'OrderAll',
      component: OrderAll,
      meta: {
        title: '订单列表'
      }
    },
    {
      path: '/app/orderdetail',
      name: 'OrderDetail',
      component: OrderDetail,
      meta: {
        title: '订单详情'
      }
    },
    {
      path: '/app/confirmorder',
      name: 'ConfirmOrder',
      component: ConfirmOrder,
      meta: {
        title: '确认订单'
      }
    },
    {
      path: '/app/address',
      name: 'Address',
      component: Address,
      meta: {
        title: '收货地址'
      }
    },
    {
      path:'/app/addaddress',
      name: 'AddAddress',
      component: AddAddress,
      meta: {
        title: '添加地址'
      }
    },
    {
      path: '/app/pay',
      name: 'Pay',
      component: Pay,
      meta: {
        title: '支付方式'
      }
    },
    {
      path: '/app/couponlist',
      name: 'CouponList',
      component: CouponList,
      meta: {
        title: '优惠券列表'
      }
    },
    {
      path: '/app/bind',
      name: 'Bind',
      component: Bind,
      meta: {
        title: '绑定中心'
      }
    },
    {
      path: '/app/chickenstore',
      name: 'ChickenStore',
      component: ChickenStore,
      meta: {
        title: '鸡库'
      }
    },
    {
      path: '/app/eggstore',
      name: 'EggStore',
      component: EggStore,
      meta: {
        title: '蛋库'
      }
    },
    {
      path: '/app/outstore',
      name: 'OutStore',
      component: OutStore,
      meta: {
        title: '出入库记录'
      }
    },
    {
      path: '/app/videoList',
      name: 'VideoList',
      component: VideoList,
      meta: {
        title: '鸡笼视频列表'
      }
    },
    {
      path: '/app/givestore',
      name: 'GiveStore',
      component: GiveStore,
      meta: {
        title: '送鸡记录'
      }
    },
    {
      path: '/app/exchange',
      name: 'Exchange',
      component: Exchange,
      meta: {
        title: '兑换中心'
      }
    },
    {
      path: '/app/objcoupon',
      name: 'ObjCoupon',
      component: ObjCoupon,
      meta: {
        title: '实物券兑换'
      }
    },
    {
      path: '/app/moneycoupon',
      name: 'MoneyCoupon',
      component: MoneyCoupon,
      meta: {
        title: '充值券兑换'
      }
    },
    {
      path: '/app/wallet',
      name: 'Wallet',
      component: Wallet,
      meta: {
        title: '我的钱包'
      }
    },
    {
      path: '/app/countsearch',
      name: 'CountSearch',
      component: CountSearch,
      meta: {
        title: '账目查询'
      }
    },
    {
      path: '/app/recharge',
      name: 'Recharge',
      component: Recharge,
      meta: {
        title: '微信充值'
      }
    },
    {
      path: '/app/cash',
      name: 'Cash',
      component: Cash,
      meta: {
        title: '申请提现'
      }
    },
    {
      path: '/app/message',
      name: 'Message',
      component: Message,
      meta: {
        title: '反馈中心'
      }
    },
    {
      path: '/app/entrust',
      name: 'Entrust',
      component: Entrust,
      meta: {
        title: '反馈中心'
      }
    },
    {
      path: '/app/coupon',
      name: 'Coupon',
      component: Coupon,
      meta: {
        title: '优惠券'
      }
    },
    {
      path: '/app/adopt',
      name: 'Adopt',
      component: Adopt,
      meta: {
        title: '我的认养'
      }
    },
    {
      path: '/app/product/',
      name: 'Product',
      component: Product,
      meta: {
        title: '商品'
      },
      children: [
        {
          path: ':id',
          name: 'ProductDetail',
          component: ProductDetail,
          meta: {
            title: '商品详情'
          }
        }
      ]
    },
    {
      path: '/app/categoryproduct',
      name: 'CategoryProduct',
      component: CategoryProduct,
      meta: {
        title: '热销商品'
      }
    },
    {
      path: '/app/companymenu',
      name: 'CompanyMenu',
      component: CompanyMenu,
      meta: {
        title: '公司介绍'
      }
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



