<template>
  <scroll class="egg-store" :data="category">
    <div class="egg-scroll">
      <store-list :category="category" :categoryId="categoryId" @dispatchEgg="dispatchEgg"></store-list>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import StoreList from 'base/storeList/storeList'
  import {axiosGet} from 'api/config'
  import MockData from 'common/mock/data'
  import {mapMutations} from 'vuex'

  export default {
    data() {
      return {
        category: [],
        categoryId: 0
      }
    },
    created() {
      axiosGet('/storehouse/egg').then(res => {
        if(res.status === 200) {
          this.category = res.data.data
        }
      })
//      this.category = MockData.storeData.eggData
    },
    methods: {
      dispatchEgg(idx) {
        console.log(this.category[idx])
        this.setStoreEggNum(this.category[idx].num)
        let arr = [{"id": this.category[idx].item.id, "count": this.category[idx].num}]
        let arr1 = JSON.stringify(arr)
        this.$router.push({
          path: `/app/submitorder?type=${arr1}&eggstore=1&eggId=${this.category[idx].id}`
        })
      },
      ...mapMutations({
        setStoreEggNum: 'SET_STOREEGGNUM'
      })
    },
    components: {
      Scroll,
      StoreList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"

  .egg-store
    pos-all()
    background: #f2f2f2
    z-index: 11
</style>



