<template>
  <div class="order-list">
    <ul>
      <li v-for="(item, index) in orderList" @click="goDetail(index, item.id)">
        <div class="product-content" v-for="(data, num) in item.items">
          <section class="product-pic">
            <img :src="data.cover.url" alt="">
          </section>
          <section class="product-info">
            <div class="checked-show">
              <h4>
                <span>{{data.title}}</span>
                <a>{{data.deliver_type}}</a>
              </h4>
              <section class="checked-price">
                <span class="price"><small>￥</small>{{data.price}}</span>
                <span class="num">×{{data.count}}</span>
              </section>
            </div>
          </section>
        </div>
        <div class="product-content" v-for="(res, keys) in item.stageItems">
          <section class="product-pic">
            <img :src="res.cover.url" alt="">
          </section>
          <section class="product-info">
            <div class="checked-show">
              <h4>
                <span>{{res.title}}</span>
                <a>{{res.deliver_type}}</a>
              </h4>
              <section class="checked-price">
                <span class="price"><small>￥</small>{{res.price}}</span>
                <span class="num">×{{res.count}}</span>
              </section>
            </div>
          </section>
        </div>
        <div class="order-tip" v-if="item.items">
          共{{item.items.length + item.stageItems.length}}件商品 合计:￥<span>{{item.price}}</span>
        </div>
        <div class="order-btn" @click.stop>
          <section class="order-status" v-show="item.status === 'wait_pay'">
            <a class="cancel" @click="cancel(index)">取消订单</a>
            <a class="pay" @click="pay(index)" v-show="item.items">付款</a>
          </section>
          <section class="order-status" v-show="item.status === 'wait_deliver'">
            <a class="cancel" @click="cancel(index, 'confirm')">删除订单</a>
          </section>
          <section class="order-status" v-show="item.status === 'wait_recv'">
            <a class="cancel" @click="cancel(index, 'recv')">删除订单</a>
            <a class="pay" @click="getGoods(index, 'recv')">确认收货</a>
          </section>
          <section class="order-status" v-show="item.status === 'finish'">
            <a class="cancel" @click="cancel(index, 'finish')">删除订单</a>
          </section>
        </div>
      </li>
    </ul>

  </div>
</template>

<script>

  import {axiosDelete} from 'api/config'

  export default {
    props: {
      orderList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {

      }
    },
    created() {
    },
    computed: {

    },
    methods: {
      cancel(idx, status) {
        this.$emit('cancel', idx, status)
      },
      pay(idx) {
        this.$emit('pay', idx)
      },
      getGoods(idx, status) {
        this.$emit('getGoods', idx, status)
      },
      goDetail(idx, id) {
        this.$emit('goDetail', idx, id)
      }
    },
    components: {

    },
    watch: {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .order-list
    padding: 0 15px
    li
      margin-top: 15px
      background: #fff
      border-radius: 6px
      padding: 15px 0 10px
      .product-content
        margin: 15px 0
        padding: 0 15px
        display: flex
        align-items: center
        .product-pic
          flex: 0 0 80px
          width: 80px
          height: 80px
          margin: 0 15px 0 8px
          img
            height: 100%
            width: 100%
        .product-info
          box-sizing: border-box
          flex: 1 1 auto
          height: 80px
          .checked-show
            padding: 10px 0
            display: flex
            flex-direction: column
            justify-content space-between
            height: 60px
            h4
              display: flex
              span
                flex: 1 1 auto
                margin-right: 20px
                font-size: 14px
              a
                flex: 0 0 60px
                height: 16px
                line-height: 16px
                border-radius: 8px
                text-align: center
                width: 60px
                background: #ff8852
                color: #fff
                font-size: 12px
            .checked-price
              display: flex
              span
                flex: 0 0 50%
              .num
                color: #999
                text-align: right
              .price
                color: #ff8852
                small
                  font-size: 12px
      .product-content:first-child
        margin-top: 0
      .product-content:last-child
        margin-bottom: 0
      .order-tip
        padding: 0 15px
        font-size: 12px
        text-align: right
        span
          font-size: 16px
      .order-btn
        margin-top: 15px
        padding: 0 15px
        line-height: 44px
        height:44px
        .order-status
          text-align: right
          a
            margin-right: 10px
            padding: 6px 18px
            border: 1px solid #ddd
            border-radius: 6px
          a.pay
            border: 1px solid #ff8852
            color: #ff8852
          a:last-child
            margin-right: 0
</style>



