<template>
  <div class="recharge">
    <h3>请选择充值金额</h3>
    <section class="recharge-list">
      <ul>
        <li v-for="(item, index) in rechargeList" @click="rechargeOrder(index)">
          <h5>{{item.money}}元</h5>
          <span>{{item.msg}}</span>
        </li>
      </ul>
    </section>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import {axiosGet, axiosPost, axiosPut, token} from 'api/config'
  import weChat from 'api/mixins'
  import Confirm from 'base/confirm/confirm'
  import {isIOSOrAndroid} from 'api/util'

  export default {
    created() {
//      if(isIOSOrAndroid()) {
//        this.url = location.hostname + '/app/home'
//      } else {
//
//      }
      this.url = window.location.href
      this.weChatConfig(this.url)
      this.getData()
    },
    data() {
      return {
        rechargeList: [],
        msg: '',
        url: ''
      }
    },
    mixins: [weChat],
    methods: {
      getData() {
        axiosGet('/recharge').then(res => {
          this.rechargeList = this._normalizeData(res.data)
        })
      },
      _normalizeData(arr) {
        arr.map(item => {
          if(!item.free) {
            item.msg = `面值${item.money}元`
          } else {
            item.msg = `充${item.money}送${item.free}元`
          }
        })
        return arr
      },
      rechargeOrder(idx) {
        let {id} = this.rechargeList[idx]
        axiosPost(`/wallet/recharge/${id}`).then(res => {
          console.log(res)
          if(res.data.status == 1) {
            axiosPut(`/orders/${res.data.order.id}/pay`).then(item => {
              console.log(item)
              if(item.status === 200) {
                this.goPay(item.data.data)
              }
            })
          }
        })
      }
    },
    components: {
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .recharge
    background: #f2f2f2
    z-index: 11
    pos-all()
    h3
      background: #ffffff
      padding: 0 15px
      border-bottom: 1px solid #f2f2f2
      line-height: 36px
      height: 36px
    .recharge-list
      background: #fff
      ul
        display: flex
        box-sizing: border-box
        padding: 2vw
        flex-wrap: wrap
        li
          flex: 0 0 46vw
          border: 1px solid #47a5fd
          border-radius: 8px
          padding: 15px 0
          box-sizing: border-box
          display: flex
          flex-direction: column
          justify-content: center
          margin: 1vw
          text-align: center
          color: #47a5fd
          h5
            font-weight: bold
            margin-bottom: 10px
            font-size: 16px


</style>



