<template>
  <div class="article-list">
    <ul>
      <li v-for="(item, index) in articleList" @click="wxArticle(index)">
        <span>{{item.title}}</span>
        <small><img src="./images/more@2x.png" alt=""></small>
      </li>
    </ul>
  </div>
</template>

<script>
  import {axiosGet} from 'api/config'
  export default {
    created() {
    },
    props: {
      articleList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {

      }
    },
    methods: {
      wxArticle(idx) {
        this.$emit('wxArticle', idx)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .article-list
    h3
      padding: 0 15px
      border-bottom: 1px solid #f2f2f2
      box-sizing: border-box
      line-height: 44px
      height: 44px
    ul
      padding-left: 15px
      li
        display: flex
        height: 44px
        align-items: center
        border-bottom: 1px solid #f2f2f2
        padding-right: 15px
        span
          flex: 1 1 auto
          text-overflow: ellipsis
          overflow: hidden
          white-space: nowrap
        small
          flex: 0 0 6px
          height: 10px
          width: 6px
          img
            height: 100%
            width: 100%
</style>



