<template>
  <div class="share-url">
    <scroll class="share-in">
      <div class="share-scroll">
        <div class="share-content">
          <h3>推广令</h3>
          <section class="user-info">
            <div class="user-pic">
              <img :src="userObj.avatarUrl" alt="">
            </div>
            <h4>{{userObj.nickname}}</h4>
            <small>邀你一起加入，推广赢奖励</small>
            <section class="user-qcode">
              <div class="qcode-pic">
                <img :src="userQcode.code" alt="">
              </div>
              <p>长按保存到相册</p>
              <div class="qcode-url">
                <input type="text" v-model="urlStr">
                <a>长按 - 全选 - 复制</a>
              </div>
            </section>
          </section>
        </div>
        <div class="coupon-take">
          <h3>领取福利</h3>
          <div class="coupon-ps">
            <article>
              <p>
                <span>20元现金券：</span>需要您发送二维码或是链接，成功邀请3人便能领取20元（20元直接充入余额）
              </p>
              <p>
                <span>30元现金券：</span>需要您发送二维码或是链接，成功邀请1人您邀请的客户成功消费一笔，便能领取30元（30元直接充入余额）
              </p>
              <p>点击下方领取福利</p>
            </article>
          </div>
          <div class="coupon">
            <ul>
              <li @click="takeTwo">
                <img src="./images/coupontake20@3x.png" alt="">
              </li>
              <li @click="takeThree">
                <img src="./images/coupontake30@3x.png" alt="">
              </li>
            </ul>
          </div>
        </div>
      </div>
    </scroll>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import {axiosGet} from 'api/config'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'

  export default {
    data() {
      return {
        userObj: {},
        userQcode: {},
        urlStr: '',
        message: ''
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/users/show').then(res => {
          if(res.status === 200) {
            this.userObj = res.data.data
          }
        })
        axiosGet('/inviteLink').then(res => {
          console.log(res)
          if(res.status === 200) {
            this.userQcode = res.data
            this.urlStr = res.data.url
          }
        })
      },
      takeTwo() {
        axiosGet('/inviteBonus').then(res => {
          console.log(res)
          if(res.status === 200) {
            this.message = res.data.message
            this.$refs.confirm.show()
          }
        })
      },
      takeThree() {
        axiosGet('inviteSaleBonus').then(res => {
          console.log(res)
          if(res.status === 200) {
            this.message = res.data.message
            this.$refs.confirm.show()
          }
        })
      }
    },
    components: {
      Scroll,
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"

  .share-in
    pos-all()
    z-index: 11
    background: #f2f2f2
    .share-content
      margin: 0 auto
      border: 2px solid #fff
      background: linear-gradient(top, #fff4e8, #fff)
      width: 80%
      text-align: center
      padding: 0 0 15px
      h3
        color: #ff8852
        font-size: 18px
        line-height: 80px
        height: 80px
        text-align: center
      .user-info
        .user-pic
          height: 60px
          width: 60px
          margin: 0 auto
          img
            border-radius: 50%
            width: 60px
            height: 60px
        h4
          margin: 12px 0
        small
          color: #999
          font-size: 14px
      .user-qcode
        margin-top: 12px
        .qcode-pic
          margin: 0 auto
          height: 100px
          width: 100px
          img
            height: 100px
            width: 100px
        .qcode-url
          margin-top: 12px
          input
            width: 90%
            height: 36px
            line-height: 36px
            border: 1px solid #f2f2f2
          a
            margin-top: 12px
            display: block
    .coupon-take
      background: #ffffff
      margin-top: 12px
      h3
        color: #ff8852
        font-size: 18px
        line-height: 60px
        height: 60px
        text-align: center
      .coupon
        height: 33.333vw
        ul
          display: flex
          height: 33.333vw
          justify-content: center
          align-items: center
          li
            width: 44vw
            height: 26.666vw
            margin-right: 15px
            img
              height: 100%
              width: 100%
          li:last-child
            margin-right: 0
      .coupon-ps
        padding: 0 15px
        p
          margin-bottom: 15px
          line-height: 20px
          color: #999
          span
            color: #ff8852
        p:last-child
          margin: 0
</style>



