<template>
    <div class="message">
      <section class="message-content">
        <span>问题</span>
        <textarea placeholder="请输入问题详情"></textarea>
      </section>
      <section class="message-btn">
        <a>确认提交</a>
      </section>
    </div>
</template>

<script>
  export default {
    name: "message"
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .message
    background: #f2f2f2
    z-index: 11
    pos-all()
    .message-content
      background: #ffffff
      padding: 15px
      display: flex
      span
        flex: 0 0 60px
      textarea
        border: 1px solid #eee
        height: 160px
        flex: 1 1 auto
    .message-btn
      color: #fff
      margin-top: 12px
      padding: 0 15px
      a
        color: #fff
        border-radius: 8px
        background: #ff8852
        text-align: center
        height: 44px
        line-height: 44px
        display: block
</style>



