<template>
  <div class="adopt-list">
    <div class="adopt-content">
      <ul>
        <li v-for="(item, index) in adpotArr">
          <i><img :src="item.item.cover.url" alt=""></i>
          <section class="product-info">
            <h4>
              <span>种类：{{item.item.title}}</span>
            </h4>
            <p>状态：{{item.item.stateInfo}}</p>
            <p>现可提成本蛋: {{item.can_num}}个</p>
            <p>余可提成本蛋: {{item.egg_num}}个</p>
            <div class="product-btn">
              <a @click="eatChicken(index)" v-show="renyShow[index] && adpotArr[index].status !=2">吃鸡</a>
              <a @click="eatEgg(index)" v-show="adpotArr[index].status !=2">吃蛋</a>
              <a @click="deleteChicken(index)" v-show="adpotArr[index].status == 2">删除</a>
            </div>
          </section>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

  export default {
    props: {
      adpotArr: {
        type: Array,
        default: []
      }
    },
    data() {
      return {
        renyShow: []
      }
    },
    methods: {
      eatChicken(idx) {
        this.$emit('eatChicken', idx, this.adpotArr[idx].item.id)
      },
      eatEgg(idx) {
        this.$emit('eatEgg', idx, this.adpotArr[idx].item.id)
      },
      deleteChicken(idx) {
        console.log()
        this.$emit('delChicken', idx, this.adpotArr[idx].id)
      }
    },
    watch: {
      adpotArr(newVal, oldVal) {
        console.log(newVal)
        newVal.map(item => {
          if(item.status == 1) {
            this.renyShow.push(false)
          } else {
            this.renyShow.push(true)
          }

        })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .adopt-content
    ul
      padding: 0 15px
      li
        padding: 20px 15px
        border-radius: 8px
        background: #ffffff
        margin-top: 12px
        display: flex
        align-items center
        i
          flex: 0 0 80px
          height: 80px
          width: 80px
          margin-right: 15px
          img
            height: 100%
            width: 100%
        .product-info
          display: flex
          flex-direction: column
          flex: 1 1 auto
          justify-content: center
          h4
            display: flex
            padding-right: 15px
            span
              flex: 1 1 auto
            small
              flex: 0 0 20px
              color: #999
          p
            margin-top: 5px
          .product-btn
            margin-top: 10px
            padding-right: 15px
            a
              background: #ff8852
              color: #fff
              border-radius: 6px
              width: 70px
              height: 30px
              line-height: 30px
              text-align: center
              display: inline-block
</style>



