<template>
  <div class="cash">
    <section class="cash-info">
      <section class="cash-input">
        <span>提现金额</span>
        <input type="text" v-model="count">
      </section>
      <section class="cash-take">
        <span>可提现金额 <small>￥{{canMoney.toFixed(2)}}</small></span>
        <a @click="takeMoney">全部提现</a>
      </section>
    </section>
    <section class="cash-tip">
      <h5>提现说明</h5>
      <a>每日最多5笔提现申请，每笔金额1-100元整数</a>
    </section>

    <section class="cash-btn">
      <a @click="applyCash">申请提现</a>
    </section>
    <confirm :message="message" ref="confirm"></confirm>
  </div>
</template>

<script>
  import Confirm from 'base/confirm/confirm'
  import {axiosGet, axiosPost} from 'api/config'

  export default {
    data() {
      return {
        count: 0,
        canMoney: 0,
        walletMoney: {},
        message: ''
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        axiosGet('/wallet').then(res => {
          if(res.status === 200) {
            this.walletMoney = res.data[0]
            this.canMoney = this.walletMoney.can_money
          }
        })
      },
      takeMoney() {
        this.count = this.canMoney
      },
      applyCash() {
        axiosPost('/wallet/cash', {
          data: {
            money: this.count
          }
        }).then(res => {
          if(res.status === 200) {
            if(res.data.status == 1) {
              this.message = res.data.msg
              this.$refs.confirm.show()
            } else {
              this.message = '有笔提现正在进行中...'
              this.$refs.confirm.show()
            }
          }
        })
      }
    },
    components: {
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'

  .cash
    background: #f2f2f2
    z-index: 11
    pos-all()
    .cash-info
      background: #fff
      .cash-input
        font-size: 16px
        display: flex
        padding: 0 15px
        width: 100%
        height: 44px
        line-height: 44px
        border-bottom: 1px solid #f2f2f2
        box-sizing: border-box
        span
          flex: 0 0 80px
        input
          padding: 0
          text-align: right
          flex: 1 1 auto
      .cash-take
        display: flex
        padding: 0 15px
        width: 100%
        height: 44px
        line-height: 44px
        box-sizing: border-box
        span
          flex: 0 0 50%
          small
            color: #ff8852
        a
          color: #ff8852
          text-align: right
          flex: 0 0 50%
    .cash-tip
      margin-top: 12px
      padding:0 15px
      background: #fff
      display: flex
      flex-direction: column
      justify-content: center
      height: 60px
      h5
        margin-bottom: 10px
    .cash-btn
      color: #fff
      margin-top: 12px
      padding: 0 15px
      a
        color: #fff
        border-radius: 8px
        background: #ff8852
        text-align: center
        height: 44px
        line-height: 44px
        display: block
</style>



